function validateImages(file){
    let permitFormat = ["png","jpeg","jpg"]
    let result = {
        status : false,
        message : "the file is not in the required format",
        typePermit : "image"
    } 
    if( Boolean(file) ){
        const type = file.type.split('/')
        if( type[0] === "image" ){
            let format = type[1]
            let formatRequired = permitFormat.includes(format)
            if( formatRequired ){
                let urlImage = URL.createObjectURL(file)
                result.status = true
                result.url = urlImage
                delete result.message
                delete result.typePermit
                return result
            }else{
                result.typePermit = permitFormat
                return result
            }
        }else{
            return result
        }
    }
}
function validateText(text){
    let textRegex = /^[a-zA-ZÀ-ÿ\u00f1\u00d1]+(\s*[a-zA-ZÀ-ÿ\u00f1\u00d1]*)*[a-zA-ZÀ-ÿ\u00f1\u00d1]+$/g
    return textRegex.test(text)
}

function Validation(){
    this.validateImages = validateImages
    this.validateText = validateText
}

export default Validation