import React from 'react';
import Form from './Form';
import { connect } from 'react-redux'
import { callFormEditCategorie, storeUpdatedCategory, updateSelectedCategory } from '../../../redux/actions/CategoriesAction'

const mapPropsToState = (state) => {
    return {
        categoryUpdate : state.Categories.editCategory.categoryUpdate,
        categoryUpdateName : state.Categories.editCategory.categoryUpdate.name,
        categoryUpdateActive : state.Categories.editCategory.categoryUpdate.active,
        categorySelected : state.Categories.categorySelected,
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        callFormEditCategorie : (status) => dispatch(callFormEditCategorie(status)),
        storeUpdatedCategory : (category) => dispatch(storeUpdatedCategory(category)),
        updateSelectedCategory : (category) => dispatch(updateSelectedCategory(category)),
    }
}

const EditCategory = connect(mapPropsToState, mapDispatchToProps)(({categorySelected, categoryUpdate, categoryUpdateName, categoryUpdateActive, ...actions}) => {

    const { callFormEditCategorie, storeUpdatedCategory, updateSelectedCategory } = actions
    
    React.useEffect(() => {
        return () => {
            storeUpdatedCategory({})
            updateSelectedCategory({})
        }
    },[])

    return(
        <>
            <Form
                callFormEditCategorie={callFormEditCategorie}
                categorySelected={categorySelected}
                storeUpdatedCategory={storeUpdatedCategory}
                categoryUpdate={categoryUpdate}
                categoryUpdateName={categoryUpdateName}
                categoryUpdateActive={categoryUpdateActive}
            />
        </>
    )
})

export default EditCategory