import store from '../../redux/store'
import { activeModalToCreateUnitOfMeasure } from '../../redux/actions/UnitSizeAction'
import history from '../../handlers/history'
import app from '../../connection/Api'
const dispatch = (fn) => store.dispatch(fn)

const getUnitSizes = async (limit, page, unitSize_id) => {
    try{
        let url = `/get/unit/size?`
        if( Boolean(limit) ) url = url+`limit=${limit}&`
        if( Boolean(page) ) url = url+`page=${page}&`
        if( Boolean(unitSize_id) ) url = url+`unitSize_id=${unitSize_id}&`
        const response = await app.get(url)
        return response
    }catch(err){
        console.log(err)
    }
}
const createUnitSizes = async (unitSize) => {
    try{
        const payload = {unitSize}
        const response = await app.post('/save/unit/size', payload)
        return response
    }catch(err){
        console.log(err)
    }
}
const editUnitSize = async (unitSize) => {
    try{
        const payload = {payload : [unitSize]}
        const response = await app.post('/update/unit/size', payload)
        return response
    }catch(err){
        console.log(err)
    }
}
const deleteUnitSize = async (unitSize) => {
    try{
        const payload = {payload : unitSize}
        const response = await app.post('/delete/unit/size', payload)
        return response
    }catch(err){
        console.log(err)
    }
}

export default function UnitSize(){
    this.get = getUnitSizes
    this.create = createUnitSizes
    this.update = editUnitSize
    this.delete = deleteUnitSize
}
export const goCreateUnitSize = () => {
    dispatch(activeModalToCreateUnitOfMeasure(true))
}
export const goManageUnitSize = (businessName) => {
    history.push(`/${businessName}/manage/unit/sizes`)
}