import constant from '../actions/index'

const initialState = {
    pendingRequestsList : false,
}

export default function EmployedReducer (state = initialState, action){
    const globalState  = {...state}
    switch (action.type) {
        case constant.ACTIVE_MODAL_PENDING_REQUEST_LIST_EMPLOYED:
            globalState.pendingRequestsList = action.status
        break;
        default:
            return globalState;
    }
    return globalState
}