import React from 'react';
import { Dialog as Di, DialogTitle, DialogContent, Zoom, IconButton, Box } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import Text from './Text'
import CloseRoundedIcon from '@material-ui/icons/CloseRounded';
import Tooltip from '../components/Tooltips'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Zoom ref={ref} {...props} />;
});

const Dialog = ({ maxWidth="sm", open=false, close= _ => false, children, severity="warning", alert=false, title="", component, fullScreen=false }) => {

  return (
    <div>
      <Di fullScreen={fullScreen} open={open} onClose={close} maxWidth={maxWidth} fullWidth TransitionComponent={Transition} >
        {title &&
        <DialogTitle style={{ height : "50px", padding: "0px 24px" }} >
          <Box display="flex" alignItems="center" >
            <Text text={title} uppercase fontSize="modal-title" />
            <Box>
              <Tooltip title='cerrar' placement='bottom' >
                <IconButton size='small' color="secondary" onClick={ () => close() } >
                  <CloseRoundedIcon />
                </IconButton>
              </Tooltip>
            </Box>
          </Box>
        </DialogTitle> }
        
        <DialogContent>
          { children }
          { alert &&
            <Alert severity={severity}>{ alert }</Alert>
          }
          {component}
        </DialogContent>
      </Di>
    </div>
  );
}

export default Dialog