import React from 'react'
import { Box, Paper } from '@material-ui/core'
import Table from '../../../components/Table'
import Autocomplete from '../../../components/Autocomplete'
import CompountProduct from '../class'
import Product from '../../products/class'
import { Start } from '../../../components/icons'

const TableCompountProduct = ({ inputValueProduct, compount, priceList, ...actions }) => {
    const { searchProductToProductCompount, selectProductFormComposite, openCompositeProductConfigurationWindow, updateProductPriceList, editSelectedCompositeProduct, updateSelectedQuantity, updatePositionOfThePuttingUnit, assignMainProduct } = actions
    const compountClass = new CompountProduct()
    const columns = [
        {
            field: 'product.name',
            headerName: 'Producto',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params.row.product)
                return(
                    <>
                        {params.row.main ? <Start width='20px' style={{ marginRight: "10px" }} /> : <></>}
                        {params.row.product.name}
                    </>
                )
            },
        },{
            field: 'quantity',
            headerName: 'Cantidad',
            editable: false,
            align: "center",
            renderCell : (params) => {
                const unitSize = compountClass.getUnitSize(params.row.product.unitSize, params.row.positionUnitSize)
                return(
                    <>{`${params.row.quantity} (${unitSize})`}</>
                )
            },
        },{
            field: 'cost',
            headerName: 'Costo',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                const cost = compountClass.calculateCost(params.row.product, params.row.quantity, params.row.positionUnitSize)
                return(
                    <>{`$ ${cost}`}</>
                )
            },

        },
    ]

    React.useEffect(() => {
        if(compount.length >= 1){
            let cost = 0 
            for(let item of compount){
                const costCalc = compountClass.calculateCost(item.product, item.quantity, item.positionUnitSize, false)
                cost += costCalc
            }
            const product = new Product()
            const price = product.updatePriceList(0, 'cost', cost)
            updateProductPriceList(price)
        }
    },[compount])
    return(
        <>
            <Box mb={2}>
                <Paper variant='outlined' elevation={0} >
                    <Box p={1} >
                        <Autocomplete
                            options={inputValueProduct}
                            label='Productos'
                            onInputChange={(event) => searchProductToProductCompount(event.target.value) }
                            onChange={(val, element) => {
                                val.value = element
                                if( val.value.length <= 0 ){
                                    val.value = {}
                                    openCompositeProductConfigurationWindow(false)
                                    return
                                }
                                selectProductFormComposite(val.value)
                                openCompositeProductConfigurationWindow(true)
                            }}
                        />
                        <Table
                            getRowId={row => row.product._id}
                            hideFooter
                            pointer
                            onCellClick={(val) => {
                                const { row } = val
                                openCompositeProductConfigurationWindow(true)
                                editSelectedCompositeProduct(true)
                                selectProductFormComposite(row.product)
                                updateSelectedQuantity(row.quantity)
                                updatePositionOfThePuttingUnit(row.positionUnitSize)
                                assignMainProduct(row.main)
                            }}
                            // updatePage={(pg) => updateVariantsListPagePosition(pg)}
                            // page={position}
                            // rowCount={variantsTotal}
                            rows={compount}
                            columns={columns}
                            // overlayText="Cargando unidades de medidas"
                            loading={true}
                        />
                    </Box>
                </Paper>
            </Box>
        </>
    )
}

export default TableCompountProduct