/* eslint no-undef: 0 */
import React from 'react';
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { makeStyles } from '@material-ui/core';
import { SpeedDial, SpeedDialIcon, SpeedDialAction } from '@material-ui/lab';
import AppsIcon from '@material-ui/icons/Apps';
import { CategoriesSVG, DashboardSVG, ProductSVG, SelleSVG, PriceSVG } from './icons'


const useStyles = makeStyles((theme) => ({
  speedDial: {
    position: 'fixed',
    bottom: theme.spacing(2),
    right: theme.spacing(8),
  },
}));




const SpeedDials = ({ business, idBusiness, businessName }) => {

  const history = useHistory()
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const actions = [
    { 
      icon: <DashboardSVG width="22px" />,
      name: 'Administrar negocio',
      onClick : () => history.push(`/admin/${businessName}/${idBusiness}`)
    },
    { icon: <PriceSVG width="22px" />, name: 'Administrar precios ' },
    { icon: <SelleSVG width="22px" />, name: 'Administrar Ventas' },
    { icon: <ProductSVG width="22px" />, name: 'Administrar productos' },
    { 
      icon: <CategoriesSVG width="24px" />,
      name: 'Administrar categoria',
      onClick : () => history.push(`/admin/manage/categories/${businessName}/${idBusiness}`)
    },
  ];

  return (
    <SpeedDial
      ariaLabel="SpeedDial openIcon example"
      className={classes.speedDial}
      icon={<SpeedDialIcon openIcon={<AppsIcon />} />}
      onClose={handleClose}
      onOpen={handleOpen}
      open={open}
    >
    {actions.map((action) => (
      <SpeedDialAction
        key={action.name}
        icon={action.icon}
        tooltipTitle={action.name}
        onClick={() => {
          if( typeof action.onClick === 'function' ){
            action.onClick()
          }
          handleClose()
        }}
      />
    ))}
    </SpeedDial>
  );
}

export default connect(state => {
  return{
    business : state.dataState.business.data,
    idBusiness : state.Dashboard.shiftID,
    businessName : state.Dashboard.businessName,
  }
},null)(SpeedDials)