import { getToday, pastDays } from '../../methods/Moment';
import constant from '../actions/index'

const initialState = {
	socket : {},
	weekDate : [ getToday(), pastDays(1), pastDays(2), pastDays(3), pastDays(4), pastDays(5), pastDays(6)].reverse(),
	listRerporForWeek : [],
	weeklyEarningsList : [],
	weeklyProfitPercentageList : [],
	days : ['Lunes', 'Martes', 'Miércoles', 'Jueves', 'Viernes', 'Sábado', 'Domingo'],
	week : [{day : 1, active : false}, {day : 2, active : false}, {day : 3, active : false}, {day : 4, active : false}, {day : 5, active : false}, {day : 6, active : false},{day : 0, active : false}],
	alertSistem : {
		isOpen : false,
		locale : "",
		alert : "",
		prop : null,
	},
	callFormAddBusiness : false,
	callFormAddCategory : false,
	formAddCategoryError : false,
	callNotificationComponent : { active : false, message : '', severity : false },
	shiftID : false,
	businessName : '',
	tokenErr : false,
	businessTypesList : [],
}

export default function DashboardReducer(state = initialState, action){
	let globalState = {...state}
	switch (action.type) {
		case constant.ADD_TOTAL_SALE_OF_THE_WEEK:
			globalState.listRerporForWeek = action.listRerporForWeek
			globalState.weeklyEarningsList = action.weeklyEarningsList
			globalState.weeklyProfitPercentageList = action.weeklyProfitPercentageList
		break;
		case constant.SHOW_ADD_NEW_BUSINESS:
			globalState.callFormAddBusiness = true
		break;
		case constant.HIDE_ADD_NEW_BUSINESS:
			globalState.callFormAddBusiness = false
		break;
		case constant.SHOW_ADD_NEW_CATEGORY:
			globalState.callFormAddCategory = true
		break;
		case constant.HIDE_ADD_NEW_CATEGORY:
			globalState.callFormAddCategory = false
			globalState.formAddCategoryError = false
		break;
		case constant.UPDATE_SHIFT_ID:
			globalState.shiftID = action._id_
			globalState.businessName = action.businessName
		break;
		case constant.RETURN_ADD_NEW_CATEGORY_WITH_ERROR:
			globalState.callFormAddCategory = true
			globalState.formAddCategoryError = action.message
		break;
		case constant.CALL_NOFICATION_SNACKBAR:
			globalState.callNotificationComponent = { active : action.active, message : action.message, severity : action.severity }
		break;
		case constant.DETECTED_TOKEN_ERROR:
			globalState.tokenErr = action.status
		break;
		case constant.LOAD_BUSINESS_TYPES_ON_STATE:
			globalState.businessTypesList = action.list
		break;
		case constant.LOAD_SOCKET_IO_IN_STATE:
			globalState.socket = action.socket
		break;
		case constant.UPDATE_STATE_DASHBOARD:
			globalState[action.state]= action.value
		break;
		case constant.UPDATE_LIST_OF_DAYS_OF_THE_WEEK :
			globalState.week = action.weeks
		break;
		case constant.UPDATE_SYSTEM_MESSAGE :
			globalState.alertSistem.locale = action.locale
			globalState.alertSistem.alert = action.alert
			globalState.alertSistem.prop = action.prop
			globalState.alertSistem.isOpen = action.status
		break;
		default:
			return state
	}
	return globalState
}