import React from 'react';
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { Paper, Box, Fade, Grid, Typography, Chip, Hidden } from '@material-ui/core';
import { submitRoleAndPermissionsForm, updateStateRole } from '../../redux/actions/RolesAndPermissionsAction'
import Autocomplete from '../../components/Autocomplete'
import Button from '../../components/Button'
import TextField from '../../components/TextField'


const CreateRole = ({ dispatch, permissions, newRoleForm, saveForm, nameRole, permissionsRole }) => {

  const [manageErrors, setManageErrors] = React.useState({})
  const history = useHistory()

  React.useEffect(()=>{
    dispatch({ type : 'CLEAN_FORM_ROLE_AND_PERMISSIONS', cleanForm : { permissions : [] } })
// eslint-disable-next-line react-hooks/exhaustive-deps
  },[])

  React.useEffect(() => {
    if( saveForm ){
      history.push('/admin/roles/list')
    }
// eslint-disable-next-line react-hooks/exhaustive-deps
  },[saveForm])

  const handlerFormRole = ({attr,value}) => {
    const copyRoleForm = {...newRoleForm}
    copyRoleForm[attr] = value
    dispatch(updateStateRole('newRole',copyRoleForm))
  }
  const submitRoleForm = ( e ) => {
    e.preventDefault()
    // setManageErrors({})
    // if( !Boolean(newRoleForm.rolename) ){
    //   setManageErrors(manageErrors => ({...manageErrors, rolename : 'Debe definir el nombre del rol' }))
    //   return
    // }else{
    //   if( newRoleForm.rolename === "" || newRoleForm.rolename.trim() === "" || newRoleForm.rolename.length <= 0  ){
    //     setManageErrors(manageErrors => ({...manageErrors, rolename : 'El nombre del rol no es valido' }))
    //     return
    //   }else{
    //     if(newRoleForm.permissions.length === 0){
    //       setManageErrors(manageErrors => ({...manageErrors, permissions : 'Seleccione los permisos para el rol' }))
    //       return
    //     }
    //   }
    // }
    // dispatch( submitRoleAndPermissionsForm(newRoleForm) )
  }
 
  return (
    <Fade in={true} >
        <React.Fragment>
          <form action="#" onSubmit={ e => submitRoleForm(e) } >
              <Typography variant="subtitle1" > Crear Rol </Typography>
              <Paper elevation={4} >
                  <Box p={2} >
                    <Grid container spacing={2} justify="center" >
                        <Grid item xs={10} >
                            <Grid container spacing={2} justify="center" >
                                <Grid item xs={12} md={6} >
                                    <TextField
                                        error={ manageErrors.rolename && Boolean(manageErrors.rolename) }
                                        helperText={ Boolean(manageErrors.rolename) && manageErrors.rolename }
                                        fullWidth
                                        variant="outlined"
                                        name="name"
                                        label="Rol"
                                        placeholder="Ej : Cajero"
                                        onChange={handlerFormRole}
                                        value={nameRole}
                                    />
                                    <Hidden smDown>
                                      <Box margin="auto" mt={3} >
                                        <Button type="submit" color="primary" variant="outlined" > Registrar </Button>
                                      </Box>
                                    </Hidden>
                                </Grid>
                                <Grid item xs={12} md={6} >
                                    <Autocomplete
                                        name="permissions"
                                        value={permissionsRole}
                                        onChange={(e, newValue) => {
                                            e.value = newValue
                                            handlerFormRole(e)
                                          }
                                        }
                                        disableCloseOnSelect
                                        multiple
                                        options={permissions}
                                        getOptionLabel={(option) => option.name}
                                        filterSelectedOptions
                                        label="Permisos"
                                        renderTags={(value, getTagProps) =>
                                          value.map((option, index) => (
                                            <Chip
                                              size="small"
                                              variant="outlined"
                                              label={option.name}
                                              {...getTagProps({ index })}
                                              color="primary"
                                              
                                            />
                                          ))
                                        }
                                    />
                                </Grid>
                                <Hidden mdUp>
                                  <Grid item xs={12} >
                                    <Box margin="auto" >
                                      <Button type="submit" color="primary" variant="outlined" > Registrar </Button>
                                    </Box>
                                  </Grid>
                                </Hidden>
                            </Grid>
                        </Grid>
                    </Grid>
                  </Box>
              </Paper>
          </form>
        </React.Fragment>
    </Fade>
  );
}

export default connect(state => {
  return {
    permissions : state.dataState.permissions,
    newRoleForm : state.rolesAndPermissionsState.newRole,
    nameRole : state.rolesAndPermissionsState.newRole.name,
    permissionsRole : state.rolesAndPermissionsState.newRole.permissions,
    saveForm : state.rolesAndPermissionsState.saveForm,
  }
},null)(CreateRole)