import React from 'react';
import CreateCategory from '../create_categories/Form'

const Form = ({ callFormEditCategorie, categorySelected, storeUpdatedCategory, categoryUpdate, categoryUpdateName, categoryUpdateActive }) => {
    return(
        <>
            <CreateCategory
                editForm
                callFormEditCategorie={callFormEditCategorie}
                categorySelected={categorySelected}
                storeUpdatedCategory={storeUpdatedCategory}
                categoryUpdate={categoryUpdate}
                categoryUpdateName={categoryUpdateName}
                categoryUpdateActive={categoryUpdateActive}
            />
        </>
    )
}

export default Form