import React from 'react'
import { Autocomplete as Select, createFilterOptions } from '@material-ui/lab'
import { Box, IconButton, makeStyles, Paper, TextField } from '@material-ui/core'
import ArrowDropDownRoundedIcon from '@material-ui/icons/ArrowDropDownRounded';
import AddCircleOutlineRoundedIcon from '@material-ui/icons/AddCircleOutlineRounded';
import Tooltip from './Tooltips';

const CustomPaper = (props) => {
    return <Paper elevation={0} {...props} />;
};

const useStyles = makeStyles((theme) => ({
    resultsAutocomplete : {
        border : `1px solid ${theme.palette.grey[400]}`,
        fontWeight : "bold"
    }
}))

const Autocomplete = ({noOptionsText = 'Lista vacia',options=[], optionLabel="name", error = false,
helperText = false, label='', onChange=false, fieldName="_id", value=false, name="", filterOptions={}, required=false,
defaultValue={}, renderOption=false, renderTags=false, multiple=false, disableCloseOnSelect=false, open=false, ListboxProps=false,
onAdd=false, textAddButton, mb=2, groupBy, ListboxComponent, onInputChange, inputValue }) => {
    const selectName = React.useRef()
    const createFilterUnique = createFilterOptions(filterOptions)
    const clasess = useStyles()
    return(
        <Box display="flex" mb={mb} width="100%" >
            <Select
                {...(ListboxComponent ? {ListboxComponent : ListboxComponent} : {})}
                popupIcon={<ArrowDropDownRoundedIcon />}
                classes={{
                    paper : clasess.resultsAutocomplete
                }}
                PaperComponent={CustomPaper}
                disableCloseOnSelect={disableCloseOnSelect}
                multiple={multiple}
                ref={selectName}
                {...(Object.keys(filterOptions).length >= 1 ? {filterOptions : createFilterUnique} : {})}
                noOptionsText={noOptionsText}
                {...(groupBy ? {groupBy : groupBy} : {})}
                {...(value ? {value : value} : {})}
                {...(ListboxProps ? {ListboxProps : ListboxProps} : {})}
                {...(open ? {open : true} : {})}
                {...(renderTags ? {renderTags : renderTags} : {})}
                defaultValue={defaultValue}
                fullWidth
                options={options}
                name={name}
                getOptionLabel={(option) => option[optionLabel] || ""}
                onInputChange={onInputChange}
                onChange={ (n,evt) => {
                    const e = evt??[]
                    if( Boolean(e) ){
                        const objectName = selectName.current.getAttribute('name')
                        const data = { value : multiple ? e : e[fieldName], attr : objectName }
                        if(typeof onChange === 'function'){
                            return onChange(data,e)
                        }else{
                            return
                        }
                    }
                }}
                {...( renderOption && { renderOption } )}
                renderInput={(params) => (
                    <TextField
                        required={required}
                        error={error}
                        helperText={helperText}
                        {...params}
                        label={label}
                        {...(inputValue ? {onChange: inputValue}: {})}
                        variant="outlined"
                    />
                )}
            />
            {typeof onAdd === "function" &&
                <Tooltip title={textAddButton} >
                    <Box display="flex" alignItems="center" >
                        <IconButton color="primary" size='small' onClick={() => onAdd()} >
                            <AddCircleOutlineRoundedIcon />
                        </IconButton>
                    </Box>
                </Tooltip>
            }
        </Box>
    )
}

export default Autocomplete
