import constant from "./index";
import store from "../store";
import parseData from "../../methods/parseData";
import Market from "../../views/market/class";
import Sockets from "../../socket/Socket";
import { callNotificationSnackbar } from "./DashboardAction";

export const activeMarket = (status) => {
    return { type : constant.ACTIVE_VIEW_MARKET, status }
}
export const updateProductIdentifierValue = (value) => {
    return {type: constant.UPDATE_PRODUCT_IDENTIFIER_VALUE, value }
}
export const activateResultList = (value) => {
    return {type: constant.ACTIVATE_RESULTS_LIST, value }
}
export const updateResults = (value) => {
    return {type: constant.UPDATE_RESULTS, value }
}
export const activateEmptyResultsAlert = (value) => {
    return {type: constant.ACTIVATE_EMPTY_RESULTS_ALERT, value }
}
export const lowerInPositionOfTheList = () => {
    const productsResults = store.getState().Market.productsResults
    const listPosition = store.getState().Market.selectedProductConfiguration.listPosition
    const successfulSearch = store.getState().Market.successfulSearch
    
    if( productsResults.length >= 1 && successfulSearch){
        if( listPosition+1 <= productsResults.length-1 ){
            return {type: constant.LOWER_IN_POSITION_OF_THE_LIST, value : listPosition+1}
        }else{
            return {type: constant.LOWER_IN_POSITION_OF_THE_LIST, value : listPosition}
        }
    }else{
        return {type: constant.LOWER_IN_POSITION_OF_THE_LIST, value : 0}
    }
}
export const goUpInPositionOfTheList = () => {
    const listPosition = store.getState().Market.selectedProductConfiguration.listPosition
    const productsResults = store.getState().Market.productsResults
    const successfulSearch = store.getState().Market.successfulSearch
    if( productsResults.length >= 1 && successfulSearch){
        if( listPosition >= 1 ){
            return {type: constant.GO_UP_IN_POSITION_OF_THE_LIST, value: listPosition-1}
        }else{
            return {type: constant.GO_UP_IN_POSITION_OF_THE_LIST, value: listPosition}
        }
    }else{
        return {type: constant.GO_UP_IN_POSITION_OF_THE_LIST, value: 0}
    }
}
export const updateSelectedProduct = (value) => {
    return {type : constant.UPDATE_SELECTED_PRODUCT, value}
}
export const activateManualPrice = () => {
    const isManualPrice = store.getState().Market.selectedProductConfiguration.isManualPrice
    const successfulSearch = store.getState().Market.successfulSearch
    if( successfulSearch ){
        return { type: constant.ACTIVATE_MANUAL_PRICE, value: !isManualPrice }
    }else{
        return { type: constant.ACTIVATE_MANUAL_PRICE, value: isManualPrice }
    }
}
export const restoreInitialSettings = () => {
    return {type: constant.RESTORE_INITIAL_SETTINGS }
}
export const changeDefaultPrice = () => {
    const positionPrice = store.getState().Market.selectedProductConfiguration.positionPrice
    const listPosition = store.getState().Market.selectedProductConfiguration.listPosition
    const isManualPrice = store.getState().Market.selectedProductConfiguration.isManualPrice
    const productsResults = store.getState().Market.productsResults
    const successfulSearch = store.getState().Market.successfulSearch
    const productSelected = store.getState().Market.productsResults[listPosition]
    const priceList = productSelected.prices

    if( successfulSearch && productsResults.length >= 1 && !isManualPrice){
        if( positionPrice === priceList.length-1) return {type: constant.CHANGE_DEFAULT_PRICE, position : 0 }
        return {type: constant.CHANGE_DEFAULT_PRICE, position : positionPrice+1 }
    }else{
        return {type: constant.CHANGE_DEFAULT_PRICE, position : 0 }
    }

}
export const changeDefaultUnitOfMeasure = () => {
    const successfulSearch = store.getState().Market.successfulSearch
    const unitOfMeasurePosition = store.getState().Market.selectedProductConfiguration.unitOfMeasurePosition
    
    if( successfulSearch ){
        const listPosition = store.getState().Market.selectedProductConfiguration.listPosition
        const productsResults = store.getState().Market.productsResults
        const productSelected = productsResults[listPosition]
        const productUnitSize = productSelected.unitSize
        
        if( Boolean(productUnitSize.baseUnit) && unitOfMeasurePosition === 0 ){
            return {type: constant.CHANGE_DEFAULT_UNIT_OF_MEASURE, position : 1 }
        }else{
            return {type: constant.CHANGE_DEFAULT_UNIT_OF_MEASURE, position : 0 }
        }
    }else{
        return {type: constant.CHANGE_DEFAULT_UNIT_OF_MEASURE, position : unitOfMeasurePosition }
    }
}
export const saveProductInShoppingCard = () => async (dispatch) => {
    try{
        const shoppingList = parseData(store.getState().Market.shoppingList)
        const productsResults = parseData(store.getState().Market.productsResults)
        const selectedProductConfiguration = parseData(store.getState().Market.selectedProductConfiguration)
        const isFocusTable = parseData(store.getState().Market.isFocusTable)
        const position = isFocusTable ? 0 : selectedProductConfiguration.listPosition
        const selectedProduct = productsResults[position]
        const existingProduct = shoppingList.find(product => product.product._id === selectedProduct._id)

        if( Boolean(existingProduct) ){
            for(let index in shoppingList){
                if( shoppingList[index].product._id === selectedProduct._id){
                    shoppingList[index].product = selectedProduct
                    shoppingList[index].config = selectedProductConfiguration
                    dispatch(updateProductsToShoppingList(shoppingList))
                    dispatch(activateResultList(false))
                }
            }
        }else{
            dispatch(addProductsToShoppingList({
                product: selectedProduct,
                config: selectedProductConfiguration
            }))
            dispatch(activateResultList(false))
        }
    }catch(err){
        console.log(err)
    }
}
