export const lowerInPositionOfThePaymentMethod = () => {
    const methodPosition = store.getState().Market.paymentMethods.methodPosition
    const methods = store.getState().Market.paymentMethods.methods
    
    if( methodPosition+1 <= methods.length-1 ){
        return {type: constant.UPDATE_PAYMENT_METHOD_POSITION, value : methodPosition+1}
    }else{
        return {type: constant.UPDATE_PAYMENT_METHOD_POSITION, value : methodPosition}
    }
    
}
export const goUpInPositionOfThePaymentMethod = () => {
    const methodPosition = store.getState().Market.paymentMethods.methodPosition
    if( methodPosition >= 1 ){
        return {type: constant.UPDATE_PAYMENT_METHOD_POSITION, value: methodPosition-1}
    }else{
        return {type: constant.UPDATE_PAYMENT_METHOD_POSITION, value: methodPosition}
    }
}
