import React from 'react'
import { Tooltip as Tool, withStyles } from '@material-ui/core'

const HtmlTooltip = withStyles((theme) => ({
tooltip: {
    backgroundColor: '#f5f5f9',
    color: 'rgba(0, 0, 0, 0.87)',
    maxWidth: 220,
    fontSize: theme.typography.pxToRem(12),
    border: '1px solid #dadde9',
},
}))(Tool);

export const CustomizeTooltip = ({ children,title="", placement="top", classes={} }) =>{
    return (
        <HtmlTooltip
            classes={classes}
            placement={placement}
            title={
                <React.Fragment>
                    {title}
                </React.Fragment>
            }
        >
            {children}
        </HtmlTooltip>
    )
}

const Tooltip = ({children,title="", placement="top", open }) => {
    return(
        <Tool title={title} placement={placement} {...(open && {open})} >
            {children}
        </Tool>
    )
}

export default Tooltip
