import history from '../../handlers/history'
import parseData, { createFormData } from '../../methods/parseData'
import store from '../../redux/store'
import { priceList as priceTemplate } from '../../redux/reducers/ProductsReducer'
import { activeModalLoadMasiveProducts } from '../../redux/actions/ProductsAction'
import * as XLSX from 'xlsx';
import app from '../../connection/Api'

function validateStateProduct(status){
    const values = ['new', 'use']
    const isString = typeof status === "string" ? status : null
    const isValue = Boolean(isString) ? values.find(val => val === isString ) : false
    if( Boolean(isValue) ){
        return true
    }else{
        return false
    }
}
function validateCompountProduct(compounts){
    if( Array.isArray(compounts) && compounts.length >= 1 ){
        let newCompount = []
        for( let item of compounts ){
            newCompount.push({
                product : item.product._id,
                quantity : item.quantity,
                positionUnitSize : item.positionUnitSize,
                main : item.main
            })
        }
        return newCompount
    }else{
        return false
    }
}
function validateCategoryOrUnitSize(category, unitSize){
    if(typeof category === "string" || typeof unitSize === "string" ){
        if( category.trim() !== "" || unitSize.trim() !== "" ){
            return true
        }else{
            return false
        }
    }else{
        return false
    }
    
}
function validateNameProduct(name){
    const isString = typeof name === "string" ? true : false
    const isValid = isString ? (name.trim() === "" ? false : true) : false
    return isValid
}
function validatePriceList(priceList){
    if( Array.isArray(priceList) && priceList.length >= 1 ){
        let cost = priceList[0].cost
        if( cost <= 0 || typeof cost !== "number" ) return false
        for( let prices of priceList ){
            if( typeof prices.identifier !== "string" && prices.identifier.trim() === "") return false
        }
        return true
    }else{
        return false
    }
    
}
function validateStocks(stock, alert, useStock){
    if( useStock ){
        if( typeof stock !== "number" && typeof alert !== "number" ){
            return false
        }else{
            if( stock <= 0 ){
                return false
            }else{
                return true
            }
        }
    }else{
        return true
    }
}
function validateForm(form, typePage, compount){
    const compountList = store.getState().CompountProduct.create_product_compount.compount
    const images = store.getState().Products.images
    let result = {
        ...form.productBasicData,
        prices : [...form.priceList],
        calculate : form.calculatePrice,
        stock : form.stock,
        alert : form.stockAlert,
        useStock : form.useStock,
        images,
    }
    result.category = result.category._id
    result.unitSize = result.unitSize._id
    if( typePage === "update" ) result._id = form._id

    const errors = []
    const compountProductList = validateCompountProduct(compountList)
    const status = validateStateProduct(result.status)
    const selectableValue = validateCategoryOrUnitSize(result.category, result.unitSize)
    const nameProduct = validateNameProduct(result.name)
    const priceList = validatePriceList(result.prices)
    const stocks = validateStocks(result.stock, result.alert, result.useStock)
    
    if( !status ) errors.push({field: "status"})
    if( !selectableValue ) errors.push({field: "selectable"})
    if( !nameProduct ) errors.push({field: "name-product"})
    if( !priceList ) errors.push({field: "price-list"})
    if( !stocks ) errors.push({field: "stock"})
    if( compount ){
        if( !compountProductList ) errors.push({field: "compount"})
        result.isCompount = true
        result.compount = compountProductList
    }

    if( errors.length >= 1 ){
        return {
            status : false,
            data : errors
        }
    }else{
        return {
            status : true,
            data: result
        }
    }
}
function addNewPrice(){
    const priceList = parseData(store.getState().Products.createProduct.form.priceList)
    if( priceList.length <= 4 ){
        const prices = {...priceTemplate}
        prices.identifier = `Precio ${priceList.length+1}`
        delete prices.cost
        priceList.push({...prices})
        return priceList
    }else{
        return false
    }
}
function removeOnePrice(index){
    const priceList = parseData(store.getState().Products.createProduct.form.priceList)
    priceList.splice(index, 1);
    return priceList
}
function updatePriceList(index, attr, value){
    const priceList = parseData(store.getState().Products.createProduct.form.priceList)
    const calculatePrice = parseData(store.getState().Products.createProduct.form.calculatePrice)
    if( attr !== "identifier") value = parseInt(value)
    priceList[index][attr] = value

    if( calculatePrice ){
        if(attr === "cost" || attr === "percentageProfit"){
            const cost = parseInt(priceList[0].cost)
            const percentageProfit = parseInt(priceList[index].percentageProfit)
            if( typeof percentageProfit === "number" && typeof cost === "number" ){
                if( cost > percentageProfit && percentageProfit > 0 ){
                    const salePrice = calcSalePrice(cost, percentageProfit)
                    const profit = calcProfit(salePrice, cost)
                    priceList[index].salePrice = salePrice
                    priceList[index].profit = profit
                }
            }
        }
    }else{
        if(attr === "cost" || attr === "salePrice"){
            const cost = parseInt(priceList[0].cost)
            const salePrice = parseInt(priceList[index].salePrice)
            if( typeof salePrice === "number" && typeof cost === "number" ){
                if( salePrice >= cost ){
                    const profit = calcProfit(salePrice, cost)
                    const profitPercentaje = calcProfitPercentaje(cost, salePrice)
                    priceList[index].percentageProfit = profitPercentaje
                    priceList[index].profit = profit
                }
            }
        }
    }

    return priceList
}
