async function getProduct(page=1, limit=10,id,name,skucode,provcode,isCompount){
    try{
        let url = "/get/products?";
        if( Boolean(page) && Boolean(limit) ) url = url+`page=${page}&limit=${limit}&`;
        if( Boolean(id) ) url = url+`_id=${id}&`
        if( Boolean(name) ) url = url+`name=${name}&`
        if( Boolean(skucode) ) url = url+`skucode=${skucode}&`
        if( Boolean(provcode) ) url = url+`provcode=${provcode}&`
        if( isCompount === true || isCompount === false ) url = url+`isCompount=${isCompount}&`
        const data = await app.get(url);
        if(Boolean(data)) return data;
    }catch(err){
        console.log(err)
    }
}
async function saveProduct(product, images, typePage="create", compount){
    try{
        const validate = validateForm(product, typePage, compount)
        let payload = null
        let result = null
        if( validate.status ){
            if( typePage === "create" ){
                payload = createFormData(validate.data, images)
                result = await app.post('/save/product', payload, false)
            }else{
                payload = createFormData([validate.data], images)
                // payload =  [validate.data]
                result = await app.post('/update/products', payload, false)
            }
            return result
        }
    }catch(err){
        console.log(err)
    }
}
async function searchProductFast(text, page=1, limit=10, validateElem=true){
    try {
        const elem = document.getElementById('productInput')
        if( !Boolean(elem) && validateElem) throw "No existe el elemento"
        if( typeof text !== "string" || text.length <= 0 || text.trim() === "") return
        const valid = validateElem ? elem === elem.ownerDocument.activeElement : true
        if( valid ){
            const result = await app.get(`/get/products?page=${page}&limit=${limit}&textFilter=${text}`)
            return result
        }
        return
    } catch (error) {
        console.log(error)
    }
}
async function deleteProduct(products, isCompount){
    try {
        let identifiers = []
        for( let product of products ){
            if( !Boolean(product._id) ){
                identifiers = products
                break;
            }
            identifiers.push(product._id)
        }
        const result = await app.post('/delete/products', {identifiers})
        return {identifiers, result}
    } catch (error) {
        console.log(error)
    }
}
