import React from 'react'
import TextField from '../../../components/TextField'
import Button from '../../../components/Button'
import { Box, Link } from '@material-ui/core'
import locale from '../../../handlers/locale'
import { ProgressSectionApp } from '../../../components/ProgressApp'
import { connect } from 'react-redux'
import { updateAuthState, userLogIn } from '../../../redux/actions/AuthAction'

const Form = ({ dispatch, preloaderLogin, email, password, login, register }) => {
    const handlerChange = (data) => {
        const cloneLogin = {...login}
        const { form } = cloneLogin
        form[data.attr] = data.value
        cloneLogin.form = form
        dispatch(updateAuthState('login',cloneLogin))
    }
    const handleSubmit = async (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        dispatch(userLogIn({password, username : email}))
    }
    return(
        <>
            <form onSubmit={handleSubmit}>
                <TextField
                    value={email}
                    type="email"
                    variant="outlined"
                    required
                    label={locale.email}
                    autoFocus
                    name="email"
                    onChange={handlerChange}
                    // error={ errors[inputUsername] && Boolean(errors[inputUsername]) }
                    // helperText={ Boolean(errors[inputUsername]) && errors[inputUsername] }
                />
                <TextField
                    type="password"
                    value={password}
                    required
                    label={locale.password}
                    name="password"
                    onChange={handlerChange}
                    // error={ errors[inputPassword] && Boolean(errors[inputPassword]) }
                    // helperText={ Boolean(errors[inputPassword]) && errors[inputPassword] }
                />
                <Box my={1} width="100%" >
                    <Button type="submit" variant="contained" color="primary" size="large" > { locale.getInto } </Button>
                </Box>
                <Box my={1} width="100%" >
                    <Button type="button" variant="outlined" color="secondary" size="large" > Soy cliente </Button>
                </Box>
                <Link href="#" variant="body2" onClick={ _ => dispatch(updateAuthState('register',{...register, formBasicInformation : true})) } >
                    {locale.iDonotHaveAnAccount}
                </Link>
                <Link href="#" variant="body2">
                    { locale.forgotPassword }
                </Link>
            </form>
            { preloaderLogin && <ProgressSectionApp label="Ingresando..." /> }
        </>
    )
}

export default connect(state => {
    return {
        preloaderLogin : state.authState.login.preloaderLogin,
        login : state.authState.login,
        email : state.authState.login.form.email,
        password : state.authState.login.form.password,
        register : state.authState.register,
    }
},null)(Form)