import React from 'react'
import { connect } from 'react-redux'
import { Campaign } from './icons'
import Menu from './Menu';
import { EmptyNotifications } from './NotificationsItems';
import NotificationsActiveIcon from '@material-ui/icons/NotificationsActive';

const Notifications = () => {
	return(
		<>
			<Menu
				title="Notificaciones"
				width={400}
				maxHeight={100}
				iconComponent={<Campaign width="25px" />}
			>
				<EmptyNotifications Icon={NotificationsActiveIcon} message="No tienes notificaciones" />
			</Menu>
		</>
	)
}

export default connect(null,null)(Notifications)