import React from 'react'
import TextField from '../../../components/TextField'
import Button from '../../../components/Button'
import { Box, InputAdornment } from '@material-ui/core'
import { Visibility, VisibilityOff } from '@material-ui/icons';
import { connect } from 'react-redux';
import { consultUser, updateAuthState } from '../../../redux/actions/AuthAction';

const Form = ({ dispatch, register, showPassword, username, email, password, repeatPassword, errors }) => {
    
    const handlerForm = (data) => {
        const _register = JSON.parse(JSON.stringify(register))
        _register.form[data.attr] = data.value
        dispatch(updateAuthState('register',_register))
    }

    const getDataUser = (data) => {
        dispatch(consultUser(data, register))
    }


    return(
        <>
        {/* <form action="#" onSubmit={ e => submitForm(e) } > */}
            <TextField
                error={errors.username && Boolean(errors.username)}
                helperText={Boolean(errors.username) && errors.username}
                value={username}
                onChange={handlerForm}
                name={"username"}
                variant="outlined"
                autoFocus
                label="Nombre de usuario"
                required
                onBlur={getDataUser}
                InputProps={{
                    startAdornment: 
                    <InputAdornment position="start" >
                        @
                    </InputAdornment>
                }}
            />
            <TextField
                error={errors.email && Boolean(errors.email)}
                helperText={Boolean(errors.email) && errors.email}
                value={email}
                onBlur={getDataUser}
                onChange={handlerForm}
                name="email"
                variant="outlined"
                autoFocus
                type="email"
                label="Correo electronico"
                required
            />
            <TextField
                error={errors.password && Boolean(errors.password)}
                helperText={Boolean(errors.password) && errors.password}
                value={password}
                onChange={handlerForm}
                name="password"
                variant="outlined"
                label="Contraseña"
                type={showPassword ? "text" : "password" }
                InputProps={{
                    endAdornment: 
                    <InputAdornment
                        position="start"
                        onClick={ _ => dispatch(updateAuthState('register',{...register, showPassword : !showPassword})) }
                        style={{ cursor : 'pointer' }} 
                    >
                        { showPassword ? <VisibilityOff /> : <Visibility /> }
                    </InputAdornment>
                }}
                required
            />
            <TextField
                error={errors.repeatPassword && Boolean(errors.repeatPassword)}
                helperText={Boolean(errors.repeatPassword) && errors.repeatPassword}
                value={repeatPassword}
                onChange={handlerForm}
                name="repeatPassword"
                label="Repetir contraseña"
                type={showPassword ? "text" : "password" }
                required
            />
            <Box my={1} width="100%" >
                <Button type="submit" size="large" >
                    Completar mi perfil
                </Button>
            </Box>
        {/* </form> */}
        </>
    )
}

export default connect(state => {
    return {
        errors : state.authState.register.errors,
        register : state.authState.register,
        username : state.authState.register.form.username,
        email : state.authState.register.form.email,
        password : state.authState.register.form.password,
        repeatPassword : state.authState.register.form.repeatPassword,
        showPassword : state.authState.register.showPassword,
    }
},null)(Form)