import { Box, IconButton } from '@material-ui/core'
import React from 'react'
import { CashRegisterSVG } from '../../components/icons'
import Tooltip from '../../components/Tooltips'
import ClearIcon from '@material-ui/icons/Clear';
import Text from '../../components/Text';
import { useDispatch } from 'react-redux';

const Header = ({...actions}) => {

    const { marketWindw } = actions

    return(
        <>
            <Box display="flex" >
                <Box flexGrow={1} display="flex" alignItems="center" >
                    <CashRegisterSVG width="50px" />
                    <Text text='Registrar ventas' fontSize={24} mb={0} ml={2} />
                </Box>
                <Box>
                    <Tooltip title="Cerrar" placement="left-start" >
                        <IconButton color="primary" onClick={() => marketWindw(false)} >
                            <ClearIcon />
                        </IconButton>
                    </Tooltip>
                </Box>
            </Box>
        </>
    )
}

export default Header