import { combineReducers } from 'redux'
import drawerHandler from './DrawerReducer'
import handlerAuthentication from './AuthReducer'
import UserInformationReducer from './UserInformationReducer'
import BusinessReducer from './BusinessReducer'//Reemplazar por BusinessReducer
import DashboardReducer from './DashboardReducer'
import CategoriesReducer from './CategoriesReducer'
import ProductsReducer from './ProductsReducer'
import RolesAndPermissionsReducer from './RolesAndPermissionsReducer'
import MarketReducer from './MarketReducer'
import EmployedReducer from './EmployedReducer'
import PricesReducer from './PricesReducer'
import VariantsReducer from './VariantsReducer'
import UnitSizeReducer from './UnitSizeReducer'
import ReportsReducer from './ReportsReducer'
import compountProductReducer from './CompountProductReducer'

const reducers = combineReducers({
	Business : BusinessReducer,
	Categories : CategoriesReducer,
	Products : ProductsReducer,
	CompountProduct : compountProductReducer,
	Variants : VariantsReducer,
	UnitSize : UnitSizeReducer,
	Reports : ReportsReducer,
	Dashboard : DashboardReducer,
	sidebarState : drawerHandler,
	authState : handlerAuthentication,
	dataState : UserInformationReducer,
	PriceState : PricesReducer,
	rolesAndPermissionsState : RolesAndPermissionsReducer,
	Market : MarketReducer,
	EmployedState : EmployedReducer,
})

export default reducers