import React from 'react'
import { connect } from 'react-redux'
import { submitNewBusinessType, updateFormCreateBusinessType } from '../../../redux/actions/BusinessAction'
import Form from './Form'

const mapPropsToState = (state) => {
    return {
        name : state.Business.createBusinessType.businessTypeFormData.name,
        errors : state.Business.createBusinessType.errors
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        submitNewBusinessType : (data) => dispatch(submitNewBusinessType(data)),
        updateFormCreateBusinessType : (attr, value) => dispatch(updateFormCreateBusinessType(attr, value)),
    }
}

const CreateBusinessType = connect(mapPropsToState, mapDispatchToProps)(({ updateFormCreateBusinessType, updateModalStatusCreateBusinessType, submitNewBusinessType, errors,...businessTypeFormData }) => {
    const submitForm = (event) => {
        event.preventDefault()
        event.stopPropagation()
        submitNewBusinessType(businessTypeFormData)
    }    
    return(
        <>
            <form onSubmit={submitForm} >
                <Form errors={errors} updateFormCreateBusinessType={updateFormCreateBusinessType} {...businessTypeFormData} />
            </form>
        </>
    )

})

export default CreateBusinessType