import actionType from './index'
import app from '../../connection/Api'
import Validation from '../../methods/Validation'
import store from '../store'

export const updateTheStatusOfTheBusinessesRequest = (status) => {
    return { type : actionType.UPDATE_THE_STATUS_OF_THE_BUSINESSES_REQUEST, status }
}
export const updateTheStatusOfTheFormToCreateBusiness = (status) => {
    return { type : actionType.UPDATE_THE_STATUS_OF_THE_FORM_TO_CREATE_BUSINESS, status }
}
export const loadBusinessTypes = (businessTypes) => {
    return { type : actionType.LOAD_BUSINESS_TYPES, businessTypes }
}
export const loadBusiness = (business) => {
    return { type : actionType.LOAD_BUSINESS, business }
}
export const addANewBusinessToTheList = (business) => {
    return { type : actionType.ADD_A_NEW_BUSINESS_TO_THE_LIST, business }
}
export const updateFormCreateBusiness = (attr, value) => {
    return { type : actionType.UPDATE_FORM_CREATE_BUSINESS, attr, value }
}
export const getBusinessTypes = () => (dispatch) => {
    try{
        app.get('/get/business/types')
        .then(businessTypes => {
            if( businessTypes.status === 108 ){
                dispatch(loadBusinessTypes(businessTypes.payload))
            }
        })
        .catch(err => console.log(err))
    }catch(err){
        console.log(err)
    }
}
export const updateFormCreateBusinessType = (attr, value) => {
    return { type : actionType.UPDATE_FORM_CREATE_BUSINESS_TYPE, attr, value }
}
export const updateListOfErrorsInTypeOfBusiness = (errors) => {
    return { type : actionType.UPDATE_LIST_OF_ERRORS_IN_TYPE_OF_BUSINESS, errors }
}
export const addANewTypeOfBusinessToTheList = (businessType) => {
    return { type : actionType.ADD_A_NEW_TYPE_OF_BUSINESS_TO_THE_LIST, businessType }
}
export const clearStatusCreatBusinessType = () => {
    return { type : actionType.CLEAR_STATUS_CREAT_BUSINESS_TYPE }
}
export const updateModalStatusCreateBusinessType = (status) => {
    return { type : actionType.UPDATE_MODAL_STATUS_CREATE_BUSINESS_TYPE, status }
}
export const submitNewBusinessType = (data) => (dispatch) => {
    try{
        const validate = new Validation()
        const result = validate.validateText(data.name)

        if( result ){
            dispatch(updateListOfErrorsInTypeOfBusiness({}))
            app.post('/save/business/types', data)
            .then(businessTypes => {
                if(businessTypes.status === 106){
                    dispatch(updateFormCreateBusiness('businessType', businessTypes.payload))
                    dispatch(addANewTypeOfBusinessToTheList(businessTypes.payload))
                    dispatch(clearStatusCreatBusinessType())
                }
            })
            .catch(err => console.log(err))
        }else{
            let errors = {}
            errors.name = "Este campo es requerido, no acepta datos numericos"
            dispatch(updateListOfErrorsInTypeOfBusiness(errors))
        }

    }catch(err){
        console.log(err)
    }
}
export const updateWorkingHours = (type="", businessHours) => {
    const workingHours = store.getState().Business.createBusiness.businessFormData.workingHours
    let timetable;
    if(type === "add"){ 
        timetable = [...workingHours, { day : businessHours.day, open : "", close : "" }]
    }else if(type === "remove"){
        let newArray = workingHours.filter(d => d.day !== businessHours.day)
        timetable = newArray
    }
    return { type : actionType.UPDATE_WORKING_HOURS, timetable }
}
export const updateListOfErrorsInCreateBusiness = (errors) => {
    return { type : actionType.UPDATE_LIST_OF_ERRORS_IN_CREATE_BUSINESS, errors }
}
export const submitNewBusiness = (data) => (dispatch) => {
    try{
        app.post('/save/business/user', data, false)
        .then(response => {
            if(response.status === 106){
                dispatch(addANewBusinessToTheList(response.payload))
                dispatch(updateTheStatusOfTheFormToCreateBusiness(false))
            }
        })
        .catch(err => console.log(err))

    }catch(err){
        console.log(err)
    }
}
export const getBusiness = () => (dispatch) => {
    try{
        app.get('/find/user/business')
        .then(business => {
            if( business.status === 108 ){
                dispatch(loadBusiness(business.payload))
                if( business.payload.length <= 0 ){
                    dispatch(updateTheStatusOfTheFormToCreateBusiness(true))
                }else{
                    dispatch(updateTheStatusOfTheFormToCreateBusiness(false))
                }
                dispatch(updateTheStatusOfTheBusinessesRequest(true))
            }
        })
        .catch(err => console.log(err))
    }catch(err){
        console.log(err)
    }
}