import React from 'react'
import { connect } from 'react-redux'
import CreateProfile from '../CreateProfile'
import Register from '../Register'
import { updateAuthState } from '../../../redux/actions/AuthAction'
import UpdateProfilePhoto from '../UpdateProfilePhoto'

const Modals = ({ dispatch, activeModals, formBasicInformation, formCompleteProfile, formFirstBusiness, addPhoto }) => {
    
    const runModals = React.useCallback(() => {
        if(formBasicInformation || formCompleteProfile || formFirstBusiness || addPhoto){
            dispatch(updateAuthState('activeModals',true))
        }else{
            setTimeout(() => {
                dispatch(updateAuthState('activeModals',false))
            },400)
        }
    },[formBasicInformation, formCompleteProfile, formFirstBusiness, addPhoto])

    React.useEffect(() => {
        runModals()
    },[runModals])
    return(
        <>
            { activeModals &&
                <>
                    <UpdateProfilePhoto />
                    <Register />
                    <CreateProfile />
                </>
            }
        </>
    )
}

export default connect(state => {
    return {
        activeModals : state.authState.activeModals,
        formBasicInformation : state.authState.register.formBasicInformation,
        formCompleteProfile : state.authState.register.formCompleteProfile,
        formFirstBusiness : state.authState.register.formFirstBusiness,
        addPhoto : state.authState.register.addPhoto,
    }
},null)(Modals)