import constant from '../actions/index'

export const priceList = {
	identifier : 'Precio 1',
	cost : '',
	salePrice : '',
	percentageProfit : "",
	profit : "",
}
const productBasicData = {
	status : "new",
	category : {},
	name : "",
	skucode : "",
	providercode : "",
	unitSize : {},
	description : "",
}
const productForm = {
	_id: null,
	productBasicData : {...productBasicData},
	variants : {},
	priceList : [{...priceList}],
	stock : 0,
	stockAlert : 5,
	calculatePrice : false,
	useStock : true,
}

const initialState = {
	list: [],
	images: [],
	viewDataProduct : {
		isOpenModal : false,
		productSelected: {}
	},
	createProduct : {
		variantsList : [],
		form : {...productForm}
	},
	loadProducts : {
		isOpenModal : false,
	},
	deleteProduct:  {
		product: [],
		isOpenAlert: false,
		waitingAnswer: false
	},
	pagination: {
		page: 1,
		sizeTable: 10,
		totalResults: 10,
	},
	typePage : "create",
	productSelecteds: [],
	clearDataForm : false,
	doNotUseStock : false,
	variants : [],
	activePopupVariant : false,
}

export default function ProductsReducer(state = initialState, action){
	const globalState = {...state}

	switch(action.type){
		case constant.ACTIVE_MODAL_LOAD_MASIVE_PRODUCTS :
			globalState.loadProducts.isOpenModal = action.status
		break
		case constant.UPDATE_PRODUCT_PRICE_LIST :
			globalState.createProduct.form.priceList = action.list
		break
		case constant.CALCULATE_PRICE_PER_PERCENTAGE :
			globalState.createProduct.form.calculatePrice = action.status
		break
		case constant.UPDATE_VARIANT_LIST :
			globalState.createProduct.variantsList = [...globalState.createProduct.variantsList, ...action.list]
		break
		case constant.CLEAR_VARIANT_LIST :
			globalState.createProduct.variantsList = []
		break
		case constant.UPDATE_VALUE_USE_STOCK :
			globalState.createProduct.form.useStock = action.status
		break
		case constant.UPDATE_PRODUCT_FORM :
			globalState.createProduct.form[action.attr] = action.value
		break
		case constant.SAVE_PRODUCTS :
			globalState.list = action.products
		break
		case constant.LOAD_PRODUCT_DATA :
			globalState.createProduct.form.productBasicData.status = action.status
			globalState.createProduct.form.productBasicData.category = action.category
			globalState.createProduct.form.productBasicData.name = action.name
			globalState.createProduct.form.productBasicData.skucode = action.skucode
			globalState.createProduct.form.productBasicData.providercode = action.providercode
			globalState.createProduct.form.productBasicData.unitSize = action.unitSize
			globalState.createProduct.form.productBasicData.description = action.description
			globalState.createProduct.form.priceList = action.prices
			globalState.createProduct.form.stock = action.stock
			globalState.createProduct.form.useStock = action.useStock
			if( Boolean(action.images) ) globalState.images = action.images
			// globalState.createProduct.form.stockAlert = action.stockAlert
			// globalState.createProduct.form.calculatePrice = action.calculatePrice
		break
		case constant.RESTORE_PRODUCT_DATA :
			productForm.priceList = [{...priceList}]
			productForm.productBasicData = {...productBasicData}
			globalState.typePage = "create"
			globalState.createProduct.form = {...productForm}
			globalState.images = []
		break
		case constant.SELECT_PRODUCT_TO_DELETE :
			globalState.deleteProduct.product = action.product
		break
		case constant.SHOW_ALERT_TO_DELETE_PRODUCT :
			globalState.deleteProduct.isOpenAlert = action.value
		break
		case constant.WAITING_ANSWER :
			globalState.deleteProduct.waitingAnswer = action.value
		break
		case constant.UPDATE_PAGE :
			globalState.pagination.page = action.page
		break
		case constant.UPDATE_TOTAL_ROWS :
			globalState.pagination.sizeTable = action.totalRows
		break
		case constant.UPDATE_TOTAL_RESULTS :
			globalState.pagination.totalResults = action.totalResults
		break
		case constant.UPDATE_PRODUCT_SELECTEDS :
			globalState.productSelecteds = action.products
		break
		case constant.UPDATE_TYPE_PAGE :
			globalState.typePage = action.page
		break
		case constant.APPLY_PRODUCT_IDENTIFIER :
			globalState.createProduct.form._id = action.id
		break
		case constant.CHANGE_PRODUCT_INFORMATION_WINDOW_STATUS :
			globalState.viewDataProduct.isOpenModal = action.value
		break
		case constant.SELECT_PRODUCT_INFORMATION :
			globalState.viewDataProduct.productSelected = action.product
		break
		default :
			return globalState
	}

	return globalState
}