import React from 'react'
import { Box, Dialog, DialogContent, IconButton, Divider, Typography } from '@material-ui/core'
import { connect } from 'react-redux'
import { updateAuthState } from '../../redux/actions/AuthAction'
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import { svgImages } from '../../components/icons'
import Tooltip from '../../components/Tooltips'
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import DeleteIcon from '@material-ui/icons/Delete';

const UpdateProfilePhoto = ({ dispatch, addPhoto, register }) =>{

    const [photo, setPhoto] = React.useState(false)

    const loadPhotoProfile = () => {
        const l = document.getElementById('file')??null
        if( Boolean(l) ){
            l.click()
        }
    }

    const handlerChangeImage = (evt) => {
        const image = evt.target.files[0]
        if( Boolean(image) ){
            let pathImage = URL.createObjectURL(image)
            if( Boolean(pathImage) ){
                setPhoto(pathImage)
                let _register = JSON.parse(JSON.stringify(register))
                const i = {
                    blob : pathImage,
                    name : image.name,
                    type : image.type
                }
                _register.form.profilePhoto = i
                dispatch(updateAuthState('register', _register))
            }
        }
    }
    const clearImage = () => {
        const inputFile = document.getElementById('file')
        if( Boolean(inputFile) ){
            inputFile.value = ""
            setPhoto(false)
            let _register = JSON.parse(JSON.stringify(register))
            _register.form.profilePhoto = {}
            dispatch(updateAuthState('register', _register))
        }
    }
    const closeWindow = () => {
        dispatch(updateAuthState('register',{...register, addPhoto : false}))
    }
    React.useEffect(() => {
        return () =>{
            clearImage()
        }
    },[])

    return(
        <>
            <Dialog fullWidth maxWidth="sm" open={addPhoto} onClose={ _ => null } >
                <DialogContent >
                    <input id="file" type="file" accept=".jpg,.jpeg,.png" style={{ display : "none" }} onChange={handlerChangeImage} />
                    <Box display="flex" justifyContent="space-between" alignItems="center" >
                        <Box width="48px" />
                        <Typography variant="subtitle1" >
                            <Box textAlign="center" fontSize={22} >
                                FOTO DE PERFIL
                            </Box>
                        </Typography>
                        <Tooltip title="Cerrar" placement="left" >
                            <IconButton color="secondary" onClick={closeWindow} >
                                <CloseIcon />
                            </IconButton>
                        </Tooltip>
                    </Box>
                    <Box mb={2} mt={1} >
                        <Divider />
                    </Box>
                    <Box display="flex" alignItems="center" flexDirection="column" >
                        <Box display="inline-flex" >
                            <Box border={1} borderColor="grey.400" display="inline-flex" maxWidth={150} minWidth={150} minHeight={150} maxHeight={150} width="100%" height="100%" overflow="hidden" borderRadius="50%" alignItems="center" justifyContent="center" >
                                {photo ? 
                                    <>
                                        <img src={photo} style={{ objectFit: "cover" }} />
                                    </> : 
                                    <>
                                        <img src={svgImages.camera} style={{ objectFit: "cover", width : "70%" }} />
                                    </>
                                }
                            </Box>
                        </Box>
                        <Box mt={1} >
                            <Tooltip title="Cargar foto" placement="left">
                                <IconButton color="primary" onClick={loadPhotoProfile} >
                                    <AddAPhotoIcon />
                                </IconButton>
                            </Tooltip>
                            {photo &&
                                <Tooltip title="Borrar foto" placement="right">
                                    <IconButton color="secondary" onClick={clearImage} >
                                        <DeleteIcon />
                                    </IconButton>
                                </Tooltip>
                            }
                        </Box>
                    </Box>
                    <Box mt={2} mb={1} >
                        <Divider />
                    </Box>
                </DialogContent>
            </Dialog>
        </>
    )
}

export default connect(state => {
    return{
        register : state.authState.register,
        addPhoto : state.authState.register.addPhoto,
    }
},null)(UpdateProfilePhoto)