import app from '../../connection/Api'
import history from '../../handlers/history'
import parseData from '../../methods/parseData'
import { activateModalCreateCategory } from '../../redux/actions/CategoriesAction'
import store from '../../redux/store'

const dispatch = (action) => store.dispatch(action)

function validateCategorieList(categorieList){
    let index = 0
    let result = { result : [], status : false, errors : {} }
    let categories = parseData(categorieList)
    for(let data of categories){
        if( index ===  0 ){
            if( !Boolean(data.name) || data.name.trim() === "" ){
                result.errors = { name : true,  position : 0 }
                return result
            }
        }else{
            if( !Boolean(data.name) || data.name.trim() === "" ){
                delete categories[index]
            }
        }
        index += 1
    }
    result.status = true
    result.result = categories.filter(c => Boolean(c))
    return result
}
async function bringCategories(page=null, limit=null, match=null){
    try{
        let url = "/get/categories?";
        if( Boolean(page) && Boolean(limit) ) url = url+`page=${page}&limit=${limit}&`;
        if( Boolean(match) ) url = url+`match=${match}&`
        const data = await app.get(url);
        if(Boolean(data)) return data;
    }catch(err){
        console.log(err)
    }
}
export default function Categorie(){
    this.validateCategorieList = validateCategorieList
    this.bringCategories = bringCategories
}

export function goManagecategories(categories, businessName){
    if( categories.length >= 1 ){
        history.push(`/${businessName}/manage/categories`)
    }else{
        const message = "¡No tienes Categorias creadas!, Registra una o mas categorias primero!"
        dispatch(activateModalCreateCategory(true, message, 'warning'))
    }
}
export function goCreateCategory(){
    dispatch(activateModalCreateCategory(true))
}