import React from 'react'
import { Box, Grid } from '@material-ui/core'
import { Checkbox } from '../../../components/Checkbox'
import Text from '../../../components/Text'
import TextField from '../../../components/TextField'

const WorkingHours = ({ days, week, disabledTitle, updateListOfDaysOfTheWeek, updateWorkingHours, updateFormCreateBusiness, workingHours }) => {

    const handlerDays = (checked, index) => {
        let _week = [...week]
        _week[index].active = checked
        updateListOfDaysOfTheWeek(_week)

        if( checked ){
            updateWorkingHours('add', week[index])
        }else{
            updateWorkingHours('remove', week[index])
        }
    }

    const handlerWorkingHoursData = (hour, index) => {
        const dayIndex = workingHours.findIndex(d => d.day === index)
        let _workingHours = [...workingHours]
        _workingHours[dayIndex][hour.attr] = hour.value
        updateFormCreateBusiness('workingHours', _workingHours)
    }

    return(
        <>
            { !disabledTitle && <Text text='Horario de trabajo (opcional) :' uppercase /> }
            {week.map( (w,i) => {
                let _dayData = workingHours.filter(d => d.day === w.day)
                let dayData = _dayData.length === 1 ? _dayData[0] : {}
                return(
                    <Box key={i} >
                        <Grid container >
                            <Grid item xs={12} sm={3} md={3} lg={3} >
                                <Checkbox checked={w.active} color='secondary' label={days[i]} onChange={(check) => handlerDays(check, i)} />
                            </Grid>
                            <Grid item xs={12} sm={9} md={9} lg={9} >
                                <Grid container spacing={1} >
                                    <Grid item xs={6} >
                                        <TextField onChange={(val) => handlerWorkingHoursData(val, w.day)} name="open" type='time' label='Abre' size="small" disabled={!w.active} value={dayData.open} InputLabelProps={{ shrink: true }} />
                                    </Grid>
                                    <Grid item xs={6} >
                                        <TextField onChange={(val) => handlerWorkingHoursData(val, w.day)} name="close" type='time' label='Cierra' size="small" disabled={!w.active} value={dayData.close} InputLabelProps={{ shrink: true }} />
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Box>
                )
            } )}
        </>
    )
}

export default WorkingHours