import React from 'react'
import { Box, IconButton } from '@material-ui/core'
import Tooltip from './Tooltips'
import Chip from './Chip'
import CloudUploadOutlinedIcon from '@material-ui/icons/CloudUploadOutlined';
import * as XLSX from 'xlsx';

const LoadFile = (props) => {
    const { icon, title="Cargar archivo", accept=".*", onReader=null } = props
    const [sheet, setSheet] = React.useState({})
    const fileInput = React.useRef()
    const handlerLoadFile = () => fileInput.current.click()

    const onFile = (evt) => {
        const file = evt.target.files[0]
        setSheet(file)
        if( Boolean(file) ){
            let readerFile = new FileReader()
            let data = []
            readerFile.onload = event => {
                try{
                    const { result } = event.target;
                    const workbook = XLSX.read(result, { type: 'binary' });
                    for (const sheet in workbook.Sheets) {
                        if (workbook.Sheets.hasOwnProperty(sheet)) {
                            data = data.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
                            break;
                        }
                    }
                    if( data.length >= 1 ){
                        if( typeof onReader === "function" ){
                            onReader(data)
                        }
                    }
                }catch(err){
                    console.log(err)
                }
            }
            readerFile.readAsBinaryString(file);
        }
    }
    
    const clearFile = () => {
        setSheet({})
        setSheet([])
    }

    return(
        <>
            <Box>
                {   Boolean(sheet.name) &&
                        sheet.name.split('.')[1] === "xlsx" ?
                    <>
                        <Chip label={sheet.name} size="medium" color='secondary' onDelete={clearFile} />
                    </> :
                    <>
                        <Tooltip title={title} placement='bottom' >
                            <IconButton color='secondary' onClick={handlerLoadFile} >
                                { icon??<CloudUploadOutlinedIcon />  }
                            </IconButton>
                        </Tooltip>
                        <input style={{ display: "none" }} ref={fileInput} type='file' accept={accept} onChange={onFile} />
                    </>
                }
            </Box>
        </>
    )
}

export default LoadFile