import React from 'react'
import { Box, Typography, useTheme } from '@material-ui/core'

const HeaderForm = () => {
    const theme = useTheme()
    return(
        <>
            <Box display="flex" justifyContent="start" width="100%" >
                <Typography component="span" >
                    <Box fontSize={22} fontWeight="bold" style={{ color : theme.palette.secondary.main }} >
                        Bienvenido
                    </Box>
                    <Box fontSize={16} fontWeight="bold" style={{ color : theme.palette.grey[700] }} >
                        Inicia sesion en tu cuenta
                    </Box>
                </Typography>
            </Box>
            <Box display="flex" justifyContent="center" width="100%" my={2} >
                <Typography component="span" >
                    <Box fontSize={15} fontWeight="bold" style={{ color : theme.palette.secondary.main }} >
                        INGRESA CON TU CORREO
                    </Box>
                </Typography>
            </Box>
        </>
    )
}

export default HeaderForm