import constant from "."
import { variants } from "../reducers/VariantsReducer"
import store from '../store'
import parseData from '../../methods/parseData'
import app from '../../connection/Api'

export const activateModalCreateVariant = (status) => {
    return { type : constant.ACTIVATE_MODAL_CREATE_VARIANT, status }
}
export const activateModalUseColorPalette = (status, index) => {
    if( typeof index !== "number" ) index = 0
    return { type : constant.ACTIVATE_MODAL_USE_COLOR_PALETTE, status, index }
}
export const addNewVariantToTheList = () => {
    let variant = {...variants}
    return { type : constant.ADD_NEW_VARIANT_TO_THE_LIST, variant }
}
export const removeVariantToTheList = (position) => {
    return { type : constant.REMOVE_VARIANT_TO_THE_LIST, position }
}
export const addColorPaletteToVariant = (color, variantIndex) => {
    const variantsList = parseData(store.getState().Variants.create_variant.variants)
    variantsList[variantIndex].useColorPalette = true
    variantsList[variantIndex].colorHex = color
    return {type : constant.ADD_COLOR_PALETTE_TO_VARIANT, list: variantsList}
}
export const removeColorPaletteToVariant = (variantIndex) => {
    const variantsList = parseData(store.getState().Variants.create_variant.variants)
    variantsList[variantIndex].useColorPalette = false
    variantsList[variantIndex].colorHex = ""
    return {type : constant.ADD_COLOR_PALETTE_TO_VARIANT, list: variantsList}
}
export const searchListedVariantsCategory = (status) => {
    return {type : constant.SEARCH_LISTED_VARIANTS_CATEGORY, status}
}
export const updateVariantsData = (payload, index) => {
    const variantsList = parseData(store.getState().Variants.create_variant.variants)
    variantsList[index][payload.attr] = payload.value
    return {type : constant.UPDATE_VARIANTS_DATA, variants: variantsList}
}
export const updateSearchVariant = (text) => {
    return {type : constant.UPDATE_SEARCH_VARIANT, text}
}
export const updateVariantsListPagePosition = (page) => {
    if(typeof page !== "number") page = parseInt(page)
    if( page < 0 ) page = 0
    return {type : constant.UPDATE_VARIANTS_LIST_PAGE_POSITION, page}
}
export const updateDataOfTheRequestForVariants = (data) => {
    return {type : constant.UPDATE_DATA_OF_THE_REQUEST_FOR_VARIANTS, data}
}
export const loadVariantsToTheList = (variants) => {
    return {type : constant.LOAD_VARIANTS_TO_THE_LIST, variants}
}
export const updateNameOfNewVariantCategory = (name) => {
    return {type : constant.UPDATE_NAME_OF_NEW_VARIANT_CATEGORY, name}
}
export const updateVariantCategory = (variantCategory) => {
    return {type : constant.UPDATE_VARIANT_CATEGORY, variantCategory}
}
export const selectVariantCategory = (variantCategory) => (dispatch) => {
    dispatch(updateNameOfNewVariantCategory(variantCategory._id))
    dispatch(updateVariantCategory(variantCategory))
}
export const submitVariants = (payload) => (dispatch) => {
    try{
        app.post('/save/variant', payload)
        .then(response => {
            console.log(response)
        })
        .catch(err => console.log(err))
    }catch(err){
        console.log(err)
    }
}
export const getVariants = (limit, page, variant_id, variantCategory_id, match) => (dispatch) => {
    try{
        let url = `/get/variants?`
        if( Boolean(limit) ) url = url+`limit=${limit}&`
        if( Boolean(page) ) url = url+`page=${page}&`
        if( Boolean(variant_id) ) url = url+`variant_id=${variant_id}&`
        if( Boolean(variantCategory_id) ) url = url+`variantCategory_id=${variantCategory_id}&`
        if( Boolean(match) ) url = url+`match=${match}&`
        app.get(url)
        .then(response => {
            if(response.status){
                dispatch(loadVariantsToTheList(response.payload))
                dispatch(updateDataOfTheRequestForVariants(response.data))
            }
        })
        .catch(err => console.log(err))
    }catch(err){
        console.log(err)
    }
}