import app from '../../connection/Api'
import Reports from '../../views/reports/class'
import { getBusiness } from './BusinessAction'
import { getCategories } from './CategoriesAction'
import constant from './index'

export const getReportsOfTheWeeks = (dates) => async (dispatch) => {
	try{
		let listReports = []
		const report = new Reports()

		for(let i of dates){
			const result = await report.filter(i, i)
			if( !result.status ){
				listReports.push([])
			}else{
				listReports.push(result.payload)
			}
		}

		let saleTotal = []
		let profitTotal = []
		let percentageTotal = []
		if(listReports.length === 7){
			for(let sale of listReports){
				saleTotal.push(report.calculateDaySale(sale))
				profitTotal.push(report.calculateProfitDay(sale))
				percentageTotal.push(report.calculateProfitPercentageDay(sale))
			}
		}else{
			throw "Error, no se obtuvieron los reportes"
		}

		if(saleTotal.length === 7){
			dispatch(addTotalSaleOfTheWeek(saleTotal, profitTotal, percentageTotal))
		}else{
			throw "Error, no se calcularon los reportes"
		}
	}catch(err){
		console.log(err)
	}
}
export const addTotalSaleOfTheWeek = (listRerporForWeek, weeklyEarningsList, weeklyProfitPercentageList) => {
	return {type: constant.ADD_TOTAL_SALE_OF_THE_WEEK, listRerporForWeek, weeklyEarningsList, weeklyProfitPercentageList }
}

