//Cargar tipos de negocios en el estado

export const loadBusinessTypesOnState = (list) => {
	return { type : constant.LOAD_BUSINESS_TYPES_ON_STATE, list }
}

//Obtener lista de tipos de negocios

export const getBusinessTypes = () => (dispatch) => {
	try{
		app.get(`/get/list/businessTypes`)
		.then(businessTypes => {
			switch(businessTypes.code){
				case 108:
					dispatch({
						type : constant.LOAD_BUSINESS_TYPES_ON_STATE,
						list : businessTypes.data.businessTypes
					})
				break
				default:
					return
			}
		})
	}catch(err){
		console.log(err)
	}
}

