import React from 'react';
import { connect } from 'react-redux'
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import { Drawer, CssBaseline, Toolbar } from '@material-ui/core';
import CollapseList from './CollapseList'
import { ProgressSectionApp } from '../../../components/ProgressApp'
import Media from 'react-media'
import DrawerRigth from '../../../components/DrawerRigth';

const Sidebar = ({ sidebarState, children, userData, businessActive }) => {
  const useStyles = makeStyles((theme) => ({
    root: {
      display: 'flex',
    },
    drawer: {
      width: sidebarState.drawerWidth,
      flexShrink: 0,
    },
    drawerMovile: {
      width: '100%',
      flexShrink: 0,
    },
    drawerPaper: {
      width: sidebarState.drawerWidth,
    },
    drawerHeader: {
      display: 'flex',
      alignItems: 'center',
      padding: theme.spacing(0, 1),
      ...theme.mixins.toolbar,
      justifyContent: 'flex-end',
    },
    content: {
      backgroundColor : "#FFFFFF",
      flexGrow: 1,
      padding: theme.spacing(3),
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      marginLeft: -sidebarState.drawerWidth,
    },
    contentShift: {
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      marginLeft: 0,
    },
    drawerContainer: {
      overflow: 'auto',
    },
  }));

  const classes = useStyles();
 
  const [display, setDisplay] = React.useState(false)

  return (
    <div className={classes.root}>
      <CssBaseline />
      
      <Media 
        queries={{
          small: "(max-width: 599px)",
          medium: "(min-width: 600px) and (max-width: 2199px)",
        }}
        onChange={ matches => matches.small ? setDisplay(true):setDisplay(false) 
          }
      >
        {matches => (
          <React.Fragment>
            {matches.small &&
              <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={!sidebarState.drawerState}
                classes={{
                  paper: classes.drawerMovile,
                }}
              >
                <Toolbar />
                <CollapseList device={display} />
              </Drawer>
            }
            {matches.medium &&
              <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={sidebarState.drawerState}
                classes={{
                  paper: classes.drawerPaper,
                }}
              >
                
                <CollapseList userData={userData} businessActive={businessActive} />
              </Drawer>
            }
          </React.Fragment>
        )}
      </Media>
      <main
        className={ clsx(classes.content, !display && { [classes.contentShift]: sidebarState.drawerState,})}
      >
        <div className={classes.drawerHeader} />

        { children }

      </main>
        {/* <DrawerRigth /> */}
    </div>
  );
}

export default connect( state => {
  return{
    sidebarState : state.sidebarState,
  } 
})(Sidebar)