import constant from '../actions/index';

const initialState = {
	drawerState : true,
	drawerWidth : 240
}

export default function drawerHandler( state = initialState, action ){
	let globalState = {...state}
	switch (action.type) {
		case constant.UPDATE_DRAWER_STATE:
			globalState.drawerState = !state.drawerState
		break;
		default:
			return state
	}
	return globalState
}