import constant from '../actions/index'

const initialState = {
	progress : {
		status : false,
		message : ''
	},
	countries : [],
	cities : [],
	regions : [],
	business : [],
	categories : [],
	products : [],
	prices : [],
	permissions : [],
}

export default function userInformationReducer(state = initialState, action){
	const globalState = {...state}
	switch(action.type){
		case constant.LOAD_BUSINESS:
			globalState.business = action.data
		break
		case constant.AWAIT_DATA:
			globalState.progress['status'] = true
			globalState.progress['message'] = "Cargando negocios"
		break
		case constant.DATA_OBTAINED:
			globalState.progress['status'] = false
			globalState.progress['message'] = ""
		break
		case constant.LOAD_CATEGORIES:
			globalState.categories = action.categories
		break
		case constant.LOAD_PRODUCTS:
			globalState.products = action.products
		break
		case constant.LOAD_DATA_IN_INFORMATION_STATE:
			globalState[action.state] = action.value
		break
		default :
		return globalState
	}
	return globalState
}