import React from 'react';
import { Box, Grid } from '@material-ui/core';
import ItemCard from '../../../components/ItemCard';
import { FooterItemCardActionsCategories, FooterItemCardActionsCompountProducts, FooterItemCardActionsCustomers, FooterItemCardActionsProducts, FooterItemCardActionsReports, FooterItemCardActionsUnitSize, FooterItemCardActionsVariants } from './FooterItemCardActions';
import { goManagecategories, goCreateCategory } from '../../categories/class'
import { goCreateProduct, goManageProducts, loadProductsFileWindow } from '../../products/class';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { svgImages } from '../../../components/icons'
import { goCreateVariant, goManageVariants } from '../../variants/class';
import { goCreateUnitSize, goManageUnitSize } from '../../unitSize/class';
import { goCreateCompountProduct, goManageCompountProduct } from '../../compountproducts/class';
import { goTableReports } from '../../reports/class';
import { getToday, getYesterday } from '../../../methods/Moment';
import history from '../../../handlers/history';

const mapPropsToState = (state) => {
    return {
        categories: state.Categories.categories
    }
}

const ManageBusiness = connect(mapPropsToState, null)((props) => {
    const { categories } = props
    const { businessName } = useParams()
    return(
        <Box>
            <Grid container spacing={2} >
                <ItemCard
                    title='Categorias'
                    icon={svgImages.categories}
                    actions={
                        <FooterItemCardActionsCategories
                            manageCategorie={() => goManagecategories(categories, businessName)}
                            createCategorie={() => goCreateCategory()}
                        />
                    }
                />
                <ItemCard
                    title='Productos'
                    icon={svgImages.product}
                    actions={
                        <FooterItemCardActionsProducts
                            manageProducts={() => goManageProducts(businessName)}
                            createProducts={() => goCreateProduct(businessName) }
                            loadProductFile={() => loadProductsFileWindow() }
                        />
                    }
                />
                <ItemCard
                    title='Productos compuestos'
                    icon={svgImages.compount}
                    actions={
                        <FooterItemCardActionsCompountProducts
                            manageCompountProducts={() => goManageCompountProduct(businessName)}
                            createCompountProducts={() => goCreateCompountProduct(businessName) }
                        />
                    }
                />
                {/* <ItemCard
                    title='Variantes'
                    icon={svgImages.variants}
                    actions={<FooterItemCardActionsVariants
                                manageVariants={() => goManageVariants(businessName)}
                                createVariant={() => goCreateVariant()}
                            />}
                /> */}
                <ItemCard
                    title='Unidades de medidas'
                    icon={svgImages.unitsType}
                    actions={<FooterItemCardActionsUnitSize
                        createUnitSize={() => goCreateUnitSize()}
                        manageUnitSize={() => goManageUnitSize(businessName)}
                    />}
                />
                <ItemCard
                    title='Clientes'
                    icon={svgImages.customer}
                    actions={ <FooterItemCardActionsCustomers /> }
                />
                <ItemCard
                    title='Reportes de ventas'
                    icon={svgImages.reports}
                    actions={ <FooterItemCardActionsReports
                        manageReportsForTwoDays={() => goTableReports(businessName, getYesterday(), getToday())}
                    /> }
                />
                <ItemCard
                    title='Roles y permisos'
                    icon={svgImages.role}
                    actions={
                        <FooterItemCardActionsCompountProducts
                            manageCompountProducts={() => goManageCompountProduct()}
                            createCompountProducts={() => goCreateCompountProduct(businessName) }
                        />
                    }
                />
                <ItemCard
                    title='Vender'
                    icon={svgImages.cashRegister}
                    pointer
                    onClick={() => history.push(`${history.location.pathname}/market`)}
                />
                <ItemCard
                    title='Gastos'
                    icon={svgImages.bills}
                    actions={
                        <FooterItemCardActionsCompountProducts
                            manageCompountProducts={() => goManageCompountProduct()}
                            createCompountProducts={() => goCreateCompountProduct(businessName) }
                        />
                    }
                />
            </Grid>
        </Box>
    )
})

export default ManageBusiness