import React from 'react'
import { Button as Btn, CircularProgress, IconButton } from '@material-ui/core'
import CloseIcon from '@material-ui/icons/Close';

//size : small, medium, large

export const ClearButton = ({ size="small", color="primary", onClick=false }) => {
    return(
        <IconButton color={color} size={size} onClick={ _ => typeof onClick === "function" && onClick()} >
            <CloseIcon />
        </IconButton>
    )
}

export const LoadingButton = ({ variant="contained", color="primary", size='medium', fullWidth=true, disabled=false }) => {
    return(
        <Btn
            disabled={true}
            fullWidth={fullWidth}
            variant={variant}
            color={color}
            type="submit"
            size={size}
        >
            <CircularProgress size="small" style={{ width : "25px", height : "25px", marginLeft : "25px" }} />
        </Btn>
    )
}

const Button = ({ children, variant="contained", color="primary", size='medium', fullWidth=true, startIcon, disabled=false, style=false, onClick=false, type="submit" }) => {
    return(
        <Btn
            onClick={() => {
                if( typeof onClick === "function" ){
                    onClick()
                    return
                }else{
                    return
                }
            }}
            disabled={disabled}
            {...(startIcon ? { startIcon } : {})}
            {...(style ? { style } : {})}
            fullWidth={fullWidth}
            variant={variant}
            color={color}
            type={type}
            size={size}
        >
            { children }
        </Btn>
    )
}

export default Button