import React from 'react';
import { Grid, Paper, Box } from '@material-ui/core'
import Autocomplete from '../../../components/Autocomplete'
import TextField from '../../../components/TextField';
import RadioButton from '../../../components/RadioButton';
import Dropzone, { ImageGalerie } from '../../../components/Dropzone';
import parseData from '../../../methods/parseData'
import { DOMAIN_SERVER } from '../../../connection/Api';
import Product from '../class';

const BasicForm = ({categories, unitSize, productBasicData={}, images, ...actions}) => {
    const { getCategories, getUnitSize, updateProductForm, setImage, callNotificationSnackbar, loadProductData } = actions
    const { status, category, name, skucode, providercode, unitSize : unitSizeValue, description } = productBasicData
    const product = new Product()
    React.useEffect(() => {
        getCategories()
        getUnitSize()
    },[])
    const handlerDataForm = ({attr, value}) => {
        const data = parseData(productBasicData)
        data[attr] = value
        updateProductForm('productBasicData', data)
    }
    return(
        <>
            <Paper elevation={0} variant="outlined" >
                <Box display="flex" flexDirection="column" p={2} >
                    <Grid container spacing={1} >
                        <Grid item xs={12} >
                            <RadioButton
                                value={status}
                                label="Estado"
                                required
                                collection={[
                                    {label : "Nuevo", value : "new"},
                                    {label : "Usado", value : "use"},
                                ]}
                                onChange={handlerDataForm}
                                name="status"
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <Autocomplete
                                required
                                label="Categoria"
                                options={categories}
                                value={category}
                                name="category"
                                onChange={(e,a) => handlerDataForm({attr:e.attr, value:a})}
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <TextField
                                required
                                type="text"
                                onChange={handlerDataForm}
                                label="Nombre del producto"
                                value={name}
                                name="name"
                            />
                        </Grid>
                        <Grid item xs={6} >
                            <TextField
                                label="Codigo SKU"
                                onChange={handlerDataForm}
                                value={skucode}
                                name="skucode"
                            />
                        </Grid>
                        <Grid item xs={6} >
                            <TextField
                                label="Codigo proveedor"
                                onChange={handlerDataForm}
                                value={providercode??""}
                                name="providercode"
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <Autocomplete
                                required
                                label="Unidad de medida"
                                options={unitSize}
                                value={unitSizeValue}
                                onChange={(e,a) => handlerDataForm({attr:e.attr, value:a})}
                                name="unitSize"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                label="Descripcion del producto"
                                rows={4}
                                multiline
                                value={description}
                                onChange={handlerDataForm}
                                name="description"
                            />
                        </Grid>
                        { images.length >= 1 &&
                            <Grid container spacing={2} justify='center' >
                                {images.map((item, index) => {
                                    return(
                                        <Grid item xs={2} key={index} >
                                            <ImageGalerie
                                                image={`${DOMAIN_SERVER}${item}`}
                                                createURL={false}
                                                onClick={ async (img) => {
                                                    const arr = item.split("/")
                                                    const result = await product.deleteFile(arr[arr.length-2], arr[arr.length-1])
                                                    if( result.status ){
                                                        loadProductData(result.payload)
                                                    }
                                                } }
                                            />
                                        </Grid>
                                    )
                                })}
                            </Grid>
                        }
                        <Grid item xs={12}>
                            <Dropzone
                                onChange={(imgCollection) => setImage(imgCollection.value)}
                                name="imagesProduct"
                                reachedLimit={images.length === 5}
                            />
                        </Grid>
                    </Grid>
                </Box>
            </Paper>
        </>
    )
}

export default BasicForm