import React from 'react';
import { FullProgress } from './ProgressApp';
import Dialog from './Dialog';
import AlertMUI from '@material-ui/lab/Alert';
import { Box } from '@material-ui/core';

const Alert = ({isOpen, title='Mensaje del sistema', progress=false, component, severity="error", children, textAlert, close=() => null}) => {
    return(
        <>
            <Dialog component={component} title={title} open={isOpen} close={close} >
                <Box mb={1} >
                    {progress && <FullProgress message='Procesando solicitud...' />}
                    <AlertMUI severity={severity}>
                        { textAlert }
                    </AlertMUI>
                    {children}
                </Box>
            </Dialog>
        </>
    )
}

export default Alert