import React from 'react'
import { Box, Typography, makeStyles } from '@material-ui/core'
import clsx from 'clsx'

const useStyles = makeStyles(theme => ({
    upperCase : {
        textTransform : "uppercase"
    },
    fontSizeSmall : {
        fontSize : 16
    },
    fontSizeNormal : {
        fontSize : 18
    },
    fontSizeText : {
        fontSize : 20
    },
    fontSizeModalTitle : {
        fontSize : 24
    }
}))

const Text = ({ text="", uppercase, fontSize=false, mb=2, ml=0, color="#000", lineHeight=1.75, mt=0 }) => {
    const clasess = useStyles()

    const style = clsx({
        [clasess.upperCase] : uppercase,
        [clasess.fontSizeNormal] : !fontSize,
        [clasess.fontSizeModalTitle] : fontSize === "modal-title",
        [clasess.fontSizeText] : fontSize === "text",
        [clasess.fontSizeSmall] : fontSize === "small",
    })

    return(
        <>
            <Typography variant='subtitle1' component="span" style={{lineHeight}} >
                <Box style={{ color }} display="inline-flex" className={style} mb={mb} ml={ml} mt={mt} >
                    {text}
                </Box>
            </Typography>
        </>
    )
}

export default Text