import React from 'react'
import Button from '../../../components/Button'
import Alert from '../../../components/Alert'
import { Grid, Box } from '@material-ui/core'

const DeleteUnitSizes = ({ isOpenModal, unitSizeSelected, multipleUnitsSizeSelected, ...actions }) => {
    const { openWindowDeleteUnitOfMeasurement, selectUnitOfmeasurementToDelete, selectMultipleUnitsOfMeasurement, deleteUnitSize } = actions
    const messageA = `DESEA ELIMINAR LA UNIDAD DE MEDIDA "${unitSizeSelected.name}"`
    const messageB = multipleUnitsSizeSelected.length === 1 ? `DESEA ELIMINAR LA UNIDAD DE MEDIDA SELECCIONADA` : `DESEA ELIMINAR LAS ${multipleUnitsSizeSelected.length} UNIDADES DE MEDIDA SELECCIONADAS`
    const messageResult = multipleUnitsSizeSelected.length >= 1 ? messageB : messageA
    const onDeleteProduct = (evt) => {
        if(Boolean(evt) && Boolean(evt.preventDefault)) evt.preventDefault()
        deleteUnitSize(unitSizeSelected, multipleUnitsSizeSelected)
    }
    React.useEffect(() => {
        if( !isOpenModal ){
            selectUnitOfmeasurementToDelete({})
        }
    },[isOpenModal])
    return(
        <>
            <form action='#' method='post' onSubmit={onDeleteProduct} >
                <Alert severity='error' isOpen={isOpenModal} textAlert={messageResult} close={() => openWindowDeleteUnitOfMeasurement(false)} >
                    <Box mt={2} >
                        <Grid container spacing={2} >
                            <Grid item xs={6} >
                                <Button size='large' variant='outlined' onClick={onDeleteProduct} >Eliminar</Button>
                            </Grid>
                            <Grid item xs={6} >
                                <Button type='button' size='large' variant='outlined' color='secondary' onClick={() => openWindowDeleteUnitOfMeasurement(false)} >Cerrar</Button>
                            </Grid>
                        </Grid>
                    </Box>
                </Alert>
            </form>
        </>
    )
}

export default DeleteUnitSizes