import React from 'react'
import { ListItem as Item, ListItemIcon, ListItemText } from '@material-ui/core'

const ListItem = ({onClick, icon, text}) => {
    return(
        <Item button onClick={ () => typeof onClick === "function" && onClick()  }>
            {icon && <ListItemIcon> {icon} </ListItemIcon>}
			<ListItemText primary={text} />
		</Item>
    )
}

export default ListItem