import React from 'react'
import { Avatar, Box, makeStyles, Typography } from '@material-ui/core'

const useStyles = makeStyles( theme => ({
	root : {
		cursor : 'pointer',
		transition: theme.transitions.create(['background-color'], {
			duration: theme.transitions.duration.shorter,
		}),
		'&:hover' : {
			backgroundColor : theme.palette.grey[200]
		}
	}
}))

export const EmptyNotifications = ({Icon, message}) => {
	return(
		<Box p={2} display="flex" flexDirection="column" justifyItems="center" alignItems="center" >
			<Box >
				<Icon style={{ color: "#0003", fontSize: '3rem' }} />
			</Box>
			<Typography style={{ color: "#00000070", userSelect : "none" }}>{message}</Typography>
		</Box>
	)
}

export const StandartNotification = ({ image, username, notificationMessage }) => {
	const styles = useStyles()
	return (
		<Box display="flex" p={1} className={styles.root} >
			<Box> <Avatar src={image} /> </Box>
			<Box ml={1} >
				<Typography component="span" >
					<Box fontWeight="bold" color="grey.800" fontSize={15} >
						{username}
					</Box>
				</Typography>
				<Typography component="span" >
					<Box color="grey.600" fontSize={12} >
						{notificationMessage}
					</Box>
				</Typography>
			</Box>
		</Box>
	)
}