import React from 'react'
import { Avatar, Chip as Chips } from '@material-ui/core'

const Chip = ({ color="default", variant="outlined", size="small", label, onClick, onDelete, avatar=false }) => {
    return(
        <Chips
            {...(avatar && { avatar : <Avatar>{avatar}</Avatar> })}
            label={label}
            color={color}
            variant={variant}
            size={size}
            {...(onDelete ? { onDelete : () => {
                if(typeof onDelete === "function" ){
                    onDelete()
                }
            }} : {})}
            {...(onClick ? { onClick : () => {
                if(typeof onClick === "function" ){
                    onClick()
                }
            }} : {})}
        />
    )
}

export default Chip