import constant from "../actions/index";

export const newPriceForm = {
    identifierName : "",
    acquisitionPrice : "",
    salePrice : "",
    salePercentage:  "",
    currencyProfit:  "",
}

const initialState = {
    processingInformation : false,
    addedNewPrice : false,
    calcSellPriceForPercentage : false,
    newPriceForm : {...newPriceForm},
    errors : {}
}

export default function PricesReducer(state=initialState, action){
    const globalState = {...state}
    switch (action.type) {
        case constant.UPDATE_PRICE_STATE:
            globalState[action.state] = action.value
        break;
        case constant.RESET_FORM_NEW_PRICE:
            globalState.newPriceForm = {...newPriceForm}
        break;
        default:
            return globalState;
    }
    return globalState;
}