import React from 'react'
import { connect } from 'react-redux'
import { useParams } from 'react-router-dom'
import ActionButtons from '../../../components/ActionButtons'
import { DeleteSVG } from '../../../components/icons'
import Searchbar from '../../../components/Searchbar'
import Table from '../../../components/Table'
import TitleViews from '../../../components/TitleViews'
import { applyProductIdentifier, changeProductInformationWindowStatus, deleteProducts, getProductList, loadProductData, saveProducts, selectProductInformation, selectProductToDelete, showAlertToDeleteProduct, updatePage, updateProductSelecteds, updateTotalResults, updateTotalRows, updateTypePage } from '../../../redux/actions/ProductsAction'
import { goCreateProduct } from '../class'
import DeleteProduct from '../delete_product'
import { Box, IconButton } from '@material-ui/core'
import Tooltip from '../../../components/Tooltips'
import Market from '../../market/class'
import ViewProduct from '../view_product_data'
import { updateCompositeList } from '../../../redux/actions/CompountProductAction'
import { goCreateCompountProduct } from '../../compountproducts/class'
import { DOMAIN_SERVER } from '../../../connection/Api'


const mapPropsToState = (state) => {
    return{
        products : state.Products.list,
        isOpenAlert : state.Products.deleteProduct.isOpenAlert,
        productToDelete : state.Products.deleteProduct.product,
        page : state.Products.pagination.page,
        sizeTable : state.Products.pagination.sizeTable,
        totalResults : state.Products.pagination.totalResults,
        productSelecteds : state.Products.productSelecteds,
        isOpenModal : state.Products.viewDataProduct.isOpenModal,
        productSelected : state.Products.viewDataProduct.productSelected,
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        getProducts : (page, limit, isCompount) => dispatch(getProductList(page, limit, isCompount)),
        loadProductData : (product) => dispatch(loadProductData(product)),
        selectProductToDelete : (product) => dispatch(selectProductToDelete(product)),
        showAlertToDeleteProduct : (value) => dispatch(showAlertToDeleteProduct(value)),
        deleteProducts : (product, isCompount) => dispatch(deleteProducts(product, isCompount)),
        updatePage : (page) => dispatch(updatePage(page)),
        updateTotalRows : (totalRows) => dispatch(updateTotalRows(totalRows)),
        updateTotalResults : (totalResults) => dispatch(updateTotalResults(totalResults)),
        updateProductSelecteds : (products) => dispatch(updateProductSelecteds(products)),
        updateTypePage : (page) => dispatch(updateTypePage(page)),
        applyProductIdentifier : (id) => dispatch(applyProductIdentifier(id)),
        changeProductInformationWindowStatus : (value) => dispatch(changeProductInformationWindowStatus(value)),
        selectProductInformation : (product) => dispatch(selectProductInformation(product)),
        updateCompositeList : (compount) => dispatch(updateCompositeList(compount)),
        saveProducts : (product) => dispatch(saveProducts(product)),
    }
}

const ManageProducts = connect(mapPropsToState, mapDispatchToProps)(({products, isOpenAlert, productToDelete, page, sizeTable, totalResults, productSelecteds, isOpenModal, productSelected, isCompount, images, ...actions}) => {
    const { getProducts, loadProductData, selectProductToDelete, showAlertToDeleteProduct, deleteProducts, updatePage, updateProductSelecteds, updateTypePage, applyProductIdentifier, changeProductInformationWindowStatus, selectProductInformation, updateCompositeList, saveProducts } = actions
    const { businessName } = useParams()
    const actionsTable = productSelecteds.length <= 0 ?  {
        field: ' ',
        headerName: ' ',
        renderCell : (params) => {
            return <ActionButtons
            disabled={false}
            onViewData={() => {
                changeProductInformationWindowStatus(true)
                selectProductInformation(params.row)
            }}
            onEdit={() => {
                loadProductData(params.row)
                if(isCompount){
                    goCreateCompountProduct(businessName)
                    updateCompositeList(params.row.compount)
                }else{
                    goCreateProduct(businessName)
                }
                updateTypePage("update")
                applyProductIdentifier(params.row._id)
            }}
            onDeleted={() => {
                let _productToDelete = [...productToDelete]
                _productToDelete.push(params.row)
                selectProductToDelete(_productToDelete)
                showAlertToDeleteProduct(true)
            }}
        />
        },
        flex: 1,
    } : null
    
    const columns = [
        {
            field: 'category',
            headerName: 'Categoria',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params)
                return(
                    <>{params.value.name}</>
                )
            },
        },{
            field: 'name',
            headerName: 'Nombre',
            editable: false,
            flex : 1,
        },{
            field: 'skucode',
            headerName: 'Codigo SKU',
            editable: false,
            flex : 1,
        },{
            field: 'stock',
            headerName: 'Cantidad',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                const product = params.row
                const unitSize = product.unitSize.compoundUnitOfMeasure ? `${product.unitSize.abbreviation} (x${product.unitSize.compoundUnit}${product.unitSize.baseUnit.abbreviation})` : `${product.unitSize.abbreviation}`
                return(
                    <>{params.row.stock} {unitSize}</>
                )
            }
        },{...actionsTable}
    ]

    React.useEffect(() => {
        getProducts(page, sizeTable, isCompount)
    },[page, sizeTable])
    React.useEffect(() => {
        return() => {
            saveProducts([])
        }
    },[])
    return(
        <>
            <TitleViews
                title={isCompount ? "Lista de productos compuestos" : "Lista de productos"}
                tooltip={isCompount ? "Crear nuevo producto compuesto" : "Crear nuevo producto"}
                mb={2}
            />
            <Searchbar />
            {productSelecteds.length >= 1 &&
                <Tooltip title='Eliminar seleccion' >
                    <IconButton color='secondary' onClick={() => showAlertToDeleteProduct(true)} >
                        <DeleteSVG />
                    </IconButton>
                </Tooltip>
            }
            <Table
                rows={products}
                columns={columns}
                rowCount={totalResults}
                page={page}
                loading={true}
                checkboxSelection
                onSelection={(a) => updateProductSelecteds(a)}
                updatePage={(page) => updatePage(page)}
                selectionModel={productSelecteds}
            />
            <DeleteProduct
                showAlertToDeleteProduct={showAlertToDeleteProduct}
                isOpenAlert={isOpenAlert}
                productToDelete={productToDelete}
                deleteProducts={deleteProducts}
                selectProductToDelete={selectProductToDelete}
                productSelecteds={productSelecteds}
                isCompount={isCompount}
            />
            <ViewProduct
                isOpenModal={isOpenModal}
                productSelected={productSelected}
                changeProductInformationWindowStatus={changeProductInformationWindowStatus}
                selectProductInformation={selectProductInformation}
                isCompount={isCompount}
            />
        </>
    )
})

export default ManageProducts