import React from 'react'
import Button from '../../../components/Button'
import Alert from '../../../components/Alert'
import { Grid, Box } from '@material-ui/core'

const DeleteProduct = ({ isOpenAlert, productToDelete, productSelecteds, isCompount, ...actions }) => {
    const { showAlertToDeleteProduct, deleteProducts, selectProductToDelete } = actions
    const messageA = `DESEA ELIMINAR EL PRODUCTO "${productToDelete.length === 1 && productToDelete[0].name}"`
    const messageB = `DESEA ELIMINAR LOS ${productSelecteds.length} PRODUCTOS SELECCIONADOS`
    const messageResult = productSelecteds.length >= 1 ? messageB : messageA
    React.useEffect(() => {
        if( !isOpenAlert ){
            selectProductToDelete([])
        }
    },[isOpenAlert])
    const onDeleteProduct = (evt) => {
        if(Boolean(evt) && Boolean(evt.preventDefault)) evt.preventDefault()
        if( productSelecteds.length >= 1 ){
            deleteProducts(productSelecteds, isCompount)
        }else{
            deleteProducts(productToDelete, isCompount)
        }
    }
    return(
        <>
            <form action='#' method='post' onSubmit={onDeleteProduct} >
                <Alert severity='error' isOpen={isOpenAlert} textAlert={messageResult} close={() => showAlertToDeleteProduct(false)} >
                    <Box mt={2} >
                        <Grid container spacing={2} >
                            <Grid item xs={6} >
                                <Button size='large' variant='outlined' onClick={onDeleteProduct} >Eliminar</Button>
                            </Grid>
                            <Grid item xs={6} >
                                <Button type='button' size='large' variant='outlined' color='secondary' onClick={() => showAlertToDeleteProduct(false)} >Cerrar</Button>
                            </Grid>
                        </Grid>
                    </Box>
                </Alert>
            </form>
        </>
    )
}

export default DeleteProduct