import React from 'react'
import { Box, useTheme, Typography, makeStyles } from '@material-ui/core'
import dropfile from '../styles/images/dropfile.png'
import DeleteIcon from '@material-ui/icons/Delete';

const useStyle2 = makeStyles((theme) => ({
    deleteItemDropZone : {
        '& .removeItem' : {
            transition: theme.transitions.create(['all'], {
                duration: 100,
            }),
            borderRadius : theme.shape.borderRadius,
            width : "100%",
            height : "calc(100% - 8px)",
            justifyContent : "center",
            alignItems : "center",
            display : "none",
        },
        '&:hover .removeItem' : {
            backgroundColor : "#eb0d0d4f",
            cursor : "pointer",
            display : "flex",
        }
    }
}))

export const ImageGalerie = ({ image, onClick=false, createURL=true }) => {
    const theme = useTheme()
    const clasess = useStyle2()
    return(
        <Box position="relative" display="inline-flex" mb={2} mx={1} pt={1} className={`${clasess.deleteItemDropZone}`} height={90} width={90} justifyContent="center" >
            <Box position="absolute" className="removeItem" onClick={() => {
                if(typeof onClick === "function"){
                    onClick(image)
                }
            }} >
                <DeleteIcon style={{ color : "#FFF"}} />
            </Box>
            <Box display="flex" border={1} borderRadius={theme.shape.borderRadius} borderColor={theme.palette.grey[300]} overflow="hidden" >
                <img style={{ objectFit: "cover" }} src={ createURL ? URL.createObjectURL(image) : image} />
            </Box>
        </Box>
    )
}

const useStyles = makeStyles((theme) => ({
    dropzoneHoverStyle : {
        transition: theme.transitions.create(['all'], {
            duration: 100,
        }),
        cursor : "pointer",
        '&:hover' : {
            backgroundColor : "#FFFFFFA8"
        }
    },
    organizaElements : {
        [theme.breakpoints.down('xs')] : {
            flexDirection : "column",
        }
    },
}))

const Dropzone = ({ onChange=false, limitFiles=5, name="dragAndDropZone", reachedLimit=false, onAlert }) => {

    const theme = useTheme()
    const [images, setImages] = React.useState([])
    const [formats, setFormats] = React.useState(["jpg", "jpeg", "png"])
    const inputFile = React.useRef()
    const clasess = useStyles()

    const pushToCollection = (collectionFiles) => {
        let cloneCollectionImages = [...images]
        if( cloneCollectionImages.length < limitFiles && !reachedLimit ){
            for(let x of [...collectionFiles]){
                const cleanArrFiles = cloneCollectionImages.filter( f => f.name === x.name)
                if( !Boolean(cleanArrFiles) || cleanArrFiles.length <= 0){
                    const formatFile = x.name.split(".")[1]
                    const formatValidation = formats.includes(formatFile)
                    if( formatValidation ){
                        cloneCollectionImages.push(x)
                    }
                }
            }
            setImages(cloneCollectionImages)
        }
    }
    const handlerDropzone = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        const data = evt.dataTransfer.files
        pushToCollection(data)
    }
    const handlerDropOverzone = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
    }
    const hanlderChangeInputFile = (evt) => { 
        const data = evt.target.files
        pushToCollection(data)
    }
    React.useEffect(() => {
        if( typeof onAlert === "function" ){
            onAlert(images)
        }
        if(typeof onChange === "function"){
            onChange({attr : name, value : images})
        }
    },[images])
    const hanlderClick = (evt) => {
        if(evt.target.id === "dropzone" || evt.target.id === "dropzoneHoverMasck"){
            inputFile.current.click()
        }
    }
    React.useEffect(() => {
        window.addEventListener('click',hanlderClick)
        return () => {
            window.removeEventListener('click',hanlderClick)
        }
    },[])
    const removeItem = (index) => {
        const cloneImagesCollection = [...images]
        cloneImagesCollection.splice(index,1);
        setImages(cloneImagesCollection)
    }
    return(
        <>
            <input ref={inputFile} type="file" style={{ display : "none" }} multiple onChange={hanlderChangeInputFile} />
            <Box id="dropzone" display="flex" justifyContent="center" flexWrap="wrap" onDrop={handlerDropzone} onDragOver={handlerDropOverzone} border={1} borderColor="#C4C4C4" borderRadius={theme.shape.borderRadius} minHeight={150} overflow="hidden" >
                {images.length >= 1 ?
                    (
                        images.map((el,index) => {
                            return(
                                <React.Fragment key={index} >
                                    <ImageGalerie image={el} onClick={() => removeItem(index)} />
                                </React.Fragment>
                            )
                        })
                    ) :
                    <Box width="100%" position="relative" >
                        <Box id="dropzoneHoverMasck" position="absolute" width="100%" height="100%" className={clasess.dropzoneHoverStyle}  onDrop={handlerDropzone} onDragOver={handlerDropOverzone} />
                        <Box className={clasess.organizaElements} display="flex" justifyContent="center" alignItems="center" width="100%" height="100%" >
                            <Box mr={2}  >
                                <Box width={140} >
                                    <img src={dropfile} />
                                </Box>
                            </Box>
                            <Typography component="span" style={{ textAlign : "center" }} >
                                <Box fontWeight="bold" fontSize={20} color="grey.800" >
                                    Soltar o Seleccionar archivos
                                </Box>
                                <Box fontSize={14} color="grey.800" >
                                    Arrastre las imagenes o haga click para cargarlas.
                                </Box>
                                <Box fontSize={12} color={theme.palette.warning.main} >
                                    Este campo solo permite imagenes.
                                </Box>
                            </Typography>
                        </Box>
                    </Box>
                }
            </Box>
        </>
    )
}

export default Dropzone