import React from 'react'
import { Grid } from '@material-ui/core'
import Autocomplete from '../../../components/Autocomplete'
import Button from '../../../components/Button'
import LoadImage from '../../../components/LoadImage'
import TextField from '../../../components/TextField'
import Accordion from '../../../components/Accordion'
import WorkingHours from './WorkingHours'
import Text from '../../../components/Text'

const Form = ({ businessTypes, updateWorkingHours, fileInstance, days, week, match, errors, updateListOfDaysOfTheWeek, updateFormCreateBusiness, updateModalStatusCreateBusinessType, businessName, taxIdentificationNumber, address, phoneNumber, businessType, workingHours, description }) => {
    const handlerDataForm = (data) => {
        const {attr, value} = data
        updateFormCreateBusiness(attr, value)
    }
    const isError = (key) => Boolean(errors[key])

    return(
        <>
            <Grid container spacing={1} >
                <Grid item xs={12} md={3} lg={3} >
                    <LoadImage onChange={ (image) => {
                        if( Boolean(image.blob) ){
                            fileInstance(image.file) 
                        }else{
                            fileInstance(null) 
                        }
                    }} />
                </Grid>
                <Grid item xs={12} sm={12} md={9} lg={9} >
                    <Grid container spacing={1} >
                        <Grid item xs={12} sm={6} md={6} lg={6} >
                            <TextField
                                error={ isError('businessName') }
                                helperText={ isError('businessName') && "Defina el nombre de su negocio" }
                                value={businessName}
                                label="Razón social"
                                name="businessName"
                                onChange={handlerDataForm}
                                required
                            />
                        </Grid>
                        <Grid item xs={12} sm={6} md={6} lg={6} >
                            <TextField
                                type="number"
                                value={taxIdentificationNumber}
                                label="NIT"
                                name='taxIdentificationNumber'
                                onChange={handlerDataForm}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6} md={6} lg={6} >
                            <TextField
                                value={address}
                                name="address"
                                error={ isError('address') }
                                helperText={ isError('address') && "La dirección del negocio es requerida" }
                                required
                                label="Dirección"
                                onChange={handlerDataForm}
                                placeholder="calle 00 #00 - 00"
                            />
                        </Grid>
                        <Grid item xs={12} sm={6} md={6} lg={6} >
                            <TextField
                                error={ isError('phoneNumber') }
                                helperText={ isError('phoneNumber') && "el número de teléfono es requerido" }
                                name="phoneNumber"
                                type='number'
                                value={phoneNumber}
                                required
                                label="Teléfono"
                                onChange={handlerDataForm}
                            />
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} sm={12} md={12} lg={12} >
                    <Autocomplete
                        error={ isError('businessType') }
                        helperText={ isError('businessType') && "el tipo de negocio es requerido" }
                        required
                        name="businessType"
                        value={businessType}
                        label='Tipo de negocio'
                        options={businessTypes}
                        textAddButton="En caso de que tu tipo de negocio no este en esta lista puedes crearlo."
                        onChange={(e,v) => {
                            e.value = v
                            handlerDataForm(e)
                        }}
                        onAdd={() => {
                            updateModalStatusCreateBusinessType(true)
                        }}
                    />
                </Grid>
                {match &&
                    <Grid item xs={12} sm={12} md={12} lg={12} >
                        <Accordion title={<Text text='Horario de trabajo (opcional) :' uppercase />} >
                            <WorkingHours updateWorkingHours={updateWorkingHours} days={days} week={week} disabledTitle updateListOfDaysOfTheWeek={updateListOfDaysOfTheWeek} updateFormCreateBusiness={updateFormCreateBusiness} workingHours={workingHours} />
                        </Accordion>
                    </Grid>
                }
                <Grid item xs={12} sm={12} md={12} lg={12} >
                    <TextField
                        value={description}
                        name="description"
                        onChange={handlerDataForm}
                        label="Descripción"
                        multiline
                        rows={6}
                    />
                </Grid>
                <Grid item xs={12} sm={12} md={12} lg={12} >
                    <Button size="large" >
                        Registrar
                    </Button>
                </Grid>
            </Grid>
        </>
    )
}

export default Form