/* eslint no-undef: 0 */
import React from 'react';
import { connect } from 'react-redux'
import { CssBaseline, Grid, Card, CardContent, Box, makeStyles, useTheme, useMediaQuery } from '@material-ui/core';
import Register from './Register'
import login from '../../styles/images/login-lg.png'
import CreateProfile from './CreateProfile';
import Form from './LoginComponents/Form';
import HeaderForm from './LoginComponents/HeaderForm';
import Modals from './RegistersComponents/Modals';

const useStyles = makeStyles((theme) => ({
  paper: {
    justifyContent : "center",
	alignItems : "center",
    flexDirection: 'column',
    display: 'flex',
	height : "100%",
	[theme.breakpoints.up('md')]: {
		backgroundColor : "#03a9f40d",
	},
  },
  "@keyframes imageFloat" : {
    "50%": {
      transform: "translateY(0rem)"
    },
    "100%": {
      transform: "translateY(1.2rem)"
    }
  },
  imageAnimated: {
    animation: "$imageFloat 5s ease-in-out",
    animationIterationCount: "infinite",
    transform: "translateY(1.2rem)"
  }
}));

const Login = ({ dispatch }) => {
	const classes = useStyles();
    const theme = useTheme()
	const match = useMediaQuery(theme.breakpoints.up('md'))
	return (
		<>
			<CssBaseline />
			<Grid container  style={{ height : "100%" }} >
				{ match &&
					<Grid item style={{ height : "100%" }} xs={6} >
						<Box p={4} display="flex" height="100%" justifyContent="center" alignItems="center" >
							<img src={login} alt={login} style={{ width: '80%' }} className={classes.imageAnimated} />
						</Box>
					</Grid>
				}
				<Grid item style={{ height : "100%" }} {...(match ? {xs : 6} : {xs : 12} )} >
					<div className={classes.paper}>
						<Card variant="outlined" style={{ maxWidth: "450px", width : "100%" }} >
							<CardContent style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", position:'relative' }} >
								<HeaderForm />
								<Form />
							</CardContent>
						</Card>
					</div>
				</Grid>
			</Grid>
			<Modals />
		</>
	);
}

export default connect(null,null)(Login)