import React from 'react'
import { connect } from 'react-redux'
import Chart from '../../components/Chart'
import { getReportsOfTheWeeks } from '../../redux/actions/DashboardAction'
import { Grid, useTheme } from '@material-ui/core'
import TitleViews from '../../components/TitleViews'
import Card from '../../components/Card'
import Market from '../market/class'

const mapDispatchToProps = (dispatch) => {
    return{
        getReportsOfTheWeeks : (weekDate) => dispatch(getReportsOfTheWeeks(weekDate))
    }
}

const mapPropsToState = (state) => {
    return{
        weekDate : state.Dashboard.weekDate,
        listRerporForWeek : state.Dashboard.listRerporForWeek,
        weeklyEarningsList : state.Dashboard.weeklyEarningsList,
        weeklyProfitPercentageList : state.Dashboard.weeklyProfitPercentageList,
    }
}

const MainPage = connect(mapPropsToState, mapDispatchToProps)(({weekDate, listRerporForWeek, weeklyEarningsList, weeklyProfitPercentageList, ...actions}) => {
    const {getReportsOfTheWeeks} = actions
    const [datasets, setDatasets ] = React.useState([])
    const [percentageSet, setPercentageSet ] = React.useState([])
    const theme = useTheme()
    const market = new Market()
    React.useEffect(() => {
        getReportsOfTheWeeks(weekDate)
    },[weekDate])
    React.useEffect(() => {
        if( listRerporForWeek.length === 7 ){
            setDatasets([
                {
                label: 'Venta semana actual',
                data: weekDate.map((elem, index) => listRerporForWeek[index]),
                borderColor: theme.palette.secondary.main,
                backgroundColor: theme.palette.secondary.light,
                },{
                label: 'Ganancia semana actual',
                data: weekDate.map((elem, index) => weeklyEarningsList[index]),
                borderColor: theme.palette.primary.main,
                backgroundColor: theme.palette.primary.light,
                },
            ])
            setPercentageSet([
                {
                label: 'Porcentaje de ganancia semana actual ',
                data: weekDate.map((elem, index) => weeklyProfitPercentageList[index]),
                borderColor: theme.palette.secondary.main,
                backgroundColor: theme.palette.secondary.light,
                }
            ])
        }
    },[listRerporForWeek, weekDate, weeklyEarningsList, weeklyProfitPercentageList])
    return(
        <>
            <TitleViews
                title="Panel principal"
                // tooltip="Crear nuevo producto"
                mb={2}
                // onClick={() => goCreateCategory()}
                // icon={<CategoriesSVG />}
            />
            <Grid container spacing={2} >
                <Grid item xs={12} sm={12} md={12} lg={4} >
                    <Card title='Venta de hoy' text={`$ ${market.currencyFormat(listRerporForWeek[6])}`} />
                </Grid>
                <Grid item xs={12} sm={6} md={6} lg={4} >
                    <Card title='Ganancias de hoy' text={`$ ${market.currencyFormat(weeklyEarningsList[6])}`} />
                </Grid>
                <Grid item xs={12} sm={6} md={6} lg={4} >
                    <Card title='Porcentaje de ganancias' text={`${weeklyProfitPercentageList[6]} %`} themeColor={theme.palette.secondary.light} />
                </Grid>
                <Grid item xs={12} sm={12} md={6} lg={6} >
                    <Chart labels={weekDate} datasets={datasets} />
                </Grid>
                <Grid item xs={12} sm={12} md={6} lg={6} >
                    <Chart labels={weekDate} datasets={percentageSet} />
                </Grid>
            </Grid>
        </>
    )
})

export default MainPage