import { Box, Grid, IconButton, InputAdornment } from '@material-ui/core'
import React from 'react'
import Button from '../../../components/Button'
import Dialog from '../../../components/Dialog'
import { DeleteSVG, UnitSize } from '../../../components/icons'
import Text from '../../../components/Text'
import TextField from '../../../components/TextField'
import TextFieldMask from '../../../components/TextFieldMask'
import Tooltip from '../../../components/Tooltips'
import Switch from '../../../components/Switch'
import parseData, { deleteElement } from '../../../methods/parseData'
import CompountProduct from '../class'

const ConfigCompountProduct = ({ productSelected, isOpenModal, positionUnitSize, quantity, compount, isEditable, mainProduct, ...actions }) => {
    const { openCompositeProductConfigurationWindow, updatePositionOfThePuttingUnit, updateSelectedQuantity, updateCompositeList, resetCompositeProductStatus, assignMainProduct, callNotificationSnackbar, } = actions
    const compountClass = new CompountProduct()
    const addCompount = () => {
        let _compount = parseData(compount)
        const product = {product: {...productSelected}, quantity, positionUnitSize, main: mainProduct}
        const existProduct = _compount.find(p => p.product._id === productSelected._id)
        if( Boolean(existProduct) ){
            for( let i in _compount ){
                if( _compount[i].product._id === productSelected._id ){
                    _compount[i] = product
                    updateCompositeList(_compount)
                    openCompositeProductConfigurationWindow(false)
                }
            }
        }else{
            _compount.push(product)
            updateCompositeList(_compount)
            openCompositeProductConfigurationWindow(false)
        }
    }
    const removeItem = () => {
        const result = deleteElement(compount, (item) => item.product._id === productSelected._id )
        updateCompositeList(result)
        openCompositeProductConfigurationWindow(false)
    }
    const assignMain = (val) => {
        if( val ){
            for(let item of compount){
                if( item.main ){
                    callNotificationSnackbar(true, "Ya existe un producto principal en la lista", "warning")
                    return
                }
            }
            assignMainProduct(val)
        }else{
            assignMainProduct(false)
        }
    }
    React.useEffect(() => {
        if( !isOpenModal ){
            resetCompositeProductStatus()
        }
    },[isOpenModal])
    return(
        <>
            {Object.keys(productSelected).length > 1 &&
                <Dialog close={() => openCompositeProductConfigurationWindow(false)} open={isOpenModal} title={productSelected.name} >
                    <Grid container >
                        <Grid item xs={12} >
                            {isEditable &&
                                <>
                                    <Box display="flex" alignItems="center" mb={2} >
                                        <Box mr={5} >
                                            <Tooltip title='Eliminar de la lista' >
                                                <IconButton color="primary" onClick={removeItem} >
                                                    <DeleteSVG />
                                                </IconButton>
                                            </Tooltip>
                                        </Box>
                                        <Switch label='Producto principal' checked={mainProduct} onChange={assignMain} />
                                    </Box>
                                </>
                            }
                            <TextField
                                name="unitSizeValue"
                                label="Cantidad"
                                value={quantity}
                                onChange={(e) => {
                                    if( e.value.trim() === "" ) e.value = 1
                                    e.value = parseInt(e.value)
                                    updateSelectedQuantity(e.value)
                                }}
                                InputProps={{
                                    inputComponent : TextFieldMask,
                                    endAdornment: <InputAdornment position="end">
                                        <Tooltip title='Cambiar unidad de medida' >
                                            <IconButton size='small' onClick={() => {
                                                if( productSelected.unitSize.compoundUnitOfMeasure ){
                                                    const position = Boolean(positionUnitSize) ? 0 : 1
                                                    updatePositionOfThePuttingUnit(position)
                                                }
                                            }} >
                                                <UnitSize />
                                            </IconButton>
                                        </Tooltip>
                                    </InputAdornment>,
                                }}
                                inputProps={{
                                    style : { fontWeight : "600" },
                                    value : quantity,
                                    prefix : `(${compountClass.getUnitSize(productSelected.unitSize, positionUnitSize)}) - `,
                                }}
                            />
                            <Text text={`total: $ ${ compountClass.calculateCost( productSelected, quantity, positionUnitSize )}`} lineHeight={0} mb={0} uppercase />
                        </Grid>
                        <Grid item xs={12} >
                            <Box my={2} >
                                <Button size="large" variant='outlined' color='secondary' onClick={addCompount} > { isEditable ? "Guardar cambios" : "Añadir producto" }</Button>
                            </Box>
                        </Grid>
                    </Grid>
                </Dialog>
            }
        </>
    )
}

export default ConfigCompountProduct