import React from 'react'
import { makeStyles, Slide } from '@material-ui/core'
import { connect } from 'react-redux'
import bgImage from '../../styles/images/2.png'
import CompleteProfile from './RegistersComponents/CompleteProfile'
import Container from './Components/Container'
import HeaderForm from './RegistersComponents/HeaderForm'

const useStyles = makeStyles(() => ({
    "@keyframes imageFloat" : {
      "50%": {
        transform: "translateY(0rem)"
      },
      "100%": {
        transform: "translateY(1.2rem)"
      }
    },
    imageAnimated: {
      animation: "$imageFloat 5s ease-in-out",
      animationIterationCount: "infinite",
      transform: "translateY(1.2rem)"
    }
}))

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="left" ref={ref} {...props} />;
});

const CreateProfile = ({ dispatch, formCompleteProfile }) => {
    const clasess = useStyles()
    return(
      <>
        <Container
          left={true}
          open={formCompleteProfile}
          Transition={Transition}
          ComponentLeft={<> <img src={bgImage} alt=":D" style={{ width : "80%" }} className={clasess.imageAnimated} /> </>}
          ComponentRight={
            <>
              <HeaderForm title="completa tu perfil" />
              <CompleteProfile />
            </>
          }
        />
      </>
    )
}

export default connect(state => {
    return {
      formCompleteProfile : state.authState.register.formCompleteProfile,
    }
},null)(CreateProfile)