import React from 'react'
import { connect } from 'react-redux'
import { assignMainProduct, editSelectedCompositeProduct, openCompositeProductConfigurationWindow, resetCompositeProductStatus, searchProductToProductCompount, selectProductFormComposite, updateCompositeList, updatePositionOfThePuttingUnit, updateSelectedQuantity } from '../../../redux/actions/CompountProductAction'
import { callNotificationSnackbar } from '../../../redux/actions/DashboardAction'
import { updateProductPriceList } from '../../../redux/actions/ProductsAction'
import CreateProducts from '../../products/create_product'
import ConfigCompountProduct from './ConfigProdutCompount'
import TableCompountProduct from './TableCompountProduct'

const mapPropsToState = (state) => {
    return{
        inputValueProduct : state.CompountProduct.create_product_compount.inputValueProduct,
        productSelected : state.CompountProduct.create_product_compount.productSelected,
        isOpenModal : state.CompountProduct.create_product_compount.isOpenModal,
        positionUnitSize : state.CompountProduct.create_product_compount.positionUnitSize,
        quantity : state.CompountProduct.create_product_compount.quantity,
        compount : state.CompountProduct.create_product_compount.compount,
        isEditable : state.CompountProduct.create_product_compount.isEditable,
        mainProduct : state.CompountProduct.create_product_compount.mainProduct,
        priceList : state.Products.createProduct.form.priceList,
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        searchProductToProductCompount: (value) => dispatch(searchProductToProductCompount(value)),
        selectProductFormComposite: (product) => dispatch(selectProductFormComposite(product)),
        openCompositeProductConfigurationWindow: (value) => dispatch(openCompositeProductConfigurationWindow(value)),
        updatePositionOfThePuttingUnit: (position) => dispatch(updatePositionOfThePuttingUnit(position)),
        updateSelectedQuantity: (value) => dispatch(updateSelectedQuantity(value)),
        updateCompositeList: (value) => dispatch(updateCompositeList(value)),
        updateProductPriceList: (value) => dispatch(updateProductPriceList(value)),
        resetCompositeProductStatus: () => dispatch(resetCompositeProductStatus()),
        editSelectedCompositeProduct: (value) => dispatch(editSelectedCompositeProduct(value)),
        assignMainProduct: (value) => dispatch(assignMainProduct(value)),
        callNotificationSnackbar: (active, message, type) => dispatch(callNotificationSnackbar(active, message, type)),
    }
}

const CreateCompountProducts = connect(mapPropsToState, mapDispatchToProps)(({inputValueProduct, productSelected, isOpenModal, positionUnitSize, quantity, compount, priceList, isEditable, mainProduct, ...actions}) => {
    const { searchProductToProductCompount, selectProductFormComposite, openCompositeProductConfigurationWindow, updatePositionOfThePuttingUnit, updateSelectedQuantity, updateCompositeList, resetCompositeProductStatus, updateProductPriceList, editSelectedCompositeProduct, assignMainProduct, callNotificationSnackbar } = actions
    return(
        <>
            <CreateProducts
                compount
                sizeLeftBox={7}
                sizeRightBox={5}
                component={<TableCompountProduct
                    inputValueProduct={inputValueProduct}
                    searchProductToProductCompount={searchProductToProductCompount}
                    selectProductFormComposite={selectProductFormComposite}
                    openCompositeProductConfigurationWindow={openCompositeProductConfigurationWindow}
                    compount={compount}
                    updateProductPriceList={updateProductPriceList}
                    priceList={priceList}
                    editSelectedCompositeProduct={editSelectedCompositeProduct}
                    updateSelectedQuantity={updateSelectedQuantity}
                    updatePositionOfThePuttingUnit={updatePositionOfThePuttingUnit}
                    assignMainProduct={assignMainProduct}
                />}
            />
            <ConfigCompountProduct
                productSelected={productSelected}
                isOpenModal={isOpenModal}
                openCompositeProductConfigurationWindow={openCompositeProductConfigurationWindow}
                updatePositionOfThePuttingUnit={updatePositionOfThePuttingUnit}
                positionUnitSize={positionUnitSize}
                quantity={quantity}
                updateSelectedQuantity={updateSelectedQuantity}
                updateCompositeList={updateCompositeList}
                compount={compount}
                resetCompositeProductStatus={resetCompositeProductStatus}
                isEditable={isEditable}
                assignMainProduct={assignMainProduct}
                mainProduct={mainProduct}
                callNotificationSnackbar={callNotificationSnackbar}
            />
        </>
    )
})

export default CreateCompountProducts