import React from 'react'
import { Checkbox as Check, FormControlLabel, Grid, Typography, Switch as Swit } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';

export const Checkbox = ({checked=false, onChange=false, color="primary", label="",labelPlacement="end", name=""}) => {
    const [checkStatus, setCheckStatus] = React.useState(checked)
    return(
        <FormControlLabel
            labelPlacement={labelPlacement}
            label={label}
            control={
                <Check
                    name={name}
                    checked={checkStatus}
                    onChange={(elem)=> {
                        setCheckStatus(!checkStatus)
                        if( typeof onChange === "function" ){
                            onChange({attr : elem.target.name, value : !checkStatus})
                        }
                    }}
                    color={color}
                />
            }
        />
    )
}

const AntSwitch = withStyles((theme) => ({
    root: {
      width: 28,
      height: 16,
      padding: 0,
      display: 'flex',
    },
    switchBase: {
      padding: 2,
      color: theme.palette.grey[500],
      '&$checked': {
        transform: 'translateX(12px)',
        color: theme.palette.common.white,
        '& + $track': {
          opacity: 1,
          backgroundColor: theme.palette.primary.main,
          borderColor: theme.palette.primary.main,
        },
      },
    },
    thumb: {
      width: 12,
      height: 12,
      boxShadow: 'none',
    },
    track: {
      border: `1px solid ${theme.palette.grey[500]}`,
      borderRadius: 16 / 2,
      opacity: 1,
      backgroundColor: theme.palette.common.white,
    },
    checked: {},
  }))(Swit);

export const Switch = ({ textLeft="", textRight="", onChange=false, value=false }) => {
    const [checkedSwitch,setCheckedSwitch] = React.useState(false)
    const handlerSwitch = () => {
        setCheckedSwitch(!checkedSwitch)
        if(typeof onChange === "function" ){
          onChange(!checkedSwitch)
        }
    }
    React.useEffect(() => {
      setCheckedSwitch(value)
    },[value])
    return(
        <Typography component="div">
            <Grid component="label" container alignItems="center" spacing={1}>
                <Grid item>{textLeft}</Grid>
                <Grid item>
                    <AntSwitch checked={checkedSwitch} onChange={ _ => handlerSwitch() } />
                </Grid>
                <Grid item>{textRight}</Grid>
            </Grid>
        </Typography>
    )
} 