import { Box, Grid, Paper } from '@material-ui/core'
import React from 'react'
import Dialog from '../../components/Dialog'
import { Cash, CreditCard, Wallet } from '../../components/icons'
import Text from '../../components/Text'

const Card = ({ icon, text, active=false, onClick }) => {
    return(
        <>
            <Paper elevation={2} >
                <Box onClick={() => typeof onClick === "function" ? onClick() : null} p={1} display="flex" flexDirection="column" justifyContent="center" alignItems="center" style={{ cursor: "pointer" }} {...(active ? {bgcolor:"secondary.light"} : {bgcolor:"primary.light"} )} >
                    {icon}
                    <Box>
                        <Text fontSize={20} uppercase text={text} lineHeight={0} mb={0} mt={3} />
                    </Box>
                </Box>
            </Paper>
        </>
    )
}

const PaymentMethod = ({ selectPaymentMethod, methodPosition, ...actions }) => {
    const { openPaymentMethodWindow, updatePaymentMethodPosition, callNotificationSnackbar } = actions
    const selectedPayMethod = (position) => {
        updatePaymentMethodPosition(position)
        setTimeout(() => openPaymentMethodWindow(false), 300)
        callNotificationSnackbar(true,'Nuevo metodo de pago seleccionado','success')
    }
    return(
        <>
            <Dialog open={selectPaymentMethod} close={() => openPaymentMethodWindow(false)} title='Seleccione el metodo de pago'>
                <Box p={3} >
                    <Grid container spacing={2}>
                        <Grid item xs={4}>
                            <Card icon={<Cash width='80%' />} text="efectivo" {...( !Boolean(methodPosition) && {active : true} )} onClick={() => selectedPayMethod(0) } />
                        </Grid>
                        <Grid item xs={4}>
                            <Card icon={<CreditCard width='80%' />} text="Tarjeta" {...( methodPosition === 1 && {active : true} )} onClick={() => selectedPayMethod(1) } />
                        </Grid>
                        <Grid item xs={4}>
                            <Card icon={<Wallet width='80%' />} text="Credito" {...( methodPosition === 2 && {active : true} )} onClick={() => selectedPayMethod(2) } />
                        </Grid>
                    </Grid>
                </Box>
            </Dialog>
        </>
    )
}

export default PaymentMethod