import React from 'react';
import { connect } from 'react-redux'
import { Box, makeStyles } from '@material-ui/core';
import Fade from '@material-ui/core/Fade';
import bgImage from '../../styles/images/1.png'
import Form from './RegistersComponents/Form';
import HeaderForm from './RegistersComponents/HeaderForm';
import Container from './Components/Container'
import { updateAuthState } from '../../redux/actions/AuthAction';
import User from './Methods/User';
import Button from '../../components/Button'

const useStyles = makeStyles(theme => ({
  paper: {
    justifyContent : "center",
    alignItems : "center",
    flexDirection: 'column',
    display: 'flex',
    height : "100%",
  },
  "@keyframes imageFloat" : {
    "50%": {
      transform: "translateY(0rem)"
    },
    "100%": {
      transform: "translateY(1.2rem)"
    }
  },
  imageAnimated: {
    animation: "$imageFloat 5s ease-in-out",
    animationIterationCount: "infinite",
    transform: "translateY(1.2rem)"
  }
})) 

const Register = ({dispatch, formBasicInformation, register, formAuth, duplicateEmail, duplicateUsername }) => {

  const clasess = useStyles()
  const user = new User()

  const completeForm = (evt) =>{
    evt.preventDefault()
    let result = user.ValidateUserAuthData(formAuth)
    let _register = {...register}
    _register.errors = result
    if( duplicateEmail || duplicateUsername ) {
      if( duplicateEmail ) {
        _register.errors.email = "El correo electronico ya se encuentra registrado"
      }
      if( duplicateUsername ){
         _register.errors.username = "El nombre de usuario ya se encuentra registrado"
      }
    }
    dispatch(updateAuthState('register', _register))
    if( Object.keys(result).length <= 0 && !duplicateEmail && !duplicateUsername ){
      dispatch(updateAuthState('register',{...register, formBasicInformation : false, formCompleteProfile : true}))
    }
  }

  return (
    <>
      <Container
        right={true}
        open={formBasicInformation}
        Transition={Fade}
        ComponentLeft={<> <img src={bgImage} alt=":D" style={{ width : "80%" }} className={clasess.imageAnimated} /> </> }
        ComponentRight={
          <>
            <HeaderForm title="Datos de inicio de sesion" />
            <form action="#" method="POST" onSubmit={completeForm} style={{ width : "100%" }} >
              <Form />
            </form>
            <Box my={1} width="100%" >
                <Button type="button" variant="outlined" size="large" color="secondary"  onClick={ _ => dispatch(updateAuthState('register',{...register, formBasicInformation : false})) } >
                    Ya Tengo cuenta
                </Button>
            </Box>
          </>
        }
      />
    </>
  );
}
export default connect(state => {
  return{
    formBasicInformation : state.authState.register.formBasicInformation,
    register : state.authState.register,
    formAuth : state.authState.register.form,
    duplicateEmail : state.authState.register.duplicateEmail,
    duplicateUsername : state.authState.register.duplicateUsername,
  }
}, null)(Register)