import constant from ".";
import parseData from "../../methods/parseData";
import UnitSize from "../../views/unitSize/class";
import store from "../store";
import { callNotificationSnackbar } from "./DashboardAction";

export const activeModalToCreateUnitOfMeasure = (status) => {
    return { type : constant.ACTIVATE_MODAL_TO_CREATE_UNIT_OF_MEASURE, status }
}
export const updateFormdataCreateMeasurementUnit = (attr, value) => {
    return { type : constant.UPDATE_FORM_DATA_CREATE_MEASUREMENT_UNIT, attr, value }
}
export const updateListOfUnitsOfMeasure = (unitSizes) => {
    return { type : constant.UPDATE_LIST_OF_UNITS_OF_MEASURE, unitSizes }
}
export const updateDataOfTheRequestOfUnitsOfMeasurement = (data) => {
    return { type : constant.UPDATE_DATA_OF_THE_REQUEST_OF_UNITS_OF_MEASUREMENT, data }
}
export const updatePageOfTheRequestForUnitsOfMeasurement = (position) => {
    return { type : constant.UPDATE_PAGE_OF_THE_REQUEST_FOR_UNITS_OF_MEASUREMENT, position }
}
export const submitUnitSize = (payload, isEdit=false) => async (dispatch) => {
    try{
        let unitSize = new UnitSize()
        const list = store.getState().UnitSize.unitSizeList
        const page = store.getState().UnitSize.page
        const limit = store.getState().UnitSize.limit
        const data = parseData(payload)
        data.baseUnit = data.baseUnit._id
        const resp = isEdit ? await unitSize.update(data) : await unitSize.create(data)
        const message = isEdit ? "Unidad de medida actualizada con exito" : "Nueva unidad de medida creada con exito"
        const error = isEdit ? "Ocurrio un error al intentar actualizar la unidad de medida" : "Ocurrio un error al intentar crear la unidad de medida"

        
        if( resp.status ){
            dispatch(callNotificationSnackbar(true, message, "success"))
            dispatch(activeModalToCreateUnitOfMeasure(false))
            dispatch(restoreFormUnitsOfMeasurements())
            if( list ){
                dispatch(getUnitSize(limit, page))
            }
        }else{
            dispatch(callNotificationSnackbar(true, error, "error"))
        }
    }catch(err){
        console.log(err)
    }
}
export const getUnitSize = (limit, page, unitSize_id) => async (dispatch) => {
    try{
        let unitSize = new UnitSize()
        const list = await unitSize.get(limit, page, unitSize_id)
        if( list.status ){
            dispatch(updateListOfUnitsOfMeasure(list.payload))
            dispatch(updateDataOfTheRequestOfUnitsOfMeasurement(list.data))
        }else{
            console.error(list)
        }
    }catch(err){
        console.log(err)
    }
}
export const restoreFormUnitsOfMeasurements = () => {
    return {type: constant.RESTORE_FORM_UNITS_OF_MEASUREMENTS }
}
export const iAmInTheListOfMeasurementUnits = (value) => {
    return {type: constant.I_AM_IN_THE_LIST_OF_MEASUREMENT_UNITS, value }
}
export const restoreStateOfTheListUnitsOfMeasurement = () => {
    return {type: constant.RESTORE_STATE_OF_THE_LIST_UNITS_OF_MEASUREMENTS }
}
export const editModeUnitOfMeasure = (value) => {
    return {type: constant.EDIT_MODE_UNIT_OF_MEASURE, value }
}
export const updateUniqueIdentifierOfTheSelectedUnit = (value) => {
    return {type: constant.UPDATE_UNIQUE_IDENTIFIER_OF_THE_SELECTED_UNIT, value }
}
export const openWindowDeleteUnitOfMeasurement = (value) => {
    return {type: constant.OPEN_WINDOW_DELETE_UNIT_OF_MEASUREMENT, value }
}
export const selectUnitOfmeasurementToDelete = (unitSize) => {
    return {type: constant.SELECT_UNIT_OF_MEASUREMENT_TO_DELETE, unitSize }
}
export const selectMultipleUnitsOfMeasurement = (unitSizes) => {
    return {type: constant.SELECT_MULTIPLE_UNITS_OF_MEASUREMENT, unitSizes }
}
export const deleteUnitSize = (unitSizeSelected, unitSizeSelectedList) => async (dispatch) => {
    try{
        const identifiers = unitSizeSelectedList.length >= 1 ? unitSizeSelectedList : [unitSizeSelected._id]
        const page = store.getState().UnitSize.page
        const limit = store.getState().UnitSize.limit
        const unitsize = new UnitSize()
        const result = await unitsize.delete(identifiers)
        const message = identifiers.length <= 1 ? "La unidad de medida fue eliminada con exito" : "Las unidades de medida fueron eliminadas con exito"
        if( result.status ){
            dispatch(callNotificationSnackbar(true, message, "success"))
            dispatch(getUnitSize(limit, page))
            dispatch(openWindowDeleteUnitOfMeasurement(false))
        }else{
            dispatch(callNotificationSnackbar(true, "Error al intentar eliminar las unidades de medida", "error"))
        }
    }catch(err){
        console.log(err)
    }
}