import store from '../redux/store'
export const DOMAIN_SERVER = "https://kikedev-backapp.herokuapp.com"
// export const DOMAIN_SERVER = "http://localhost:3005"
const PREFIX_API = "/api"
const API_DEVELOPMENT = `${DOMAIN_SERVER}${PREFIX_API}`

const API_REQUEST = API_DEVELOPMENT


const app = {
	post : async (route, data, parseData=true) => {
		const myData = parseData ? JSON.stringify(data) : data

		const header = parseData ? {
			'Content-Type': 'application/json',
			'Authorization': `token ${localStorage.getItem('user_token')}`
		} : {
			'Accept': 'application/json',
			'Authorization': `token ${localStorage.getItem('user_token')}`,
		}
		const res = await fetch(`${API_REQUEST}${route}`,{
			method: 'POST',
			mode: 'cors', 
			cache: 'no-cache',
			credentials: 'same-origin',
			headers: header,
			body: myData
		})
		.then( data =>  data.json() )
		.then( data => {
			if(data.code === 109){
				store.dispatch({ type : 'DETECTED_TOKEN_ERROR', status : true })
				return
			}
			return data
		})
		.catch(err => console.log(err))
		return res
	},
	postFormData : async (route,data) => {
		const res = await fetch(`${API_REQUEST}${route}`,{
			method: 'POST',
			mode: 'cors', 
			cache: 'no-cache',
			credentials: 'same-origin',
			headers: {
				'Accept': 'application/json',
				'Authorization': `token ${localStorage.getItem('user_token')}`
			},
			body: data
		})
		.catch(err => console.log(err))

		return res.json()
	},
	get : async (route) => {
		const res = await fetch(`${API_REQUEST}${route}`,{
			method: 'GET',
			mode: 'cors', 
			cache: 'no-cache',
			credentials: 'same-origin',
			headers: {
				'Accept': 'application/json',
				'Authorization': `token ${localStorage.getItem('user_token')}`
			}
		})
		.then( data =>  data.json() )
		.then( data => {
			if(data.code === 109){
				store.dispatch({ type : 'DETECTED_TOKEN_ERROR', status : true })
				return
			}
			return data
		})
		.catch(err => console.log(err))
		return res
	}
}
export default app
