import React from 'react'
import { useHistory } from 'react-router-dom'
import { connect } from 'react-redux'
import { Box, Fade, Typography, Button } from '@material-ui/core'
import { RoleSVG } from '../../components/icons'
import { getRolesAndPermissionsForState } from '../../redux/actions/RolesAndPermissionsAction'
import { ProgressSectionApp } from '../../components/ProgressApp'
import RolesList from './RolesList'


const ManageRolesAndPermissions = ({ dispatch, roles, permissions, awaitData }) => {

	const history = useHistory()

	const getRolesAndPermissions = React.useCallback(() => {
		if( roles.length <= 0 || permissions.length <= 0 ){
			dispatch( getRolesAndPermissionsForState() )
		}
// eslint-disable-next-line react-hooks/exhaustive-deps
	},[roles])

	React.useEffect(() => {
		getRolesAndPermissions()
	},[getRolesAndPermissions])

	return(
		<Fade in={true} >
			<React.Fragment>
				{ awaitData && <ProgressSectionApp /> }
				
				{ roles.length <= 0 ?
					<Box style={{ width : '100%', height: '80vh' }} display="flex" justifyContent="center" alignItems="center" flexDirection="column" >
						<RoleSVG width='30%' />
						<Typography variant="h6" > NO TIENES ROLES DISPONIBLES </Typography>
						<Button
							variant="outlined"
							color="primary"
							size="small"
							onClick={ _ => history.push('/admin/create/role') }
						> Crear un nuevo rol </Button>
					</Box> : <RolesList />
				}

			</React.Fragment>
		</Fade>
	)
}

export default connect(state => {
	return {
		roles : state.rolesAndPermissionsState.roles,
		permissions : state.rolesAndPermissionsState.permissions,
		awaitData : state.rolesAndPermissionsState.awaitData,
	}
},null)(ManageRolesAndPermissions)