import React from 'react'
import { InputAdornment } from '@material-ui/core'
import HelpSharpIcon from '@material-ui/icons/HelpSharp';
import Tooltip, { CustomizeTooltip } from './Tooltips'


const HelpDescription = ({ position="start", title="", placement="top", tooltipCustomize=false }) => {
    return(
        <>
            <InputAdornment position={position} >
                { !tooltipCustomize ? 
                    <Tooltip title={title} placement={placement}>
                        <HelpSharpIcon />
                    </Tooltip> :
                    <CustomizeTooltip title={title} placement={placement}>
                        <HelpSharpIcon />
                    </CustomizeTooltip>
                }
            </InputAdornment>
        </>
    )
}

export default HelpDescription