import React from 'react'
import { Box, Dialog, DialogContent, DialogTitle, Slide, Typography } from '@material-ui/core'
import { connect } from 'react-redux';
import CreateBusiness from '.';
import CreateBusinessTypeModal from '../create_business_type/CreateBusinessTypeModal';
import { updateModalStatusCreateBusinessType } from '../../../redux/actions/BusinessAction';


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const mapPropsToState = state => {
    return {
        data : state.authState.data,
        activeModalForm : state.Business.createBusinessType.activeModalForm
    }
}

const mapDispatchToState = dispatch => {
    return {
        updateModalStatusCreateBusinessType : (status) => dispatch(updateModalStatusCreateBusinessType(status)),
    }
}

export const CreateBusinessModal = connect(mapPropsToState,mapDispatchToState)(({ data, greeting=false, activeModalForm, updateModalStatusCreateBusinessType, isOpen=false }) => {
    return(
        <>
            <Dialog fullScreen open={isOpen} onClose={() => null} TransitionComponent={Transition} transitionDuration={500} >
                <DialogTitle>
                    {greeting &&
                        <Typography component="span" color="secondary" >
                            <Box fontWeight="bold" >
                                Bienvenido <Typography component="span" color="primary" style={{ fontWeight :"bold" }} > {data.name} </Typography> ya casi acabamos, porfavor registra un negocio para comenzar a utilizar el sistema
                            </Box>
                        </Typography>
                    }
                </DialogTitle>
                <DialogContent style={{ padding :"8px 8px" }} >
                    <CreateBusiness />
                </DialogContent>
            </Dialog>
            <CreateBusinessTypeModal open={activeModalForm} updateModalStatusCreateBusinessType={updateModalStatusCreateBusinessType} />
        </>
    )
})

export default CreateBusiness