import constant from './index'
import app from '../../connection/Api'

export const awaitData = _ => {
	return { type : constant.AWAIT_DATA }
}
export const dataObtained = _ => {
	return { type : constant.DATA_OBTAINED }
}
export const loadDataInInformationState = (state,value) => {
	return { type : constant.LOAD_DATA_IN_INFORMATION_STATE, state,value }
}
export const loadCategoriesInState = (categories) => {
	return { type : constant.LOAD_CATEGORIES, categories }
}
export const loadProductsInState = (products) => {
	return { type : constant.LOAD_PRODUCTS, products }
}
export const getCategories = (businessID) => (dispatch) => {
	try{
		app.get(`/get/categories/${businessID}`)
		.then( data => {
			if(data.categories.length >= 1){
				dispatch( loadCategoriesInState(data.categories) )
			}else{
				dispatch( loadCategoriesInState([]) )
			}
		})
	}catch(err){
		console.log(err)
	}
}
export const getProducts = (businessID) => (dispatch) => {
	try{
		app.get(`/get/all/products/${businessID}`)
		.then( data => {
			if(data.products.length >= 1){
				dispatch( loadProductsInState(data.products) )
			}else{
				dispatch( loadProductsInState([]) )
			}
		})
	}catch(err){
		console.log(err)
	}
}
export const getPermissionAll = () => (dispatch) => {
	try{
		app.get('/get/all/permissions')
		.then( data => {
			if(data.status === 1){
				if(data.permissions.length >= 1){
					dispatch( loadDataInInformationState('permissions',data.permissions) )
				}
			}
		})
	}catch(err){
		console.log(err)
	}
}
export const loadDataInit = (businessID) => (dispatch) => {
	try{
		dispatch( getCategories(businessID) )
		dispatch( getProducts(businessID) )
		dispatch( getPermissionAll() )
	}catch(err){
		console.log(err)
	}
}
export const getPrices = () => (dispatch) => {
	try{
		app.get(`/get/all/prices`)
		.then(p => {
			if( p.status ){
				dispatch(loadDataInInformationState('prices',p.data))
			}
		})
	}catch(err) {
		console.log(`Error in getBusiness function ${err}`)
	}
}
export const getBusiness = () => (dispatch) => {
	try{
		app.get(`/find/user/business`)
		.then(b => {
			if(b.business.length >= 1){
				dispatch( loadDataInInformationState('business', b.business ) )
			}else{
				dispatch( loadDataInInformationState('business', false ) )
			}
		})
	}catch(err) {
		console.log(`Error in getBusiness function ${err}`)
	}
}

export const chargedNewBusinessData = (valA,valB) => {
	return { type : constant.CHARGET_NEW_DATA_BUSINESS, valA,valB }
}
export const changeBusinessDefault = (data) => async (dispatch) => {
	try{
		console.log(data)
	}catch(err){
		console.log(err)
	}
}
