import React from 'react'
import { connect } from 'react-redux';
import UseColorPalette from '../../../components/UseColorPalette';
import { activateModalCreateVariant, activateModalUseColorPalette, addColorPaletteToVariant, addNewVariantToTheList, removeColorPaletteToVariant, removeVariantToTheList, searchListedVariantsCategory, submitVariants, updateNameOfNewVariantCategory, updateVariantsData } from '../../../redux/actions/VariantsAction';
import Form from './Form';

const mapPropsToState = state => {
    return {
        variantIndex : state.Variants.use_colors.variant_index,
        variantCategoryName : state.Variants.create_variant.variantCategory.name,
        variantCategorySelected : state.Variants.create_variant.variantCategory.selected,
        variants : state.Variants.create_variant.variants,
        createCategoryVariant : state.Variants.createCategoryVariant,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        addNewVariantToTheList : () => dispatch(addNewVariantToTheList()),
        removeVariantToTheList : (position) => dispatch(removeVariantToTheList(position)),
        activateModalUseColorPalette : (status, index) => dispatch(activateModalUseColorPalette(status, index)),
        addColorPaletteToVariant : (color, index) => dispatch(addColorPaletteToVariant(color, index)),
        removeColorPaletteToVariant : (index) => dispatch(removeColorPaletteToVariant(index)),
        searchListedVariantsCategory : (status) => dispatch(searchListedVariantsCategory(status)),
        updateVariantsData : (payload, index) => dispatch(updateVariantsData(payload, index)),
        updateNameOfNewVariantCategory : (name) => dispatch(updateNameOfNewVariantCategory(name)),
        activateModalCreateVariant : (status) => dispatch(activateModalCreateVariant(status)),
        submitVariants : (payload) => dispatch(submitVariants(payload)),
    }
}

const CreateVariant = connect(mapPropsToState, mapDispatchToProps)(({variantCategoryName, variantCategorySelected, variants, variantIndex, createCategoryVariant, ...actions}) => {
    const {
            addNewVariantToTheList,
            removeVariantToTheList,
            activateModalUseColorPalette,
            addColorPaletteToVariant,
            removeColorPaletteToVariant,
            searchListedVariantsCategory,
            updateVariantsData,
            updateNameOfNewVariantCategory,
            activateModalCreateVariant,
            submitVariants
        } = actions

    
    const submitForm = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        const payload = {
            variantCategory : {
                name : variantCategoryName,
                create : createCategoryVariant
            },
            variants,
        }
        submitVariants(payload)
    }

    return(
        <>
            <form method='POST' onSubmit={submitForm} >
                <Form
                    variantCategoryName={variantCategoryName}
                    variantCategorySelected={variantCategorySelected}
                    variants={variants}
                    createCategoryVariant={createCategoryVariant}
                    addNewVariantToTheList={addNewVariantToTheList}
                    removeVariantToTheList={removeVariantToTheList}
                    activateModalUseColorPalette={activateModalUseColorPalette}
                    removeColorPaletteToVariant={removeColorPaletteToVariant}
                    searchListedVariantsCategory={searchListedVariantsCategory}
                    updateNameOfNewVariantCategory={updateNameOfNewVariantCategory}
                    updateVariantsData={updateVariantsData}
                    activateModalCreateVariant={activateModalCreateVariant}
                />
            </form>
            <UseColorPalette useColor={(hexColor) => addColorPaletteToVariant(hexColor, variantIndex)} />
        </>
    )
})

export default CreateVariant