import React from 'react';
import { connect } from 'react-redux';
import Dialog from '../../../components/Dialog';
import { callFormEditCategorie } from '../../../redux/actions/CategoriesAction';
import EditCategory from '.';

const mapPropsToState = (state) => {
    return {
        activeEditCategorieModal : state.Categories.editCategory.activeEditCategorieModal
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        callFormEditCategorie : (status) => dispatch(callFormEditCategorie(status))
    }
}

const EditCategorieModal = connect(mapPropsToState, mapDispatchToProps)(({ activeEditCategorieModal, ...actions }) => {
	const { callFormEditCategorie } = actions
    return(
		<>
			<Dialog open={activeEditCategorieModal} close={ _ => callFormEditCategorie(false) } title='Editar Categoria' >
				<EditCategory  />
			</Dialog>
		</>
	)
})

export default EditCategorieModal