import React from 'react';
import { Radio, RadioGroup, FormControlLabel, FormControl, FormLabel} from '@material-ui/core';

const RadioButton = ({label="", name="", collection=[], onChange=false, row=true, required=false, value="" }) => {

  const [valueCollection, setValueCollection] = React.useState(value)

  const handleChange = (e) => {
    setValueCollection(e.target.value)
    if(typeof onChange === "function"){
        onChange({
          attr : e.target.name,
          value : e.target.value,
        })
    }
  }

  return (
  <FormControl component="fieldset" >
  <FormLabel required={required} component="legend">{label}</FormLabel>
    <RadioGroup row={row} name={name} value={valueCollection} onChange={ e => handleChange(e) } >
      {collection.length >= 1 &&
        collection.map((elem,index) => {
          return(
            <React.Fragment key={index} >
              <FormControlLabel value={elem.value} control={<Radio color={elem.color??"primary"} />} label={elem.label} />
            </React.Fragment>
          )
        })
      }
    </RadioGroup>
  </FormControl>
  );
}
export default RadioButton