import React from 'react';
import { connect } from 'react-redux';
import Table from '../../../components/Table';
import parseData from '../../../methods/parseData';
import ActionButtons from '../../../components/ActionButtons'
import { callFormEditCategorie, getCategories, getCategoriesWhithMatch, updateAlertModalStatus, updateCategoriesPage, updateListOfCategories, updateSelectedCategory } from '../../../redux/actions/CategoriesAction';
import { Chip } from '@material-ui/core';
import { CategoriesSVG } from '../../../components/icons'
import Searchbar from '../../../components/Searchbar';
import EditCategorieModal from '../edit_categories/EditCategoryModal';
import Alert from '../../../components/Alert';
import { updateSystemMessage } from '../../../redux/actions/DashboardAction';
import TwoOptions from '../../../components/TwoOptions';
import { goCreateCategory } from '../class';
import TitleViews from '../../../components/TitleViews';

const mapPropsToState = (state) => {
    return {
        categories : state.Categories.categories,
        fetchingComplete : state.Categories.fetchingComplete,
        page : state.Categories.page,
        categoriesInList : state.Categories.categoriesInList,
        categoriesTotal : state.Categories.categoriesData.categories_total,
        numPage : state.Categories.categoriesData.num_page,
        isOpen : state.Categories.deleteCategory.alert.isOpen,
        categorySelected : state.Categories.categorySelected,
        locale : state.Dashboard.alertSistem.locale,
        alert : state.Dashboard.alertSistem.alert,
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        getCategories : (page) => dispatch(getCategories(page)),
        getCategoriesWhithMatch : (match) => dispatch(getCategoriesWhithMatch(match)),
        updateSelectedCategory : (category) => dispatch(updateSelectedCategory(category)),
        callFormEditCategorie : (status) => dispatch(callFormEditCategorie(status)),
        updateCategoriesPage : (page) => dispatch(updateCategoriesPage(page)),
        updateListOfCategories : (categories_list) => dispatch(updateListOfCategories(categories_list)),
        updateAlertModalStatus : (status) => dispatch(updateAlertModalStatus(status)),
        updateSystemMessage : (locale, alert) => dispatch(updateSystemMessage(locale, alert)),
    }
}

const ListCategories = connect(mapPropsToState, mapDispatchToProps)(({ categories, categoriesTotal, page, fetchingComplete, categoriesInList, numPage, isOpen, locale, alert, categorySelected, ...actions }) => {
    const { callFormEditCategorie, updateCategoriesPage, getCategories, updateListOfCategories, getCategoriesWhithMatch, updateSelectedCategory, updateAlertModalStatus, updateSystemMessage } = actions
    const columns = [
        {
            field: 'name',
            headerName: 'Categoria',
            editable: false,
            flex : 1,
        },{
            field: 'status',
            headerName: 'Estado',
            renderCell : (params) => {
                const { row } = params
                return <Chip
                        style={{ backgroundColor: row.active ? "#00ff0324" : "#ff000024", color : row.active ? "#01d703" : "#d70101"}}
                        label={row.active ? "Activo" : "Inactivo"}
                        size='small'
                    />
            },
            editable: false,
            flex : 1,
        },{
            field: '',
            headerName: '',
            renderCell : (params) => {
                return <ActionButtons
                onEdit={ () =>{
                    updateSelectedCategory(params.row)
                    callFormEditCategorie(true)
                }}
                onDeleted={() => {
                    updateSystemMessage('DELETE_CATEGORY','DELETE_CATEGORY_ALERT')
                    updateAlertModalStatus(true)
                    updateSelectedCategory(params.row)
                }}
                view={false}
                disabled={false}
            />
            },
            flex : 1,
        },
    ]

    React.useEffect(() => {
        if( categories.length >= 1 ){
            const categorie = parseData(categories)
            updateListOfCategories(categorie)
        }
    },[categories])

    React.useEffect(() => {
        getCategories(page)
    },[page])

    const searchMatchs = (value) => {
        if( Boolean(value) ){
            getCategoriesWhithMatch(value)
        }else{
            getCategories(page)
        }
    }

    React.useEffect(() => {
        if( !isOpen ){
            updateSelectedCategory({})
            updateSystemMessage('', '')
        }
    },[isOpen])
    return(
        <>
            <TitleViews
                title="Lista de categorias"
                tooltip="Crear nueva categoria"
                onClick={() => goCreateCategory()}
                icon={<CategoriesSVG />}
            />
            <Searchbar
                onChange={searchMatchs}
            />
            <Table
                rows={categoriesInList}
                columns={columns}
                rowCount={categoriesTotal}
                updatePage={updateCategoriesPage}
                page={numPage}
                loading={fetchingComplete}
            />
            <EditCategorieModal />
            <Alert
                isOpen={isOpen}
                close={() => updateAlertModalStatus(false)}
                locale={locale}
                alert={ Boolean(categorySelected.assignedProducts) && categorySelected.assignedProducts.length >= 1 ? alert : ''}
                prop={categorySelected.name}
                component={
                    <TwoOptions
                        buttonOne='Eliminar'
                        buttonTwo='Conservar'
                        onTwo={() => updateAlertModalStatus(false)}
                    />
                }
            />
        </>
    )
})

export default ListCategories