import { SwipeableDrawer } from '@material-ui/core'
import React from 'react'
import { useLocation, useHistory } from 'react-router-dom'

const Container = ({children, active,...action}) => {
    const [activeList, setActiveList] = React.useState(true)
    const { marketWindw } = action

    const { pathname } = useLocation()
    const history = useHistory()
    React.useEffect(() => {
		const breakDownRoute = pathname.split('/')
		if( breakDownRoute[breakDownRoute.length - 1] === "market" ){
            marketWindw(true)
		}
	},[pathname])

    React.useEffect(() => {
		const breakDownRoute = pathname.split('/')
        breakDownRoute.pop()
        const path = breakDownRoute.join("/")
        if(!active){
            history.push(path)
            setActiveList(false)
        }else{
            setActiveList(true)
        }
	},[active])
    return(
        <>
            <SwipeableDrawer PaperProps={{ style : {height : "100%", padding : "20px"} }} anchor="bottom" open={active} onClose={ () => marketWindw(false) } onOpen={ () => marketWindw(true) }>
                {children}
            </SwipeableDrawer>
        </>
    )
}

export default Container