import constant from '../actions/index'

const selectedProductConfiguration = {
    isManualPrice : false,
    positionPrice : 0,
    manualPriceValue : 0,
    unitOfMeasurePosition : 0,
    listPosition : 0,
    quantity: 1,
    total : 0,
    discount : 0,
}

const paymentMethods = {
    selectPaymentMethod: false,
    methods : ['cash', 'creditCard', 'credit'],
    methodPosition : 0,
}

const initialState = {
    market : false,
    successfulSearch : false,
    noResultsFound : false,
    isFocusTable: false,
    productID : "",
    customerID : "",
    productsResults : [],
    shoppingList: [],
    customer: {},
    totalSale : 0,
    totalProfit : 0,
    totalPercentageProfit : 0,
    openDeleteModal : false,
    selectedProductConfiguration : {...selectedProductConfiguration},
    paymentMethods : {...paymentMethods},
}

export default function MarketReducer(state=initialState, action){
    const globalState = {...state}
    switch (action.type) {
        case constant.ACTIVE_VIEW_MARKET:
            globalState.market = action.status
        break;
        case constant.UPDATE_PRODUCT_IDENTIFIER_VALUE:
            globalState.productID = action.value
        break;
        case constant.ACTIVATE_RESULTS_LIST:
            globalState.successfulSearch = action.value
        break;
        case constant.UPDATE_RESULTS:
            globalState.productsResults = action.value
        break;
        case constant.ACTIVATE_EMPTY_RESULTS_ALERT:
            globalState.noResultsFound = action.value
        break;
        case constant.UPDATE_SELECTED_PRODUCT:
            globalState.selectedProductConfiguration.listPosition = action.value
        break;
        case constant.LOWER_IN_POSITION_OF_THE_LIST:
            globalState.selectedProductConfiguration.listPosition = action.value
        break;
        case constant.GO_UP_IN_POSITION_OF_THE_LIST:
            globalState.selectedProductConfiguration.listPosition = action.value
        break;
        case constant.ACTIVATE_MANUAL_PRICE:
            globalState.selectedProductConfiguration.isManualPrice = action.value
        break;
        case constant.RESTORE_INITIAL_SETTINGS:
            globalState.isFocusTable = false
            globalState.selectedProductConfiguration = {...selectedProductConfiguration}
        break;
        case constant.CHANGE_DEFAULT_PRICE:
            globalState.selectedProductConfiguration.positionPrice = action.position
        break;
        case constant.CHANGE_DEFAULT_UNIT_OF_MEASURE:
            globalState.selectedProductConfiguration.unitOfMeasurePosition = action.position
        break;
        case constant.ADD_PRODUCT_IN_SHOPPING_LIST:
            globalState.shoppingList = [...globalState.shoppingList, action.product]
        break;
        case constant.UPDATE_PRODUCT_IN_SHOPPING_LIST:
            globalState.shoppingList = action.newShoppingList
        break;
        case constant.UPDATE_PRODUCT_QUANTITY:
            globalState.selectedProductConfiguration.quantity = action.quantity
        break;
        case constant.UPDATE_PRODUCT_DISCOUNT:
            globalState.selectedProductConfiguration.discount = action.discount
        break;
        case constant.UPDATE_MANUAL_PRICE:
            globalState.selectedProductConfiguration.manualPriceValue = action.price
        break;
        case constant.OPEN_PAYMENT_METHOD_WINDOW:
            globalState.paymentMethods.selectPaymentMethod = action.value
        break;
        case constant.UPDATE_PAYMENT_METHOD_POSITION:
            globalState.paymentMethods.methodPosition = action.value
        break;
        case constant.LOAD_PRODUCT_CONFIGURATION:
            globalState.selectedProductConfiguration = action.config
        break;
        case constant.UPDATE_SALE_VALUE:
            globalState.totalSale = action.totalSale
            globalState.totalProfit = action.totalProfit
            globalState.totalPercentageProfit = action.totalPercentageProfit
        break;
        case constant.FOCUS_SHOPPING_LIST_TABLE:
            globalState.isFocusTable = action.value
        break;
        case constant.ACTIVATE_WINDOW_TO_REMOVE_PRODUCT_FROM_THE_SHOPPING_LIST:
            globalState.openDeleteModal = action.value
        break;
        case constant.RESTORE_INITIAL_POSITIONS:
            globalState.selectedProductConfiguration.isManualPrice = false
            globalState.selectedProductConfiguration.positionPrice = 0
            globalState.selectedProductConfiguration.manualPriceValue = 0
            globalState.selectedProductConfiguration.unitOfMeasurePosition = 0
            globalState.selectedProductConfiguration.quantity = 1
            globalState.selectedProductConfiguration.discount = 0
        break;
        default:
            return globalState;
    }

    return globalState
}