import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import Table from '../../../components/Table'
import TitleViews from '../../../components/TitleViews'
import { formatDate } from '../../../methods/Moment'
import { getReports } from '../../../redux/actions/ReportsAction'
import Market from '../../market/class'

const mapPropsToState = (state) => {
    return{
        list : state.Reports.list,
        initDate : state.Reports.dateFilter.initDate,
        finDate : state.Reports.dateFilter.finDate,
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        getReports : (initDate, finDate) => dispatch(getReports(initDate, finDate))
    }
}

const Reports = connect(mapPropsToState, mapDispatchToProps)(({ initDate, finDate, list, ...actions }) => {
    const { getReports } = actions
    const columns = [
        {
            field: 'codeFac',
            headerName: 'Codigo',
            editable: false,
            renderCell : (params) => {
                const {value} = params
                return(
                    <>{`#${value}`}</>
                )
            },
        },{
            field: 'createdAt',
            headerName: 'Fecha',
            editable: false,
            renderCell : (params) => {
                const {value} = params
                const date = formatDate(value,'DD/MM/YYYY')
                return(
                    <>{date}</>
                )
            },
        },{
            field: 'user',
            headerName: 'Vendedor',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                const {name, lastname} = params.row.user
                return(
                    <> {name} {lastname} </>
                )
            },
        },{
            field: 'total',
            headerName: 'Total',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                const {value} = params
                const market = new Market()
                return(
                    <> $ {market.currencyFormat(value)} </>
                )
            },
        },{
            field: 'profit',
            headerName: 'Ganancia',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                const {value} = params
                const {profitPercentage} = params.row
                const market = new Market()
                return(
                    <> $ {market.currencyFormat(value)} - ({profitPercentage}%) </>
                )
            },
        },{
            field: ' ',
            headerName: ' ',
            editable: false,
            flex : 1,
        },
    ]
    React.useEffect(() => {
        getReports(initDate, finDate)
    },[initDate, finDate])
    return(
        <>
            <TitleViews
                title="Reportes de ventas"
                // tooltip="Crear nuevo producto"
                mb={2}
                // onClick={() => goCreateCategory()}
                // icon={<CategoriesSVG />}
            />
            <Table
                rows={list}
                columns={columns}
                rowCount={10}
                updatePage={10}
                page={1}
                loading={true}
            />
        </>
    )
})

export default Reports