import { Box, Grid, Link } from '@material-ui/core'
import React from 'react'
import { connect } from 'react-redux'
import TextField from '../../../components/TextField'
import Autocomplete from '../../../components/Autocomplete'
import Button from '../../../components/Button'
import User from '../Methods/User'
import { getCities, getCountries, getRegions, saveUser, updateAuthState } from '../../../redux/actions/AuthAction'

const CompleteProfile = ({ dispatch, countries, countrie, regions, cities, region, citie, register, name, lastname, phoneNumber, form, errors }) => {

    const handlerForm = (data) => {
        const _register = JSON.parse(JSON.stringify(register))
        _register.form[data.attr] = data.value
        if(data.attr === "countrie"){
            _register.form["citie"] = {}
            _register.form["region"] = {}
        }
        if(data.attr === "region"){
            _register.form["citie"] = {}
        }
        dispatch(updateAuthState('register',_register))
    }

    React.useEffect(() => {
        dispatch(getCountries())
    },[])

    const getRegionsUser = React.useCallback(() => {
        if( Boolean(countrie.id) ){
            dispatch(getRegions(countrie.id))
        }
    },[countrie])
    const getCitiesUser = React.useCallback(() => {
        if( Boolean(region.id) ){
            dispatch(getCities(region.id))
        }
    },[region])
    React.useEffect(() => {
        getRegionsUser()
    },[getRegionsUser])
    React.useEffect(() => {
        getCitiesUser()
    },[getCitiesUser])
    React.useEffect(() => {
        if( countries.length >= 1 ){
            let c = countries.filter(v => v.id === 82)
            if( c.length === 1 ){
                const _register = JSON.parse(JSON.stringify(register))
                _register.form.countrie = c[0]
                dispatch(updateAuthState('register',_register))
            }
        }
    },[countries])
    const HandlerSubmit = (evt) => {
        evt.preventDefault()
        const user = new User()
        const _form = JSON.parse(JSON.stringify(form))
        let _register = {...register}
        let error = user.ValidateUserData(_form)
        _register.errors = error
        dispatch(updateAuthState('register', _register))
        if( Object.keys(error).length <= 0 ){
            _form.countrie = _form.countrie.id
            _form.region = _form.region.id
            _form.citie = _form.citie.id
            user.asyncUserFormData(_form, _form.profilePhoto)
            .then(res => {
                dispatch(saveUser(res))
            })
        }
    }

    return(
        <>
        <Box width="100%" >
            <form action="#" onSubmit={HandlerSubmit} >
                <Grid container spacing={1} >
                    <Grid item xs={6} >
                        <TextField
                            error={errors.name && Boolean(errors.name)}
                            helperText={Boolean(errors.name) && errors.name}
                            label="Nombre"
                            value={name}
                            onChange={handlerForm}
                            name="name"
                            required
                        />
                    </Grid>
                    <Grid item xs={6} >
                        <TextField
                            error={errors.lastname && Boolean(errors.lastname)}
                            helperText={Boolean(errors.lastname) && errors.lastname}
                            label="Apellido"
                            value={lastname}
                            onChange={handlerForm}
                            required
                            name="lastname"
                        />
                    </Grid>
                </Grid>
                <TextField
                    required
                    error={errors.phoneNumber && Boolean(errors.phoneNumber)}
                    helperText={Boolean(errors.phoneNumber) && errors.phoneNumber}
                    label="Número de teléfono"
                    value={phoneNumber}
                    onChange={handlerForm}
                    name="phoneNumber"
                />
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Autocomplete
                            error={errors.countrie && Boolean(errors.countrie)}
                            helperText={Boolean(errors.countrie) && errors.countrie}
                            label="País"
                            name="countrie"
                            options={countries}
                            onChange={(data,evt) => {
                                data.value = evt
                                handlerForm(data)
                            }}
                            value={countrie}
                            required
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <Autocomplete
                            error={errors.region && Boolean(errors.region)}
                            helperText={Boolean(errors.region) && errors.region}
                            label="Departamento"
                            name="region"
                            options={regions}
                            onChange={(data,evt) => {
                                data.value = evt
                                handlerForm(data)
                            }}
                            value={region}
                            required
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <Autocomplete
                            error={errors.citie && Boolean(errors.citie)}
                            helperText={Boolean(errors.citie) && errors.citie}
                            label="Ciudad"
                            name="citie"
                            onChange={(data,evt) => {
                                data.value = evt
                                handlerForm(data)
                            }}
                            options={cities}
                            value={citie}
                            required
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <Link component="button" type="button" color="secondary" onClick={() => {
                            dispatch(updateAuthState('register',{...register, addPhoto : true}))
                        }} > Subir una foto de perfil </Link>
                    </Grid>
                </Grid>
                <Box my={2} >
                    <Button size="large" >Registrarme</Button>
                </Box>
            </form>
            <Box id="fileContainer" ></Box>
            <Box my={2} >
                <Button size="large" variant="outlined" color="secondary" 
                    onClick={() => {
                        dispatch(updateAuthState('register',{...register, formBasicInformation : true, formCompleteProfile : false}))
                    }}
                >Volver</Button>
            </Box>
        </Box>
        </>
    )
}

export default connect(state => {
    return {
        countries: state.dataState.countries,
        regions: state.dataState.regions,
        cities: state.dataState.cities,
        form: state.authState.register.form,
        name: state.authState.register.form.name,
        lastname: state.authState.register.form.lastname,
        phoneNumber: state.authState.register.form.phoneNumber,
        countrie: state.authState.register.form.countrie,
        region: state.authState.register.form.region,
        citie: state.authState.register.form.citie,
        register: state.authState.register,
        errors: state.authState.register.errors,
    }
},null)(CompleteProfile)