import React from 'react'
import { connect } from 'react-redux'
import { activateEmptyResultsAlert, activateManualPrice, activateResultList, activateWindowToRemoveProductFromTheShoppingList, activeMarket, changeDefaultPrice, changeDefaultUnitOfMeasure, focusShoppingListTable, goUpInPositionOfTheList, goUpInPositionOfThePaymentMethod, loadProductConfiguration, lowerInPositionOfTheList, lowerInPositionOfThePaymentMethod, openPaymentMethodWindow, restoreInitialPositions, restoreInitialSettings, saveProductInShoppingCard, saveSale, updateManualPrice, updatePaymentMethodPosition, updateProductDiscount, updateProductIdentifierValue, updateProductQuantity, updateProductsToShoppingList, updateResults, updateSaleValue, updateSelectedProduct } from '../../redux/actions/MarketAction'
import Container from './Container'
import DividerBox from './DividerBox'
import Header from './Header'
import Invoice from './Invoice'
import SaleTable from './SaleTable'
import TotalSale from './TotalSale'
import Results from './Results'
import MarketClass from './class'
import { Box } from '@material-ui/core'
import { callNotificationSnackbar } from '../../redux/actions/DashboardAction'
import PaymentMethod from './PaymentMethod'
import Text from '../../components/Text'
import DeleteMarket from './Delete'

const mapDispatchToProps = (dispatch) => {
    return{
        marketWindw : (status) => dispatch(activeMarket(status)),
        updateProductIdentifierValue : (value) => dispatch(updateProductIdentifierValue(value)),
        activateResultList : (value) => dispatch(activateResultList(value)),
        updateResults : (value) => dispatch(updateResults(value)),
        activateEmptyResultsAlert : (value) => dispatch(activateEmptyResultsAlert(value)),
        lowerInPositionOfTheList : () => dispatch(lowerInPositionOfTheList()),
        goUpInPositionOfTheList : () => dispatch(goUpInPositionOfTheList()),
        updateSelectedProduct : (value) => dispatch(updateSelectedProduct(value)),
        activateManualPrice : () => dispatch(activateManualPrice()),
        restoreInitialSettings : () => dispatch(restoreInitialSettings()),
        changeDefaultPrice : () => dispatch(changeDefaultPrice()),
        changeDefaultUnitOfMeasure : () => dispatch(changeDefaultUnitOfMeasure()),
        saveProductInShoppingCard : () => dispatch(saveProductInShoppingCard()),
        updateManualPrice : (price) => dispatch(updateManualPrice(price)),
        updateProductQuantity : (quantity) => dispatch(updateProductQuantity(quantity)),
        updateProductDiscount : (discount) => dispatch(updateProductDiscount(discount)),
        loadProductConfiguration : (config) => dispatch(loadProductConfiguration(config)),
        restoreInitialPositions : () => dispatch(restoreInitialPositions()),
        updateSaleValue : (totalSale, totalProfit, totalPercentageProfit) => dispatch(updateSaleValue(totalSale, totalProfit, totalPercentageProfit)),
        saveSale : () => dispatch(saveSale()),
        callNotificationSnackbar : (active, message, severity) => dispatch(callNotificationSnackbar(active, message, severity)),
        openPaymentMethodWindow : (value) => dispatch(openPaymentMethodWindow(value)),
        updatePaymentMethodPosition : (value) => dispatch(updatePaymentMethodPosition(value)),
        lowerInPositionOfThePaymentMethod : (value) => dispatch(lowerInPositionOfThePaymentMethod(value)),
        goUpInPositionOfThePaymentMethod : (value) => dispatch(goUpInPositionOfThePaymentMethod(value)),
        focusShoppingListTable : (value) => dispatch(focusShoppingListTable(value)),
        activateWindowToRemoveProductFromTheShoppingList : (value) => dispatch(activateWindowToRemoveProductFromTheShoppingList(value)),
        updateProductsToShoppingList : (value) => dispatch(updateProductsToShoppingList(value)),
    }
}

const mapPropsToState = (state) => {
    return{
        active: state.Market.market,
        productID: state.Market.productID,
        customerID: state.Market.customerID,
        productsResults: state.Market.productsResults,
        successfulSearch: state.Market.successfulSearch,
        noResultsFound: state.Market.noResultsFound,
        shoppingList: state.Market.shoppingList,
        totalSale: state.Market.totalSale,
        totalProfit: state.Market.totalProfit,
        totalPercentageProfit: state.Market.totalPercentageProfit,
        quantity: state.Market.selectedProductConfiguration.quantity,
        discount: state.Market.selectedProductConfiguration.discount,
        listPosition: state.Market.selectedProductConfiguration.listPosition,
        positionPrice: state.Market.selectedProductConfiguration.positionPrice,
        isManualPrice: state.Market.selectedProductConfiguration.isManualPrice,
        manualPriceValue: state.Market.selectedProductConfiguration.manualPriceValue,
        unitOfMeasurePosition: state.Market.selectedProductConfiguration.unitOfMeasurePosition,
        selectPaymentMethod: state.Market.paymentMethods.selectPaymentMethod,
        methodPosition: state.Market.paymentMethods.methodPosition,
        methods: state.Market.paymentMethods.methods,
        isFocusTable: state.Market.isFocusTable,
        openDeleteModal: state.Market.openDeleteModal,
    }
}

const Market = connect(mapPropsToState,mapDispatchToProps)(({active, totalSale, totalProfit, totalPercentageProfit, productID, customerID, productsResults, successfulSearch, noResultsFound, listPosition, positionPrice, isManualPrice, manualPriceValue, unitOfMeasurePosition, shoppingList, quantity, discount, selectPaymentMethod, methodPosition, methods, isFocusTable, openDeleteModal, ...actions}) => {
    const { marketWindw, updateProductIdentifierValue, activateResultList, updateResults, activateEmptyResultsAlert, lowerInPositionOfTheList, goUpInPositionOfTheList, activateManualPrice, restoreInitialSettings, changeDefaultPrice, changeDefaultUnitOfMeasure, saveProductInShoppingCard, updateManualPrice, updateProductQuantity, updateProductDiscount, loadProductConfiguration, restoreInitialPositions, updateSaleValue, saveSale, callNotificationSnackbar, updateSelectedProduct, openPaymentMethodWindow, updatePaymentMethodPosition, lowerInPositionOfThePaymentMethod, goUpInPositionOfThePaymentMethod, focusShoppingListTable, activateWindowToRemoveProductFromTheShoppingList, updateProductsToShoppingList } = actions
    React.useEffect(() => {
        const market = new MarketClass()
        let totalAmmoun = 0
        for( let product of shoppingList ){
            const { quantity, isManualPrice, positionPrice, manualPriceValue, discount } = product.config
            const { prices } = product.product
            const total = market.calculateTotalSale(quantity, isManualPrice, prices, positionPrice, manualPriceValue, discount, false)
            totalAmmoun += total
        }
        const profit = market.calculateProfitSale(shoppingList, false)
        const profitPercentage = market.calculatePercentageProfitSale(shoppingList, false)

        updateSaleValue(totalAmmoun, profit, profitPercentage)
    },[shoppingList])
    return(
        <>
            <div>
                <Container marketWindw={marketWindw} active={active} >
                    <Header marketWindw={marketWindw} />
                    <Text uppercase text={`Metodo de pago: ${methods[methodPosition]}`} lineHeight={0} mb={1} mt={2}  />
                    <Box display="flex" >
                        <TotalSale prefixLeft="$" amount={totalSale} isCurrency />
                        <Box ml={1} />
                        <TotalSale textLeft='GANANCIA : ' prefixRigth='%' amount={Boolean(totalPercentageProfit) ? totalPercentageProfit : 0 } />
                    </Box>
                    
                    <DividerBox
                        leftComponent={<SaleTable
                                productID={productID}
                                isFocusTable={isFocusTable}
                                focusShoppingListTable={focusShoppingListTable}
                                callNotificationSnackbar={callNotificationSnackbar}
                                lowerInPositionOfThePaymentMethod={lowerInPositionOfThePaymentMethod}
                                goUpInPositionOfThePaymentMethod={goUpInPositionOfThePaymentMethod}
                                selectPaymentMethod={selectPaymentMethod}
                                openPaymentMethodWindow={openPaymentMethodWindow}
                                shoppingList={shoppingList}
                                customerID={customerID}
                                updateProductIdentifierValue={updateProductIdentifierValue}
                                activateResultList={activateResultList}
                                updateResults={updateResults}
                                activateEmptyResultsAlert={activateEmptyResultsAlert}
                                lowerInPositionOfTheList={lowerInPositionOfTheList}
                                goUpInPositionOfTheList={goUpInPositionOfTheList}
                                activateManualPrice={activateManualPrice}
                                changeDefaultPrice={changeDefaultPrice}
                                changeDefaultUnitOfMeasure={changeDefaultUnitOfMeasure}
                                successfulSearch={successfulSearch}
                                saveProductInShoppingCard={saveProductInShoppingCard}
                                saveSale={saveSale}
                                loadProductConfiguration={loadProductConfiguration}
                                activateWindowToRemoveProductFromTheShoppingList={activateWindowToRemoveProductFromTheShoppingList}
                            />}
                        // rightComponent={<Invoice />}
                    />
                    
                </Container>
                <Results
                    productsResults={productsResults}
                    successfulSearch={successfulSearch}
                    noResultsFound={noResultsFound}
                    activateResultList={activateResultList}
                    activateEmptyResultsAlert={activateEmptyResultsAlert}
                    updateResults={updateResults}
                    listPosition={listPosition}
                    positionPrice={positionPrice}
                    isManualPrice={isManualPrice}
                    manualPriceValue={manualPriceValue}
                    restoreInitialSettings={restoreInitialSettings}
                    unitOfMeasurePosition={unitOfMeasurePosition}
                    shoppingList={shoppingList}
                    quantity={quantity}
                    discount={discount}
                    updateManualPrice={updateManualPrice}
                    updateProductQuantity={updateProductQuantity}
                    updateProductDiscount={updateProductDiscount}
                    loadProductConfiguration={loadProductConfiguration}
                    restoreInitialPositions={restoreInitialPositions}
                    callNotificationSnackbar={callNotificationSnackbar}
                    activateManualPrice={activateManualPrice}
                    changeDefaultPrice={changeDefaultPrice}
                    changeDefaultUnitOfMeasure={changeDefaultUnitOfMeasure}
                    updateSelectedProduct={updateSelectedProduct}
                    isFocusTable={isFocusTable}
                    activateWindowToRemoveProductFromTheShoppingList={activateWindowToRemoveProductFromTheShoppingList}
                />
                <PaymentMethod
                    selectPaymentMethod={selectPaymentMethod}
                    openPaymentMethodWindow={openPaymentMethodWindow}
                    methodPosition={methodPosition}
                    updatePaymentMethodPosition={updatePaymentMethodPosition}
                    callNotificationSnackbar={callNotificationSnackbar}
                />
                {productsResults.length >= 1 &&
                    <DeleteMarket
                        openDeleteModal={openDeleteModal}
                        isFocusTable={isFocusTable}
                        productsResults={productsResults}
                        listPosition={listPosition}
                        shoppingList={shoppingList}
                        activateWindowToRemoveProductFromTheShoppingList={activateWindowToRemoveProductFromTheShoppingList}
                        updateProductsToShoppingList={updateProductsToShoppingList}
                        activateResultList={activateResultList}
                    />
                }
            </div>
        </>
    )
})

export default Market