import React from 'react'
import { Box, InputAdornment, TextField as Input } from '@material-ui/core'

const TextField = React.forwardRef((props, ref) => {
    const {type="text",error,helperText,name="",label="",variant="outlined", InputProps=false, inputProps=false,
    onChange=false, endAdornment, endAdornmentOnClick, endAdornmentStyles=false, disabled, multiline=false, rows=false, value="",
    required=false, placeholder=false, onBlur=false, onFocus=false, size="medium", InputLabelProps=false, mb=2, id } = props
    return(
        <Box mb={mb} width="100%" >
            <Input
                id={id}
                ref={ref}
                size={size}
                required={required}
                fullWidth
                type={type}
                value={value}
                // {...(Boolean(value) ? {value} : {value : ""} )}
                {...(placeholder ? {placeholder} : {} )}
                {...(InputProps ? {InputProps} : {} )}
                {...(InputLabelProps ? {InputLabelProps} : {} )}
                {...(inputProps ? {inputProps} : {} )}
                {...(error ? {error} : {} )}
                {...(helperText ? { helperText } : {} )}
                {...(name ? { name } : {} )}
                {...(label ? { label } : {} )}
                {...(disabled ? {disabled} : {} )}
                {...(multiline ? {multiline} : {} )}
                {...(rows ? {rows} : {} )}
                variant={variant}
                onFocus={ e => {
                    const data = { attr : e.target.name, value : e.target.value }
                    if(typeof onFocus === 'function'){
                        onFocus(data,e)
                        return
                    }else{
                        return
                    }
                }}
                onBlur={ e => {
                    const data = { attr : e.target.name, value : e.target.value }
                    if(typeof onBlur === 'function'){
                        onBlur(data,e)
                        return
                    }else{
                        return
                    }
                }}
                onChange={ e => {
                    const data = { attr : e.target.name, value : e.target.value }
                    if(typeof onChange === 'function'){
                        if(type === "number") data.value = parseInt(data.value)
                        onChange(data,e)
                        return
                    }else{
                        return
                    }
                }}
                {...(endAdornment ? { InputProps : { endAdornment :
                    <InputAdornment
                        {...(endAdornmentStyles ? {style : endAdornmentStyles} : {})}
                        onClick={ _ => {
                            if(typeof endAdornmentOnClick === 'function'){
                                endAdornmentOnClick()
                                return
                            }else{
                                return
                            }
                        }}
                    >
                        {endAdornment}
                    </InputAdornment> } } : {} )}
            />
        </Box>
    )
})

export default TextField
