import React from 'react'
import { Box, Grid } from '@material-ui/core'
import { ChromePicker } from 'react-color'
import Dialog from './Dialog'
import Button from '../components/Button'
import { activateModalUseColorPalette } from '../redux/actions/VariantsAction'
import { connect } from 'react-redux'

const mapPropsToState = (state) => {
    return {
        activeModalColorPalette : state.Variants.use_colors.activeModalColorPalette
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        activateModalUseColorPalette : (status) => dispatch(activateModalUseColorPalette(status))
    }
}

const UseColorPalette = connect(mapPropsToState, mapDispatchToProps)(({ onChange, name="colorHex", useColor, activeModalColorPalette, ...actions }) => {
    const [color, setColor] = React.useState("#FFFFFF")
    const { activateModalUseColorPalette } = actions

    React.useEffect(() => {
        if( !activeModalColorPalette ){
            setColor("#FFFFFF")
        }
        return () => {
            setColor("#FFFFFF")
        }
    },[activeModalColorPalette])
    return(
        <Dialog open={activeModalColorPalette} maxWidth='xs' title='Paleta de colores' close={() => activateModalUseColorPalette(false)} >
            <Box bgcolor={color} display="flex" justifyContent="center" my={2} p={1} borderRadius={10} >
                <ChromePicker
                    disableAlpha
                    color={color}
                    onChange={ e => {
                        setColor(e.hex)
                        if( typeof onChange === "function" ){
                            onChange({ attr : name, value : e.hex })
                        }
                    }}
                />
            </Box>
            <Grid container spacing={1} >
                <Grid item xs={6} >
                    <Button variant='outlined' onClick={() => {
                        if( typeof useColor === "function" ){
                            useColor(color)
                        }
                    }} >Usar color</Button>
                </Grid>
                <Grid item xs={6} >
                    <Button variant='outlined' color="secondary" onClick={() => activateModalUseColorPalette(false)} >Cerrar</Button>
                </Grid>
            </Grid>
        </Dialog>
    )
})
export default UseColorPalette