import { Box } from '@material-ui/core';
import React from 'react'
import Stepper from '../../../components/Stepper'

    function getSteps() {
        return ['¿Como cargar mis productos desde un archivo excel?', '¿Como funcionan las columnas del archivo?', '¿Como añadir mas de un precio al producto en el excel?'];
    }

    function getStepContent(step) {
        switch (step) {
            case 0:
                return `Si cuentas con un inventario con muchos productos la manera mas rapida y facil de
                cargarlos sera mediantes un archivo de excel, en el archivo podras registrar nuevos productos y
                actualizarlos de manera muy sencilla.
                Solo deberas descargar la plantilla con el formato de excel soportado por el sistema, cualquier modificacion al
                formato del archivo prodia generar errores al momento de cargar los productos,
                la plantilla debera ser llenada con los datos requeridos para la creacion de productos.`;
            case 1:
                return(
                    <>
                        <Box component="span" >
                            <p>
                            El archivo cuenta con un total de 24 columnas para llenar con informacion de los productos, los titulos de las
                            columnas no deben ser modificados de ninguna forma o podra generar errores al momento de cargar los productos.
                            </p> <br />
                            <p>Detalles de columnas:</p> <br />
                            <ul>
                                <li> PRODUCTO: Nombre del producto</li>
                                <li> CODIGO SKU: Codigo SKU (codigo del articulo)</li>
                                <li> CODIGO PROVEEDOR: Codigo del proveedor del producto</li>
                                <li> CODIGO DE BARRA: Codigo de barra del producto</li>
                                <li> STOCK: Cantidad disponible del productos</li>
                                <li> STOCK MINIMO: Catidad minima producto (se envia una alerta al llegar a esta cantidad)</li>
                                <li> UNIDAD DE MEDIDA: Codigo de unidad de medida registrada en la plataforma</li>
                                <li> VARIANTE: Codigo de la variante registrada en la plataforma</li>
                                <li> CATEGORIA: Codigo de la categoria registrada en la plataforma</li>
                                <li> P-1: Identificador del precio uno (1)</li>
                                <li> C-1: Precio de compra/adquisicion uno (1)</li>
                                <li> V-1: Precio de venta uno (1) del producto</li>
                            </ul>
                        </Box>
                    </>
                );
            case 2:
                return <>
                    <Box component="span">
                        <p>
                            En la plantilla que le suministramos existen 15 columnas con las letras C, V y P que se repiten acompañadas
                            de un numero, estan divididas para poder registrar hasta 5 precios diferentes, cada numero que acompaña a la letra
                            representa la posicion del precio a registrar. Ejemplo : P-1, C-1, y V-1 representan los datos que se requieren para 
                            almacenar al precio uno (1) en la plataforma.
                        </p>
                        <p>
                            No es posible almacenar mas de los 5 precios en el excel, en caso de que altere el documento añadiendo mas columnas
                            para nuevos precios generara errores en el sistema al momento de cargar los productos
                        </p>
                    </Box>
                </>;
            default:
                return 'Unknown step';
        }
    }

const Instructions = () => {
    return(
        <>
            <Stepper getSteps={getSteps} getStepContent={getStepContent} />
        </>
    )
}

export default Instructions