import React from 'react'
import { connect } from 'react-redux'
import Form from './Form'
import { calculatePricePerPercentage, clearVariantList, loadProductData, restoreProductData, submitProductForm, updateProductForm, updateProductPriceList, updateValueUseStock, updateVariantList } from '../../../redux/actions/ProductsAction'
import { callNotificationSnackbar, updateSystemMessage } from '../../../redux/actions/DashboardAction'
import { getVariants, loadVariantsToTheList, updateSearchVariant, updateVariantsListPagePosition } from '../../../redux/actions/VariantsAction'
import { getCategories } from '../../../redux/actions/CategoriesAction'
import { getUnitSize } from '../../../redux/actions/UnitSizeAction'
import { updateCompositeList } from '../../../redux/actions/CompountProductAction'

const mapPropsToState = state => {
	return{
        priceList : state.Products.createProduct.form.priceList,
        stock : state.Products.createProduct.form.stock,
        stockAlert : state.Products.createProduct.form.stockAlert,
        calculatePrice : state.Products.createProduct.form.calculatePrice,
        useStock : state.Products.createProduct.form.useStock,
        selectedVariants : state.Products.createProduct.form.variants,
        variantsList : state.Products.createProduct.variantsList,
        productBasicData : state.Products.createProduct.form.productBasicData,
        id : state.Products.createProduct.form._id,
        typePage : state.Products.typePage,
        page : state.Variants.page,
        searchVariant : state.Variants.searchVariant,
        variants : state.Variants.list,
        categories : state.Categories.categories,
        unitSize : state.UnitSize.list,
        images : state.Products.images,
	}
}
const mapDispatchToProps = dispatch => {
    return {
        updateProductPriceList : (list) => dispatch(updateProductPriceList(list)),
        updateValueUseStock : (status) => dispatch(updateValueUseStock(status)),
        calculatePricePerPercentage : (status) => dispatch(calculatePricePerPercentage(status)),
        updateProductForm : (attr, value) => dispatch(updateProductForm(attr, value)),
        updateSystemMessage : (locale, alert, prop, status) => dispatch(updateSystemMessage(locale, alert, prop, status)),
        getVariants : (limit,page,variant_id,variantCategory_id,match) => dispatch(getVariants(limit,page,variant_id,variantCategory_id,match)),
        updateVariantsListPagePosition : (page) => dispatch(updateVariantsListPagePosition(page)),
        updateVariantList : (list) => dispatch(updateVariantList(list)),
        updateSearchVariant : (text) => dispatch(updateSearchVariant(text)),
        loadVariantsToTheList : (list) => dispatch(loadVariantsToTheList(list)),
        clearVariantList : () => dispatch(clearVariantList()),
        getCategories : () => dispatch(getCategories(0,0)),
        getUnitSize : () => dispatch(getUnitSize(0,0)),
        submitProductForm : (form, images, typePage, compount) => dispatch(submitProductForm(form, images, typePage, compount)),
        restoreProductData : () => dispatch(restoreProductData()),
        updateCompositeList : (compount) => dispatch(updateCompositeList(compount)),
        callNotificationSnackbar : (active, message, gravity) => dispatch(callNotificationSnackbar(active, message, gravity)),
        loadProductData : (data) => dispatch(loadProductData(data)),
    }
}

const CreateProducts = connect(mapPropsToState, mapDispatchToProps)(({priceList, calculatePrice, useStock, stock, stockAlert, variants, selectedVariants, page, variantsList, searchVariant, categories,unitSize, productBasicData, typePage, id, sizeLeftBox=8, sizeRightBox=4, component, compount, updateCompositeList, images, ...actions}) => {
    const {
		updateProductPriceList,
		loadProductData,
		updateSystemMessage,
		calculatePricePerPercentage,
		updateValueUseStock,
		updateProductForm,
		getVariants,
		updateVariantsListPagePosition,
		updateVariantList,
		updateSearchVariant,
		loadVariantsToTheList,
		clearVariantList,
		getCategories,
		getUnitSize,
		submitProductForm,
		restoreProductData,
		callNotificationSnackbar
	} = actions
	
	React.useEffect(() => {
		if( searchVariant.length <= 0 ){
			getVariants(20,page)
		}
	},[page, searchVariant])

	React.useEffect(() => {
		updateVariantList(variants)
	},[variants])

	React.useEffect(() => {
		return () => {
			restoreProductData()
			updateCompositeList([])
		}
	},[])

	React.useEffect(() => {
		if(searchVariant.length >= 1){
			clearVariantList()
			loadVariantsToTheList([])
			updateVariantsListPagePosition(1)
			getVariants(40, 1, null, null, searchVariant)
		}else{
			clearVariantList()
			loadVariantsToTheList([])
		}
	},[searchVariant])

	return(
		<>
			<Form
				loadProductData={loadProductData}
				callNotificationSnackbar={callNotificationSnackbar}
				variants={variantsList}
				priceList={priceList}
				calculatePrice={calculatePrice}
				updateProductPriceList={updateProductPriceList}
				updateSystemMessage={updateSystemMessage}
				calculatePricePerPercentage={calculatePricePerPercentage}
				useStock={useStock}
				updateValueUseStock={updateValueUseStock}
				stock={stock}
				stockAlert={stockAlert}
				updateProductForm={updateProductForm}
				selectedVariants={selectedVariants}
				{...(searchVariant.length <= 0 && {updateVariantsListPagePosition : updateVariantsListPagePosition})}
				variantPage={page}
				updateSearchVariant={updateSearchVariant}
				categories={categories}
				unitSize={unitSize}
				getCategories={getCategories}
				getUnitSize={getUnitSize}
				productBasicData={productBasicData}
				submitProductForm={submitProductForm}
				typePage={typePage}
				productIdentifier={id}
				sizeLeftBox={sizeLeftBox}
				sizeRightBox={sizeRightBox}
				component={component}
				compount={compount}
				images={images}
			/>
		</>
	)
})

export default CreateProducts