import { Box, Typography } from '@material-ui/core'
import React from 'react'
import Market from './class'

const TotalSale = ({ textLeft="TOTAL :", amount=0, isCurrency=false, prefixLeft="", prefixRigth=""}) => {
    const market = new Market()
    return(
        <>
            <Box maxWidth={400} minWidth={200} my={1} >
                <Box borderRadius={5} display="inline-flex" border={1} p={1} >
                    <Typography component="span">
                        <Box fontWeight="bold" fontSize={24}  >
                            {textLeft} <Typography component="span" style={{ fontSize : "24px", color:"#0bad00", fontWeight :"bold" }} > {prefixLeft} { isCurrency ? market.currencyFormat(amount) : amount} {prefixRigth}</Typography>
                        </Box>
                    </Typography>
                </Box>
            </Box>
        </>
    )
}

export default TotalSale