import constant from ".";
import Product from "../../views/products/class";

export const updateProductSearchValue = (value) => {
    return {type: constant.UPDATE_PRODUCT_SEARCH_VALUE, value}
}
export const searchProductToProductCompount = (value) => async (dispatch) => {
    try{
        const product = new Product()
        const result = await product.search(value, 1, 0, false)
        if( Boolean(result) ){
            if( result.status ){
                dispatch(updateProductSearchValue(result.payload))
            }
        }
    }catch(err){
        console.log(err)
    }
}
export const selectProductFormComposite = (product) => {
    return {type: constant.SELECT_PRODUCT_FOR_COMPOSITE, product}
}
export const openCompositeProductConfigurationWindow = (value) => {
    return {type: constant.OPEN_COMPOSITE_PRODUCT_CONFIGURATION_WINDOW, value}
}
export const updatePositionOfThePuttingUnit = (postion) => {
    return {type: constant.UPDATE_POSITION_OF_THE_PUTTING_UNIT, postion}
}
export const updateSelectedQuantity = (value) => {
    return {type: constant.UPDATE_SELECTED_QUANTITY, value}
}
export const updateCompositeList = (value) => {
    return {type: constant.UPDATE_COMPOSITE_LIST, value}
}
export const resetCompositeProductStatus = () => {
    return {type: constant.RESET_COMPOSITE_PRODUCT_STATUS}
}
export const editSelectedCompositeProduct = (value) => {
    return {type: constant.EDIT_SELECTED_COMPOSITE_PRODUCT, value}
}
export const assignMainProduct = (value) => {
    return {type: constant.ASSIGN_MAIN_PRODUCT, value}
}