import constant from "../actions/index";

export const variants = { name : "", useColorPalette: false, colorHex: "" }

const initialState = {
    list : [],
    categories : [],
    searchVariant : "",
    limit : 10,
	page : 1,
    variantData : {},
    getCategoriesComplete : false,
    createCategoryVariant : false,
    use_colors :{
        activeModalColorPalette : false,
        variant_index : 0,
    },
    create_variant : {
        activeModalCreateVariant : false,
        variantCategory: {
            name : "",
            selected : {}
        },
        variants : [{...variants}]
    },
}

export default function VariantsReducer(state=initialState, action){
    const globalState = {...state}
    switch (action.type) {
        case constant.ACTIVATE_MODAL_USE_COLOR_PALETTE:
            globalState.use_colors.activeModalColorPalette = action.status
            globalState.use_colors.variant_index = action.index
        break;
        case constant.ACTIVATE_MODAL_CREATE_VARIANT:
            globalState.create_variant.activeModalCreateVariant = action.status
        break;
        case constant.ADD_COLOR_PALETTE_TO_VARIANT:
            globalState.create_variant.variants = action.list
            globalState.use_colors.activeModalColorPalette = false
        break;
        case constant.LOAD_VARIANTS_TO_THE_LIST:
            globalState.list = action.variants
        break;
        case constant.ADD_NEW_VARIANT_TO_THE_LIST:
            globalState.create_variant.variants = [...globalState.create_variant.variants, action.variant ]
        break;
        case constant.REMOVE_VARIANT_TO_THE_LIST:
            let variants = [...globalState.create_variant.variants]
            variants.splice(action.position, 1)
            globalState.create_variant.variants = variants
        break;
        case constant.SEARCH_LISTED_VARIANTS_CATEGORY:
            globalState.createCategoryVariant = action.status
        break;
        case constant.UPDATE_VARIANTS_DATA:
            globalState.create_variant.variants = action.variants
        break;
        case constant.UPDATE_NAME_OF_NEW_VARIANT_CATEGORY:
            globalState.create_variant.variantCategory.name = action.name
        break;
        case constant.UPDATE_VARIANT_CATEGORY:
            globalState.create_variant.variantCategory.selected = action.variantCategory
        break;
        case constant.UPDATE_VARIANTS_LIST_PAGE_POSITION:
            globalState.page = action.page
        break;
        case constant.UPDATE_SEARCH_VARIANT:
            globalState.searchVariant = action.text
        break;
        case constant.UPDATE_DATA_OF_THE_REQUEST_FOR_VARIANTS:
            globalState.variantData = action.data
        break;
        default:
            return globalState;
    }
    return globalState;
}