import React from 'react';
import { Box, Grid } from '@material-ui/core';
import TitleViews from '../../../components/TitleViews'
import BasicForm from './BasicForm';
import PriceForm from './PriceForm';
import StockForm from './StockForm';
import { ProductSVG } from '../../../components/icons'
import Autocomplete from '../../../components/Autocomplete';
import Button from '../../../components/Button';
import Product from '../class';

const Form = ({ priceList, calculatePrice, useStock, stock, stockAlert, variants, selectedVariants, variantPage, categories, unitSize, productBasicData, typePage, productIdentifier, sizeLeftBox, sizeRightBox, component, compount, images, ...actions }) => {
    const [image, setImage] = React.useState([])
    const product = new Product()
    const localeText = product.localteText(typePage, compount)
    
    const {
        loadProductData,
        updateProductPriceList,
        updateSystemMessage,
        calculatePricePerPercentage,
        updateValueUseStock,
        updateProductForm,
        updateVariantsListPagePosition,
        updateSearchVariant,
        getCategories,
        callNotificationSnackbar,
        getUnitSize,
        submitProductForm
    } = actions

    const submitForm = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        const form = {productBasicData, priceList, stock, stockAlert, calculatePrice, useStock, _id: productIdentifier}
        submitProductForm(form, image, typePage, compount)
    }

    return(
        <>
            <TitleViews
                mb={2}
                title={localeText.title}
                tooltip={localeText.explicitForm}
                onClick={() => null}
                icon={<ProductSVG />}
            />
            <form method="POST" action="#" encType="multipart/form-data" onSubmit={submitForm} >
				<Grid container spacing={2} >
                    {/* <Grid item xs={12} >
                        <Autocomplete
                            mb={0}
                            label="Variante"
                            options={variants}
                            groupBy={(option) => option.variantCategory.name}
                            value={selectedVariants}
                            onChange={(e,elem) => updateProductForm(e.attr, elem)}
                            name="variants"
                            onInputChange={(event,text) => updateSearchVariant(text)}
                            ListboxProps={{
                                onScroll: (event) => {
                                    const listboxNode = event.currentTarget;
                                    const match = Math.round((listboxNode.scrollTop+listboxNode.clientHeight).toFixed(2))
                                    const clientHeight = listboxNode.scrollHeight
                                    if ( match === clientHeight || match === clientHeight+1 || match === clientHeight-1 ) {
                                        if(typeof updateVariantsListPagePosition === "function"){
                                            updateVariantsListPagePosition(variantPage+1)
                                        }
                                    }
                                }
                            }}
                            renderOption={(option) => {
                                return(
                                    <>
                                        { option.useColorPalette ? <Box mr={1} borderRadius={8} width={25} height={25} bgcolor={option.colorHex} /> : null } { option.name }
                                    </>
                                )
                            }}
                        />
                    </Grid> */}
                    <Grid item xs={12} sm={12} md={sizeLeftBox} lg={sizeLeftBox} >
                        <BasicForm
                            categories={categories}
                            unitSize={unitSize}
                            getCategories={getCategories}
                            getUnitSize={getUnitSize}
                            productBasicData={productBasicData}
                            updateProductForm={updateProductForm}
                            setImage={setImage}
                            images={images}
                            callNotificationSnackbar={callNotificationSnackbar}
                            loadProductData={loadProductData}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12} md={sizeRightBox} lg={sizeRightBox} >
                        {component}
                        <PriceForm
                            priceList={priceList}
                            calculatePrice={calculatePrice}
                            updateProductPriceList={updateProductPriceList}
                            updateSystemMessage={updateSystemMessage}
                            calculatePricePerPercentage={calculatePricePerPercentage}
                        />
                        <StockForm
                            useStock={useStock}
                            updateValueUseStock={updateValueUseStock}
                            stock={stock}
                            stockAlert={stockAlert}
                            updateProductForm={updateProductForm}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12} md={5} lg={4} >
                        <Button size="large" type='submit' >{ localeText.buttonSubmit }</Button>
                    </Grid>
                </Grid>
			</form>
        </>
    )
}

export default Form