import React from 'react'
import Text from './Text'
import { Box, Paper, useTheme } from '@material-ui/core'

const Card = ({ title, text, themeColor }) => {
    const theme = useTheme()
    const color = themeColor ? themeColor : theme.palette.primary.light
    return(
        <>
            <Paper elevation={2} style={{ backgroundColor: color }} >
                <Box p={2} display="inline-flex" flexDirection="column" >
                    <Text text={title} uppercase mb={0} lineHeight={0} />
                    <Text text={text} uppercase mb={0} lineHeight="1.5" />
                </Box>
            </Paper>
        </>
    )
}

export default Card