import { Box, Grid } from '@material-ui/core';
import React from 'react';
import Button from './Button';

const TwoOptions = ({buttonOne="", buttonTwo="", onOne, onTwo}) => {

    const validFunction = (fn) => {
        if( typeof fn === "function" ){
            fn()
        }
    }
    return(
        <>
            <Box my={1} >
                <Grid container spacing={2} >
                    <Grid item xs={6} >
                        <Button
                            variant='outlined'
                            color='secondary'
                            onClick={() => validFunction(onOne)}
                        >{buttonOne}</Button>
                    </Grid>
                    <Grid item xs={6} >
                        <Button
                            variant='outlined'
                            onClick={() => validFunction(onTwo)}
                        >{buttonTwo}</Button>
                    </Grid>
                </Grid>
            </Box>
        </>
    )
}

export default TwoOptions