import React from 'react'
import { Box, IconButton, Link } from '@material-ui/core'
import { CameraSVG } from './icons'
import AddAPhotoRoundedIcon from '@material-ui/icons/AddAPhotoRounded';
import DeleteRoundedIcon from '@material-ui/icons/DeleteRounded';
import Tooltip from './Tooltips';
import Validation from '../methods/Validation';

const LoadImage = ({ width=100, height=100,bgColor="#FFFFFF", onChange=false }) => {
    const [image, setImage] = React.useState('')

    const handlerChange = (evt) => {
        let file = evt.target.files[0]
        const validation = new Validation()
        const result = validation.validateImages(file)
        if( Boolean(result) && result.status ){
            setImage(result.url)
            if( typeof onChange === "function" ){
                onChange({
                    file,
                    blob : result.url,
                })
            }
        }
    }
    const loadFile = () => {
        let inp = document.getElementById('loadFile')
        if( Boolean(inp) ){
            inp.click()
        }
    }

    const delFile = () => {
        let inp = document.getElementById('loadFile')
        inp.value = ""
        setImage('')
        onChange({
            file : [],
            blob : '',
        })
    }
    
    return(
        <>
            <Box bgcolor={bgColor} display="inline-flex" flexDirection="column" alignItems="center" width="100%" >
                <Box width={width} height={height} border={1} borderRadius={150} borderColor="grey.400" overflow="hidden" >
                    <input type="file" id="loadFile" accept='image/*' onChange={handlerChange} style={{ display:"none" }} />
                    {Boolean(image) ?
                        <img src={image} alt='image' /> : 
                        <CameraSVG  width="100%" style={{objectFit : "cover", padding : "18px" }} />
                    }
                </Box>
                <Box width="100%" textAlign="center" mt={1} >
                    <Tooltip title='Subir imagen' placement='bottom' >
                        <IconButton size='small' color="secondary" onClick={loadFile} >
                            <AddAPhotoRoundedIcon />
                        </IconButton>
                    </Tooltip>
                    {Boolean(image) &&
                        <Tooltip title='Eliminar imagen' placement='bottom' >
                            <IconButton size='small' color="primary" onClick={delFile} >
                                <DeleteRoundedIcon />
                            </IconButton>
                        </Tooltip>
                    }
                </Box>
            </Box>
        </>
    )
}

export default LoadImage