import { Box, Divider, Grid, Typography } from '@material-ui/core'
import React from 'react'
import Carouse from '../../../components/Carousel'
import Dialog from '../../../components/Dialog'
import Text from '../../../components/Text'
import { formatDate } from '../../../methods/Moment'
import CompountProduct from '../../compountproducts/class'
import Market from '../../market/class'
import Product from '../class'

const ShowData = ({title, text, children}) => {
    return(
        <>
            <Box p={1} mb={1} >
                <Text text={title} lineHeight={0} mb={0} uppercase fontSize="small" />
                {text &&
                    <Typography >
                        <Box component="span" >{text}</Box>
                    </Typography>
                }
                {children}
            <Divider />
            </Box>
        </>
    )
}

const ViewProduct = ({ isOpenModal, productSelected={}, isCompount, ...actions }) => {
    const { changeProductInformationWindowStatus, selectProductInformation } = actions
    const market = new Market()
    const compountProduct = new CompountProduct()
    const product = new Product()
    const unitSizes = product.unitSize(productSelected.unitSize)
    React.useEffect(() => {
        if( !isOpenModal ){
            selectProductInformation({})
        }
    },[isOpenModal])
    return(
        <>
            <Dialog open={isOpenModal} close={() => changeProductInformationWindowStatus(false)} title='Informacion del producto' maxWidth='xs' >
                {Object.keys(productSelected).length >= 11&&
                    <>
                        {Boolean(productSelected.images) &&
                            <Carouse images={productSelected.images} />
                        }

                        <ShowData title="estado:" text={productSelected.status === "new" ? "Nuevo" : "Usado"} />
                        <ShowData title="nombre:" text={productSelected.name} />
                        <ShowData title="Codigo SKU:" text={!Boolean(productSelected.skucode) ? "No disponible": productSelected.skucode} />
                        <ShowData title="Codigo del proveedor:" text={!Boolean(productSelected.providercode) ? "No disponible": productSelected.providercode} />
                        <ShowData title="Categoria:" text={ productSelected.category.name } />
                        <ShowData title="Unidad de medida:" text={ unitSizes.isCompountUnit ? `${unitSizes.compountUnit.name} (${unitSizes.compountUnit.abbreviation})x${unitSizes.compountUnit.units}${unitSizes.baseUnit.abbreviation}`: `${unitSizes.baseUnit.name} (${unitSizes.baseUnit.abbreviation})`} />
                        <ShowData title="Cantidad disponible:" text={`${productSelected.stock} ${ unitSizes.isCompountUnit ? `(${unitSizes.compountUnit.abbreviation})x${unitSizes.compountUnit.units}${unitSizes.baseUnit.abbreviation}`: `${unitSizes.baseUnit.name} (${unitSizes.baseUnit.abbreviation})`}`} />
                        <ShowData title="Compuesto:" text={productSelected.isCompount ? `Es compuesto`: `No es compuesto`} />
                        <ShowData title={`Costo por ${unitSizes.isCompountUnit ? `${unitSizes.baseUnit.name}`: `${unitSizes.baseUnit.name} (${unitSizes.baseUnit.abbreviation})`}`} text={`$ ${market.currencyFormat(productSelected.costMin)} ${unitSizes.isCompountUnit ? `x(${unitSizes.baseUnit.abbreviation})`: `${unitSizes.baseUnit.name} (${unitSizes.baseUnit.abbreviation})`} `} />
                        <ShowData title={`Costo por unidad`} text={`$ ${market.currencyFormat( unitSizes.isCompountUnit ? (unitSizes.compountUnit.units*productSelected.costMin) : (unitSizes.baseUnit.units*productSelected.costMin) ) }`} />
                        <ShowData title="Precios:">
                            {productSelected.prices.map((price, index) => {
                                return(
                                    <Box key={index} >
                                        <ShowData title={price.identifier}>
                                            <Grid container >
                                                <Grid item xs={6} > COSTO.: ${ market.currencyFormat(productSelected.prices[0].cost) } </Grid>
                                                <Grid item xs={6} >P.V.: ${ market.currencyFormat(price.salePrice) }</Grid>
                                                <Grid item xs={6} >GANANCIA.: ${ market.currencyFormat(price.profit) }</Grid>
                                                <Grid item xs={6} >P.G.: { price.percentageProfit }%</Grid>
                                            </Grid>
                                        </ShowData>
                                    </Box>
                                )
                            })}
                        </ShowData>
                        {productSelected.isCompount &&
                            <ShowData title="Compuesto por:">
                                {productSelected.compount.map((item, index) => {
                                    return(
                                        <Box key={index} >
                                            <ShowData title={item.product.name}>
                                                <Grid container >
                                                    <Grid item xs={12} >COSTO.: ${ compountProduct.calculateCost(item.product, item.quantity, item.positionUnitSize, true) }</Grid>
                                                    <Grid item xs={12} > CANTIDAD.: {item.quantity} {compountProduct.getUnitSize(item.product.unitSize, item.positionUnitSize)} </Grid>
                                                    {item.main &&
                                                        <Grid item xs={12} > PRINCIPAL.: ES PRINCIPAL </Grid>
                                                    }
                                                </Grid>
                                            </ShowData>
                                        </Box>
                                    )
                                })}
                            </ShowData>
                        }
                        <ShowData title="Descripcion:" text={!Boolean(productSelected.description) ? "No disponible": productSelected.description} />
                        <ShowData title="Registrado:" text={formatDate(productSelected.createdAt, "DD/MM/YYYY") } />
                    </>
                }
                
            </Dialog>
        </>
    )
}

export default ViewProduct