const validate = {
    businessName : (businessName) => {
        return (typeof businessName === "string") && (businessName.length >= 2)
    },
    address : (address) => {
        return (typeof address === "string") && (address.length >= 9)
    },
    phoneNumber : (phoneNumber) => {
        return (typeof parseInt(phoneNumber) === "number") && (phoneNumber.length >= 9)
    },
    businessType : (businessType) => {
        return (typeof businessType === "object") && (Object.keys(businessType).length >= 1) && ( Boolean(businessType._id) )
    },
}
function validateData(index, business) {
    const keys = Object.keys(business)
    if( keys.includes(index) ){
        return validate[index](business[index])
    }
}
function validateSubmitNewBusiness (business){
    let errors = {}
    const results = {
        businessName : validateData('businessName', business),
        address : validateData('address', business),
        phoneNumber : validateData('phoneNumber', business),
        businessType : validateData('businessType', business),
    }

    for(let i in results){
        if( !results[i] ){
            errors[i] = true
        }
    }

    return errors
}

function formDataNewBusiness(business={}, files=null){
    const result = validateSubmitNewBusiness(business)
    if( Object.keys(result).length >= 1 ){
        return result
    }else{
        let formData = new FormData()
        business.workingHours = business.workingHours.length >= 1 ? JSON.stringify(business.workingHours) : []
        business.businessType = business.businessType._id 
        for(let key in business ){
            formData.append(key, business[key])
        }
        if( Boolean(files) ){
            formData.append('logo', files)
        }
        return formData
    }
}

export default function Business() {
    this.validate = validate
    this.formDataNewBusiness = formDataNewBusiness
}