import React from 'react';
import { connect } from 'react-redux'
import { Snackbar, Fade, makeStyles, createStyles } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';
import { callNotificationSnackbar } from '../redux/actions/DashboardAction'

const useStyles = makeStyles(theme => createStyles({
  root : {
    backgroundColor : "#6B6B6B"
  }
}))

const Alert = (props) =>{
  return(
    <>
      <MuiAlert elevation={6} variant="filled" {...props} />;
    </>
  )
}

const SnackbarNotification = ({dispatch, trigger=false, message="", severity=false}) => {
  const classes = useStyles()
  React.useEffect(() => {
    if( trigger ){
      setTimeout(() => {
        dispatch(callNotificationSnackbar(false,"",false))
      }, 4000);
    }
  }, [trigger])

  return (
    <div>
      {severity === false &&
        <Snackbar
          open={trigger}
          TransitionComponent={Fade}
          message={message}
          key={Fade}
          className={classes.root}
          autoHideDuration={3500}
        />
      }{typeof severity === "string" &&
        <Snackbar open={trigger} TransitionComponent={Fade} key={Fade} className={classes.root} autoHideDuration={3500}>
          <Alert severity={severity}>
            {message}
          </Alert>
        </Snackbar>
      }
    </div>
  );
}

export default connect(null,null)(SnackbarNotification)