import React from 'react';
import { Box, Grid, Typography, withStyles } from '@material-ui/core';
import { Switch as Swit} from '@material-ui/core';

const Switc = withStyles((theme) => ({
    root: {
        width: 28,
        height: 16,
        padding: 0,
        display: 'flex',
    },
    switchBase: {
    padding: 2,
    color: theme.palette.grey[500],
    '&$checked': {
            transform: 'translateX(12px)',
            color: theme.palette.common.white,
            '& + $track': {
                opacity: 1,
                backgroundColor: theme.palette.primary.main,
                borderColor: theme.palette.primary.main,
            },
        },
    },
    thumb: {
        width: 12,
        height: 12,
        boxShadow: 'none',
    },
    track: {
        border: `1px solid ${theme.palette.grey[500]}`,
        borderRadius: 16 / 2,
        opacity: 1,
        backgroundColor: theme.palette.common.white,
    },
    checked: {},
}))(Swit);

const Switch = ({ name="", checked=false, text='', label='', onChange }) => {

    return(
        <>
            <Box>
                <Typography component='span' >
                    <Box mb={1} >
                        {label}
                    </Box>
                </Typography>
                <Typography component="div">
                    <Grid component="label" container alignItems="center" spacing={1}>
                        <Grid item>
                            <Switc checked={checked} onChange={() => typeof onChange === "function" ? onChange(!checked, name) : null} />
                        </Grid>
                        <Grid item>{text}</Grid>
                    </Grid>
                </Typography>
            </Box>
        </>
    )
}

export default Switch