export const submitDeleteCategoriesForm = (categories, businessID) => (dispatch) => {
	try{
		const data = { categories }
		app.post('/delete/categories', data )
		.then( a => {
			if(a.status === 1){
				const active = true
				dispatch( getCategories(businessID) )
				dispatch( getProducts(businessID) )
				dispatch({ type: 'CALL_FORM_DELETE_CATEGORIE', status : false })
				dispatch({ type : 'CLEAR_DATA_FORM_DELETE' })
				dispatch({ type : 'CALL_NOFICATION_SNACKBAR', active, message : 'La categoria ha sido eliminada' })
				setTimeout(_ => dispatch({ type : 'CALL_NOFICATION_SNACKBAR', active : !active, message : '' }), 3000)
			}
		})
	}catch(err){
		console.log(err)
	}
}
