export const addNewFieldToCreateCategoryForm = () => {
	return { type : constant.ADD_NEW_FIELD_TO_CREATE_CATEGORY_FORM }
}
export const removeFieldToCreateCategoryForm = (index) => {
	const list = store.getState().Categories.createCategorie.categoriesForm
	const newList = [...list]
	newList.splice(index,1)
	return { type : constant.REMOVE_FIELD_TO_CREATE_CATEGORY_FORM, newList }
}
export const updateFieldInCategoryList = (index, attr, value) => {
	const list = store.getState().Categories.createCategorie.categoriesForm
	let newList = parseData(list)
	newList[index][attr] = value
	return { type : constant.UPDATE_FIELD_IN_CATEGORY_LIST, newList }
}
