export const submitCategoriesForm = (categories) => (dispatch) => {
	try{
		console.log(categories)
		dispatch( awaitResponseFromSaveCategories(true) )
		app.post('/save/categories', {categories} )
		.then( response => {
			if( response.status ){
				dispatch( awaitResponseFromSaveCategories(false) )
				dispatch(callNotificationSnackbar(true,'Las categorias se crearon satisfactoriamente','success'))
				dispatch(addNewCategoriesToTheCategoryList(response.payload))
				dispatch( closeFormCreateCategories(false) )
			}
		})
	}catch(err){
		console.log(err)
	}
}
export const getCategories = (page=1, limit=10) => async (dispatch) => {
	try{
		dispatch(updateStatusOfEarnedCategories(false))
		const response = await categorie.bringCategories(page, limit)
		if( response.status ){
			dispatch(loadListOfCategories(response.payload))
			dispatch(updateInformationOfTheApplicationOfTheCategories(response.data))
			dispatch(updateStatusOfEarnedCategories(true))
		}
	}catch(err){
		console.log(err)
	}
}
export const getCategoriesWhithMatch = (match='') => (dispatch) => {
	try{
		dispatch(updateStatusOfEarnedCategories(false))
		app.get(`/get/categories?match=${match}`)
		.then( response => {
			if( response.status ){
				dispatch(loadListOfCategories(response.payload))
				dispatch(updateInformationOfTheApplicationOfTheCategories(response.data))
			}
			dispatch(updateStatusOfEarnedCategories(true))
		})
	}catch(err){
		console.log(err)
	}
}