import { createMuiTheme } from '@material-ui/core/styles';


let primaryPalette = {
  contrastText : '#FFFFFF',
  main: '#EC407A', //03A9F4
  dark : '#ea3a72', //0288D1
  light : '#fde8ef', //B3E5FC
  gradiantPrimary : 'linear-gradient(0deg, #0288D1  30%, #03A9F4 90%)'
}

let secondaryPalette = {
  contrastText : '#FFFFFF',
  main: '#203461', //ff3535
  dark : '#1c2f59', //c3000c
  light : '#e4e7ec', //ff7060
}

const theme = createMuiTheme({
  shape : {
    borderRadius: 8
  },
  mixins : {
    toolbar: {
      minHeight: 35
    }
  },
  typography: {
    subtitle1: {
      fontFamily: 'Klavika, Arial',
      fontWeight: 'bold',
      fontSize : '2rem',
      width : '100%'
    },
    subtitle2: {
      fontWeight: 'bold',
      fontSize : '1.5rem',
      width : '100%'
    },
    button : {
      fontWeight : "bold"
    },
  },
  palette: {
    primary: primaryPalette,
    secondary :secondaryPalette
  },
  overrides: {
    MuiTableRow: {
        root: {
        '&.Mui-selected, &.Mui-selected:hover': {
          backgroundColor: primaryPalette.light
        }
      }
    },
    MuiCssBaseline: {
      '@global': {
        '*::-webkit-scrollbar': {
          width: '0.5em',
        },
        '*::-webkit-scrollbar-thumb': {
          backgroundColor: 'rgba(0,0,0,.2)',
          borderRadius : "50px"
        },
        '.MuiAutocomplete-option[aria-selected="true"]' : {
          background: `${primaryPalette.light} !important`,
          padding : "12px",
          margin : "0px 10px",
          borderRadius : "8px"
        },
        '.MuiAutocomplete-option[data-focus="true"]': {
          background: `${primaryPalette.light} !important`,
          padding : "12px",
          margin : "0px 10px",
          borderRadius : "8px"
        }
      }
    }
  },
});

console.log(theme)

export default theme