import React from 'react'
import { DeleteSVG, UnitSize } from '../../../components/icons'
import TitleViews from '../../../components/TitleViews'
import Table from '../../../components/Table'
import { goCreateUnitSize } from '../class'
import { connect } from 'react-redux'
import { deleteUnitSize, editModeUnitOfMeasure, getUnitSize, iAmInTheListOfMeasurementUnits, openWindowDeleteUnitOfMeasurement, restoreStateOfTheListUnitsOfMeasurement, selectMultipleUnitsOfMeasurement, selectUnitOfmeasurementToDelete, updateFormdataCreateMeasurementUnit, updateUniqueIdentifierOfTheSelectedUnit } from '../../../redux/actions/UnitSizeAction'
import { Box, IconButton } from '@material-ui/core'
import ActionButtons from '../../../components/ActionButtons'
import DeleteUnitSizes from '../delete_unit_size'
import Tooltip from '../../../components/Tooltips'

const mapDispatchToProps = (dispatch) => {
    return {
        getUnitSize : (limit, page) => dispatch(getUnitSize(limit, page)),
        restoreStateOfTheListUnitsOfMeasurement : () => dispatch(restoreStateOfTheListUnitsOfMeasurement()),
        iAmInTheListOfMeasurementUnits : (value) => dispatch(iAmInTheListOfMeasurementUnits(value)),
        updateFormdataCreateMeasurementUnit : (attr, value) => dispatch(updateFormdataCreateMeasurementUnit(attr, value)),
        editModeUnitOfMeasure : (value) => dispatch(editModeUnitOfMeasure(value)),
        updateUniqueIdentifierOfTheSelectedUnit : (value) => dispatch(updateUniqueIdentifierOfTheSelectedUnit(value)),
        openWindowDeleteUnitOfMeasurement : (value) => dispatch(openWindowDeleteUnitOfMeasurement(value)),
        selectUnitOfmeasurementToDelete : (value) => dispatch(selectUnitOfmeasurementToDelete(value)),
        selectMultipleUnitsOfMeasurement : (value) => dispatch(selectMultipleUnitsOfMeasurement(value)),
        deleteUnitSize : (unitSizeSelected, unitSizeSelectedList) => dispatch(deleteUnitSize(unitSizeSelected, unitSizeSelectedList)),
    }
}
const mapPropsToState = (state) => {
    return {
        unitSizes : state.UnitSize.list,
        isOpenModal : state.UnitSize.deleteUnitSize.isOpenModal,
        unitSizeSelected : state.UnitSize.deleteUnitSize.unitSizeSelected,
        multipleUnitsSizeSelected : state.UnitSize.deleteUnitSize.multipleUnitsSizeSelected,
    }
}

const ManageUnitSize = connect(mapPropsToState, mapDispatchToProps)(({unitSizes, iAmInTheListOfMeasurementUnits, isOpenModal, unitSizeSelected, multipleUnitsSizeSelected, ...actions}) => {
    const { getUnitSize, restoreStateOfTheListUnitsOfMeasurement, updateFormdataCreateMeasurementUnit, editModeUnitOfMeasure, updateUniqueIdentifierOfTheSelectedUnit, openWindowDeleteUnitOfMeasurement, selectUnitOfmeasurementToDelete, selectMultipleUnitsOfMeasurement, deleteUnitSize } = actions
    const tools = multipleUnitsSizeSelected.length <= 0 ? {
        field: ' ',
        headerName: ' ',
        align : "center",
        editable: false,
        flex : 1,
        renderCell : (params) => {
                return <ActionButtons
                    view={false}
                    disabled={false}
                    onEdit={() => {
                        editModeUnitOfMeasure(true)
                        updateUniqueIdentifierOfTheSelectedUnit(params.row._id)
                        updateFormdataCreateMeasurementUnit('name', params.row.name)
                        updateFormdataCreateMeasurementUnit('abbreviation', params.row.abbreviation)
                        updateFormdataCreateMeasurementUnit('compoundUnitOfMeasure', params.row.compoundUnitOfMeasure)
                        if( params.row.compoundUnitOfMeasure ){
                            updateFormdataCreateMeasurementUnit('baseUnit', params.row.baseUnit)
                            updateFormdataCreateMeasurementUnit('compoundUnit', params.row.compoundUnit)
                        }
                        goCreateUnitSize()
                    }}
                    onDeleted={() => {
                        selectUnitOfmeasurementToDelete(params.row)
                        openWindowDeleteUnitOfMeasurement(true)
                    }}
                />
        },
    } : {}
    const columns = [
        {
            field: 'name',
            headerName: 'Unidad de medida',
            editable: false,
            flex : 1,
        },{
            field: 'abbreviation',
            headerName: 'Signo/Abreviatura',
            editable: false,
            flex : 1,
            align : "center"
        },{
            field: 'baseUnit',
            headerName: 'Unidad base',
            editable: false,
            flex : 1,
            renderCell : ({row}) => {
                return(
                    <Box display="flex" alignItems="center" >
                        { Boolean(row.baseUnit) ? row.baseUnit.name : "" }
                    </Box>
                )
            }
        },{
            field: 'compoundUnit',
            headerName: 'Cantidad',
            editable: false,
            flex : 1,
            renderCell : ({row}) => {
                return(
                    <Box display="flex" alignItems="center" >
                        {row.compoundUnit >= 1 ? row.compoundUnit : ""} { Boolean(row.baseUnit) ? row.baseUnit.name : "" }
                    </Box>
                )
            }
        },{
            field: 'unitSizeCode',
            headerName: 'Codigo',
            editable: false,
            flex : 1,
            renderCell : ({row}) => {
                return(
                    <Box display="flex" alignItems="center" >
                        {`UNI-${row.unitSizeCode <= 9 ? `0${row.unitSizeCode}` : row.unitSizeCode }`}
                    </Box>
                )
            }
        }, {...tools}
    ]

    React.useEffect(() => {
        getUnitSize(10, 1)
        iAmInTheListOfMeasurementUnits(true)
    },[])

    React.useEffect(() => {
        return () => {
            restoreStateOfTheListUnitsOfMeasurement()
        }
    },[])
    return(
        <>
            <TitleViews
                mb={2}
                title="Lista de unidades de medidas"
                tooltip="Crear nueva unidad de medida"
                onClick={() => goCreateUnitSize()}
                icon={<UnitSize />}
            />
            { multipleUnitsSizeSelected.length >= 1 &&
                <>
                    <Tooltip title='Eliminar seleccion' >
                        <IconButton color='secondary' onClick={() => openWindowDeleteUnitOfMeasurement(true)} >
                            <DeleteSVG />
                        </IconButton>
                    </Tooltip>
                </>
            }
            <Table
                // updatePage={(pg) => updateVariantsListPagePosition(pg)}
                // page={position}
                // rowCount={variantsTotal}
                checkboxSelection
                rows={unitSizes}
                columns={columns}
                // overlayText="Cargando unidades de medidas"
                onSelection={(selection) => selectMultipleUnitsOfMeasurement(selection)}
                loading={true}
                selectionModel={multipleUnitsSizeSelected}
            />
            <DeleteUnitSizes
                openWindowDeleteUnitOfMeasurement={openWindowDeleteUnitOfMeasurement}
                isOpenModal={isOpenModal}
                unitSizeSelected={unitSizeSelected}
                multipleUnitsSizeSelected={multipleUnitsSizeSelected}
                selectUnitOfmeasurementToDelete={selectUnitOfmeasurementToDelete}
                selectMultipleUnitsOfMeasurement={selectMultipleUnitsOfMeasurement}
                deleteUnitSize={deleteUnitSize}
            />
        </>
    )
})

export default ManageUnitSize