import React from 'react';
import { Accordion as Accor, AccordionDetails, AccordionSummary, Box, makeStyles } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
}));

const Accordion = ({ title="", children }) => {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className={classes.root}>
      <Accor expanded={expanded === 'panel1'} onChange={handleChange('panel1')} elevation={0} >
        <AccordionSummary expandIcon={<ExpandMoreIcon />} style={{ padding : "0px", margin : "0px" }} >
          { title }
        </AccordionSummary>
        <AccordionDetails style={{ padding : "0px" }} >
          <Box width="100%" >
            { children }
          </Box>
        </AccordionDetails>
      </Accor>
    </div>
  );
}

export default Accordion