import constant from './index';
import app from '../../connection/Api'
import locale from '../../handlers/locale';
import { loadDataInInformationState } from '../actions/UserInformationAction'
import Authentication from '../../views/auth/Class';

export const getCountries = (id) => (dispatch) => {
	try{
		const route = Boolean(id) ? `/get/countries/${id}` : `/get/countries`
		app.get(route)
		.then(data => {
			dispatch(loadDataInInformationState('countries',data.countries))
		})
		.catch(err => console.log(err))
	}catch(err){
		console.log(err)
	}
}
