export const updateAuthState = (state,value) => {
	return { type : "UPDATE_STATE_AUTH", state, value }
}
export const consultUser = (query, state) => (dispatch) => {
	try{
		const { attr, value } = query
		const _state = {...state}
		if( value.trim() !== "" && value.length >= 1){
			app.get(`/get/data/from/a/user?field=${attr}&value=${value}`)
			.then(resp => {
				if( Boolean(resp.user) ){
					if( attr === "email" ) {
						_state.duplicateEmail = true
						_state.errors[attr] = "El correo electronico ya se encuentra registrado"
					}
					if( attr === "username" ){
						_state.duplicateUsername = true
						 _state.errors[attr] = "El nombre de usuario ya se encuentra registrado"
					}
					dispatch(updateAuthState('register', _state))
				}else{
					if( attr === "email" ) {
						_state.duplicateEmail = false
					}
					if( attr === "username" ){
						_state.duplicateUsername = false
					}
					delete _state.errors[attr]
					dispatch(updateAuthState('register', _state))
				}
			})
			.catch(err => console.log(err))
		}
	}catch(err){
		console.log(err)
	}
}
export const saveUser = (data) => (dispatch) => {
	try{
		app.post(`/register/user`, data, false)
		.then(resp => {
			if( Boolean(resp.data.token) ){
				localStorage.setItem('user_token', resp.data.token)
				dispatch(updateAuthState( 'data', resp.data.user ))
			}
		})
		.catch(err => console.log(err))
	}catch(err){
		console.log(err)
	}
}
export const secureLogin = () => (dispatch) => {
	try{
		app.post(`/login/secure/user`)
		.then(resp => {
			dispatch(updateAuthState( 'data', resp.data ))
		})
		.catch(err => console.log(err))
	}catch(err){
		console.log(err)
	}
}
export const userLogIn = (data) => async (dispatch) => {
	try{
        const auth = new Authentication()
		const result = await auth.logIn(data)
		if( result.status ){
            localStorage.setItem('user_token', result.payload.token)
			window.location.reload(true)
        }
	}catch(err){
		console.log(err)
	}
}