import React from 'react'
import { Box, CircularProgress, Fade, Grow, LinearProgress, Typography } from '@material-ui/core';

const styles = {
	fullProgress : {
	    width: '100%',
		height: '100%',
		top : '0px',
		left : '0px',
	    display: 'flex',
	    justifyContent: 'center',
	    alignItems: 'center',
		flexDirection : 'column',
	    position: 'absolute',
		zIndex : '10000',
		backgroundColor : '#FFFFFF'
	},
	sectionBarProgress : {
		height: '100%',
		flexDirection :"column",
		display: 'flex',
		justifyContent: 'center',
		alignItems: 'center',
		width: '100%',
		background: 'rgb(255,255,255,.8)',
		position: 'absolute',
		zIndex: '1000',
	},
	formProgress : {
		marginTop : '20px',
		marginBotton : '20px'
	}
}

export const FullProgress = ({ message="" }) => {
	return(
		<div style={styles.fullProgress}>
			<CircularProgress />
			<Typography component="span" >
				<Box fontWeight="bold" mt={5}  style={{ textTransform : 'uppercase' }} >
					{message}
				</Box>
			</Typography>
		</div>
	)
}
export const ProgressSectionApp = ({ label="Procesando información..." }) => {
	return(
		<Grow in={true} >
			<div style={styles.sectionBarProgress}>
				<CircularProgress />
				<Typography component="span" variant="subtitle1" >
					<Box marginTop={1} fontSize={18} textAlign="center" style={{ textTransform : "uppercase" }} >
						{label}
					</Box>
				</Typography>
			</div>
		</Grow>
	)
}
export const BarProgress = () => <div style={styles.formProgress}> <LinearProgress /> </div>
