import React from 'react'
import { connect, useDispatch } from 'react-redux'
import { Switch, Route } from "react-router-dom";
import SpeedDial from '../../components/SpeedDial'
import SnackbarNotification from '../../components/Snackbar'
import Container from './components/Container'
import { FullProgress, } from '../../components/ProgressApp'
import ManageBusiness from '../business/manage_business';
import CreateProducts from '../products/create_product';
import ManageRolesAndPermissions from '../roleandpermissions/ManageRolesAndPermissions'
import CreateRole from '../roleandpermissions/CreateRole'
import ManageProducts from '../products/list_products/';
import Market from '../../views/market/';
import PopupsModals from '../../components/PopupsModal';
import { loadInitialData, updateSystemMessage } from '../../redux/actions/DashboardAction';
import { CreateBusinessModal } from '../business/create_business/CreateBusinessModal';
import ListCategories from '../categories/list_categories';
import Alert from '../../components/Alert';
import ListVariants from '../variants/list_variants';
import ManageUnitSize from '../unitSize/list_unitSize';
import CreateCompountProducts from '../compountproducts/create_compount_product';
import Reports from '../reports/tableReports';
import MainPage from '.';
import Sockets from '../../socket/Socket';
import ManageCompountProducts from '../compountproducts/list_compount_product';

const mapPropsToState = state => {
	return {
		data : state.authState.data,
		business : state.Business.business,
		requestComplete : state.Business.requestComplete,
		activateMandatoryForm : state.Business.createBusiness.activateMandatoryForm,
		callNotificationComponent : state.Dashboard.callNotificationComponent,
		locale : state.Dashboard.alertSistem.locale,
		alert : state.Dashboard.alertSistem.alert,
		isOpen : state.Dashboard.alertSistem.isOpen,
		prop : state.Dashboard.alertSistem.prop,
	}
}

const mapDispatchToProps = dispatch => {
	return {
		updateSystemMessage : (locale, alert, status) => dispatch(updateSystemMessage(locale, alert, status))
	}
}

const Dashboard = connect(mapPropsToState, mapDispatchToProps)(({ data, business, activateMandatoryForm, requestComplete, callNotificationComponent, locale, alert, isOpen, prop, ...actions }) => {
	const { updateSystemMessage } = actions
	const dispatch = useDispatch()
	const runSocket = React.useCallback(() => {
		const socket = new Sockets()
	},[])
	React.useEffect(() => {
		runSocket()
	},[])
	React.useEffect(() => {
		dispatch(loadInitialData())
	},[])
	
	return(
		<>
			{ (Object.keys(data).length >= 1) && (requestComplete === true) ?
				<>
					{ (business.length >= 1) ?
						<>
							<Container userData={data} businessActive={business.filter(company => company.active === true)} >
								<Switch>
									<Route path="/:businessName/:market?" exact component={ManageBusiness} />
									<Route path="/:businessName/manage/categories/:market?" exact component={ListCategories} />
									<Route path="/:businessName/create/product/:market?" exact component={CreateProducts} />
									<Route path="/:businessName/manage/variants/:market?" exact component={ListVariants} />
									<Route path="/:businessName/manage/unit/sizes/:market?" exact component={ManageUnitSize} />
									<Route path="/:businessName/manage/products/:market?" exact component={ManageProducts} />
									<Route path="/:businessName/create/compount/product/:market?" exact component={CreateCompountProducts} />
									<Route path="/:businessName/manage/compount/product/:market?" exact component={ManageCompountProducts} />
									<Route path="/:businessName/manage/reports/:market?" exact component={Reports} />
									<Route path="/:market?" exact component={MainPage} />

									<Route path="/roles/list/:market?" exact component={ManageRolesAndPermissions} />
									<Route path="/create/role/:market?" exact component={CreateRole} />
								</Switch>
								<PopupsModals />
								{callNotificationComponent.active &&
									<SnackbarNotification
										trigger={callNotificationComponent.active}
										message={callNotificationComponent.message}
										severity={callNotificationComponent.severity}
									/>
								}
								{/* { business.length >= 1 && <SpeedDial /> } */}
								{/* <Market /> */}
								<Market />
								<Alert alert={alert} locale={locale} isOpen={isOpen} prop={prop} close={() => updateSystemMessage('', '', null, false)}  />
							</Container>
						</> :
						<>
							<CreateBusinessModal greeting isOpen={activateMandatoryForm} />
						</>
					}
					
				</> :
				<>
					<FullProgress message="Cargando información" />
				</>
			}
		</>
	)
})

export default Dashboard