import React from 'react'
import { Box } from '@material-ui/core'
import Dialog from '../../../components/Dialog'
import { Sheets } from '../../../components/icons'
import LoadFile  from '../../../components/LoadFile'
import { connect } from 'react-redux'
import Product, { loadProductsFileWindow } from '../class'
import Instructions from './Instructions'

const mapPropsToState = (state) => {
    return {
        status : state.Products.loadProducts.isOpenModal
    }
}

const LoadProducts = connect(mapPropsToState,null)(({status}) => {
      
    return(
        <>
            <Dialog fullScreen open={status} close={() => loadProductsFileWindow()} title="Cargar lista de productos" >
                <Box display="flex" width="100%" justifyContent="right" >
                    <LoadFile
                        icon={<Sheets />}
                        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                        onReader={(data) => {
                            const product = new Product(data)
                            console.log(":DDDDD",product)
                        }}
                    />
                </Box>
                <Instructions />
            </Dialog>
        </>
    )
})

export default LoadProducts