import React from 'react'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import ListItem from '../../../components/ListItem';
import { StoreSVG, DashboardSVG } from '../../../components/icons';

const CollapseList = ({userData, businessActive}) => {

	const history = useHistory()
	const goRoute = (path) => {
		history.push(path.replace(/ /g, "-"))
	}
	return(
		<React.Fragment>
			{/* <ListItem text="Administrar negocio" icon={<StoreSVG />} onClick={_ => goRoute('/admin/list/business')} /> */}
			<ListItem text="Dashboard" icon={<DashboardSVG />} onClick={_ => goRoute(`/`)} />
			<ListItem text="Administrar negocio" icon={<StoreSVG />} onClick={_ => goRoute(`/${businessActive[0].businessName}`)} />
			{/* <ListItem text="Roles y permisos" icon={<RoleSVG />} onClick={_ => goRoute('/admin/roles/list')} /> */}
			{/* <ListItem text="Inhabilitados" icon={<InhabilitadoSVG />} onClick={_ => goRoute('/admin/off')} /> */}
			{/* <ListItem text="Vender" icon={<CashRegisterSVG />} onClick={_ => goRoute(`${history.location.pathname}/market`)} /> */}
		</React.Fragment>
	)
}

export default connect(null,null)(CollapseList)