import constant from '../actions/index'

const initialState = {
    list : [],
    dateFilter : {
        initDate: "",
        finDate: "",
    }
}

export default function ReportsReducer(state = initialState, action){
    const globalState = {...state}
    switch(action.type){
        case constant.UPDATE_REPORTING_DATES:
            globalState.dateFilter.initDate = action.initDate
            globalState.dateFilter.finDate = action.finDate
        break
        case constant.ADD_REPORTS_TO_LIST:
            globalState.list = action.reports
        break
        default :
            return globalState
    }
    return globalState
}