import React from 'react'
import { connect } from 'react-redux'
import { Mail } from './icons'
import Menu from './Menu';
import TextsmsIcon from '@material-ui/icons/Textsms';
import { EmptyNotifications } from './NotificationsItems';

const Mails = () => {
	return(
		<>
			<Menu
                title="Mensajes"
                width={400}
                maxHeight={100}
                iconComponent={<Mail width="25px" />}
            >
				<EmptyNotifications Icon={TextsmsIcon} message="No tienes mensajes disponibles" />
			</Menu>
		</>
	)
}

export default connect(null,null)(Mails)