import React from 'react';
import { connect } from 'react-redux';
import Dialog from '../../../components/Dialog';
import { activateModalCreateCategory } from '../../../redux/actions/CategoriesAction';
import CreateCategorie from './'

const mapPropsToState = state => {
    return {
        activeCreateCategorieModal : state.Categories.createCategorie.activeCreateCategorieModal,
        createCategorieModalMessage : state.Categories.createCategorie.createCategorieModalMessage,
        createCategorieModalMessageSeverity : state.Categories.createCategorie.createCategorieModalMessageSeverity,
    }
}
const mapDispatchToProps = (dispatch) => {
	return {
		activateModalCreateCategory : () => dispatch(activateModalCreateCategory(false))
	}
}

const CreateCategorieModal = connect(mapPropsToState, mapDispatchToProps)(({activeCreateCategorieModal, createCategorieModalMessage, createCategorieModalMessageSeverity, activateModalCreateCategory}) => {
	return(
		<>
			<Dialog open={activeCreateCategorieModal} close={activateModalCreateCategory} alert={createCategorieModalMessage} severity={createCategorieModalMessageSeverity} title='Registrar Categoria' >
				<CreateCategorie />
			</Dialog>
		</>
	)
})

export default CreateCategorieModal