import constant from './index'
import app from '../../connection/Api'
import { getCategories } from './UserInformationAction'
import { callNotificationSnackbar } from './DashboardAction'
import Product from '../../views/products/class'
import { updateCompositeList } from './CompountProductAction'
const product = new Product()

export const updateProductPriceList = (list) => {
	return { type : constant.UPDATE_PRODUCT_PRICE_LIST, list }
}
export const activeModalLoadMasiveProducts = (status) => {
	return { type : constant.ACTIVE_MODAL_LOAD_MASIVE_PRODUCTS, status }
}
export const calculatePricePerPercentage = (status) => {
	return { type : constant.CALCULATE_PRICE_PER_PERCENTAGE, status }
}
export const updateValueUseStock = (status) => {
	return { type : constant.UPDATE_VALUE_USE_STOCK, status }
}
export const updateProductForm = (attr, value) => {
	return { type : constant.UPDATE_PRODUCT_FORM, attr, value }
}
export const updateVariantList = (list) => {
	return {type : constant.UPDATE_VARIANT_LIST, list}
}
export const clearVariantList = () => {
	return {type : constant.CLEAR_VARIANT_LIST}
}
export const updateListsAfterSaving = (businessID) => (dispatch) => {
	try{
		app.get(`/get/all/products/${businessID}`)
		.then( res => {
			if(res.status === 1){
				if( res.products.length >= 1 ){
					dispatch({ type : 'LOAD_PRODUCTS', products : res.products })
					dispatch( getCategories(businessID) )
				}
			}
		})
	}catch(err){
		console.log(err)
	}
}
export const submitProductForm = (productForm, images, typePage="create", compount=false) => async (dispatch) => {
	try{
		const localeText= product.localteText(typePage, compount)
		const result = await product.save(productForm, images, typePage, compount)
		if( result.status ){
			dispatch(callNotificationSnackbar(true, localeText.successMessage, "success"))
			if( typePage === "create" ) dispatch(restoreProductData())
			if( typePage === "create" && compount === true ) dispatch(updateCompositeList([]))
		}else{
			dispatch(callNotificationSnackbar(true, localeText.errorMessage, "error"))
		}
	}catch(err){
		console.log(err)
	}
}
export const saveProducts = (products) => {
	return {type: constant.SAVE_PRODUCTS, products}
}
export const getProductList = (page, limit, isCompount=false) => async (dispatch) => {
	try{
		const result = await product.get(page, limit,null,null,null,null,isCompount)
		if( result.status ){
			dispatch(saveProducts(result.payload))
			dispatch(updateTotalRows(result.data.data_per_page))
			dispatch(updateTotalResults(result.data.product_total))
		}
	}catch(err){
		console.log(err)
	}
}
export const loadProductData = (product) => {
	return {type: constant.LOAD_PRODUCT_DATA, ...product }
}
export const selectProductToDelete = (product) => {
	return {type: constant.SELECT_PRODUCT_TO_DELETE, product }
}
export const showAlertToDeleteProduct = (value) => {
	return {type: constant.SHOW_ALERT_TO_DELETE_PRODUCT, value }
}
export const waitingAnswer = (value) => {
	return {type: constant.WAITING_ANSWER, value }
}
export const deleteProducts = (productList, isCompount=false) => async (dispatch) => {
	try{
		dispatch(waitingAnswer(true))
		const {identifiers, result} = await product.delete(productList, isCompount)
		if( result.status ){
			dispatch(showAlertToDeleteProduct(false))
			const message = identifiers.length >= 2 ? "Productos Eliminados con exito" : "Producto eliminado con exito"
			dispatch(callNotificationSnackbar(true, message, "success"))
			const products = await product.get(1,10,null,null,null,null,isCompount)
			if( products.status ){
				dispatch(saveProducts( products.payload ))
				dispatch(updateTotalRows(products.data.data_per_page))
				dispatch(updateTotalResults(products.data.product_total))
			}
			dispatch(waitingAnswer(false))
		}
	}catch(err){
		console.log(err)
	}
}
export const updatePage = (page) => {
	return {type: constant.UPDATE_PAGE, page }
}
export const updateTotalRows = (totalRows) => {
	return {type: constant.UPDATE_TOTAL_ROWS, totalRows }
}
export const updateTotalResults = (totalResults) => {
	return {type: constant.UPDATE_TOTAL_RESULTS, totalResults }
}
export const updateProductSelecteds = (products) => {
	return {type: constant.UPDATE_PRODUCT_SELECTEDS, products }
}
export const restoreProductData = () => {
	return {type: constant.RESTORE_PRODUCT_DATA }
}
export const updateTypePage = (page) => {
	return {type: constant.UPDATE_TYPE_PAGE, page }
}
export const applyProductIdentifier = (id) => {
	return {type: constant.APPLY_PRODUCT_IDENTIFIER, id }
}
export const changeProductInformationWindowStatus = (value) => {
	return {type: constant.CHANGE_PRODUCT_INFORMATION_WINDOW_STATUS, value}
}
export const selectProductInformation = (product) => {
	return {type: constant.SELECT_PRODUCT_INFORMATION, product}
}