import { Box, Grid } from '@material-ui/core'
import React from 'react'
import Alert from '../../components/Alert'
import Button from '../../components/Button'
import { deleteElement } from '../../methods/parseData'

const DeleteMarket = ({ isFocusTable, productsResults, listPosition, shoppingList, openDeleteModal, ...actions }) => {
    const { activateWindowToRemoveProductFromTheShoppingList, updateProductsToShoppingList, activateResultList } = actions
    const [product, setProduct ] = React.useState({})
    const position = isFocusTable ? 0 : listPosition
    const deleteItem = () => {
        const newProductList = deleteElement(shoppingList, (item) => item.product._id === product._id ) 
        activateWindowToRemoveProductFromTheShoppingList(false)
        activateResultList(false)
        updateProductsToShoppingList(newProductList)
    }
    React.useEffect(() => {
        const productSelected = productsResults[position]
        setProduct(productSelected)
    },[position, listPosition, productsResults])
    return(
        <>
            <Alert isOpen={openDeleteModal} close={() => activateWindowToRemoveProductFromTheShoppingList(false)} textAlert={`DESEA ELIMINAR EL PRODUCTO "${product.name}" DE LA LISTA DE COMPRAS`}>
                <Box mt={2} >
                    <Grid container spacing={2} >
                        <Grid item xs={6} >
                            <Button color='secondary' variant='outlined' onClick={deleteItem} >ELIMINAR</Button>
                        </Grid>
                        <Grid item xs={6} >
                            <Button variant='outlined' onClick={() => activateWindowToRemoveProductFromTheShoppingList(false)} >CERRAR</Button>
                        </Grid>
                    </Grid>
                </Box>
            </Alert>
        </>
    )
}

export default DeleteMarket