import { Box, Grid, Paper } from '@material-ui/core';
import React from 'react';
import { Checkbox } from '../../../components/Checkbox';
import HelpDescription from '../../../components/HelpDescription';
import TextField from '../../../components/TextField';
import Tooltip from '../../../components/Tooltips';

const StockForm = ({ useStock, stock, stockAlert,...actions }) => {
    const { updateValueUseStock, updateProductForm=()=>null } = actions

    const handlerChanges = (payload) => {
        let { attr, value } = payload
        value = parseInt(value)
        updateProductForm(attr, value)
    }

    React.useEffect(() => {
        if( !useStock ){
            updateProductForm('stock', 0)
            updateProductForm('stockAlert', 0)
        }else{
            updateProductForm('stockAlert', 5)
        }
    },[useStock])
    return(
        <>
            <Paper variant='outlined' >
                <Box p={2} >
                    <TextField
                        disabled={!useStock}
                        required={useStock}
                        type="number"
                        label="Stock"
                        inputProps={{ min: 0 }}
                        value={stock}
                        name="stock"
                        onChange={handlerChanges}
                    />
                    <TextField
                        disabled={!useStock}
                        required={useStock}
                        type="number"
                        name="stockAlert"
                        label="Stock minimo"
                        value={stockAlert}
                        onChange={handlerChanges}
                        inputProps={{ min: 0 }}
                        InputProps={{
                            endAdornment: <HelpDescription position="end" title="Asigne un valor para notificarle cuando el producto este por terminarse" />
                        }}
                    />
                    <Tooltip title='Al no usar stock no podra llevar un control de la cantidad de producto disponible (siempre habran "unidades disponibles" en el inventario). ' >
                        <Box>
                            <Checkbox
                                label="No usar stock"
                                onChange={(res) => updateValueUseStock(!res.value)}
                            />
                        </Box>
                    </Tooltip>
                </Box>
            </Paper>
        </>
    )
}

export default StockForm