import React from 'react'
import { Grid } from '@material-ui/core'

const DividerBox = ({ leftComponent, rightComponent }) => {
    return(
        <>
            <Grid container spacing={2}>
                <Grid item md={12} >
                    {leftComponent}
                </Grid>
                {/* <Grid item md={4} >
                    {rightComponent}
                </Grid> */}
            </Grid>
        </>
    )
}

export default DividerBox