import React from 'react'
import { Box, Card, CardContent, Dialog, DialogContent, Grid, useMediaQuery, useTheme } from '@material-ui/core'

const Container = ({ open, Transition, ComponentLeft, ComponentRight, left=false, right=false }) => {
    const theme = useTheme()
    const match = useMediaQuery(theme.breakpoints.up('md'))
    const [RenderComponent, setRenderComponent] = React.useState(<></>)

    React.useEffect(() => {
        if( left ){
            setRenderComponent(
                <>
                { match &&
                <Grid item xs={6} style={{ height : "100%" }} >
                    <Box display="flex" alignItems="center" justifyContent="center" height="100%" flexDirection="column" >
                    {ComponentLeft}
                    </Box>
                </Grid>
                }
                <Grid item  {...(match ? {xs : 6} : {xs : 12} )} style={{ height : "100%" }} >
                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" >
                        <Card variant="outlined" style={{ maxWidth: "450px", width : "100%" }} >
                            <CardContent style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", position:'relative' }}>
                                {ComponentRight}
                            </CardContent>
                        </Card>
                    </Box>
                </Grid>
                </>
            )
        }
        if( right ){
            setRenderComponent(
                <>
                
                <Grid item {...(match ? {xs : 6} : {xs : 12} )} style={{ height : "100%" }} >
                    <Box display="flex" justifyContent="center" alignItems="center" height="100%" >
                        <Card variant="outlined" style={{ maxWidth: "450px", width : "100%" }} >
                            <CardContent style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", position:'relative' }}>
                                {ComponentRight}
                            </CardContent>
                        </Card>
                    </Box>
                </Grid>
                { match &&
                    <Grid item xs={6} style={{ height : "100%" }} >
                        <Box display="flex" alignItems="center" justifyContent="center" height="100%" flexDirection="column" >
                            {ComponentLeft}
                        </Box>
                    </Grid>
                }
                </>
            )
        }
    },[match, left, right, ComponentLeft, ComponentRight])

    return(
        <>
        <div>
            <Dialog open={open} onClose={ _ => null } fullScreen TransitionComponent={Transition} >
                <DialogContent style={{ position : 'relative', padding : "8px 8px" }} >
                <Grid container style={{ height : "100%" }} >
                    {RenderComponent}
                </Grid>
                </DialogContent>
            </Dialog>
        </div>
        </>
    )
}

export default Container