import constant from './index'
import app from '../../connection/Api'

export const awaitDataToReducerState = (status) => {
	return { type : constant.AWAIT_DATA_ROLES_AND_PERMISSIONS, status }
}
export const updateStateRole = (state,value) => {
	return { type : constant.UPDATE_STATE_ROLE, state, value }
}

export const loadRolesToReducerState = (roles) => {
	return { type : constant.LOAD_ROLES_TO_REDUCER_STATE, roles }
}

export const loadPermissionsToReducerState = (permissions) => {
	return { type : constant.LOAD_PERMISSIONS_TO_REDUCER_STATE, permissions }
}

export const getRolesForUser = () => (dispatch) =>{
	try{
		app.get('/get/user/roles')
		.then( data => {
			if(data.status === 1){
				if(data.roles.length >= 1){
					dispatch( loadRolesToReducerState(data.roles) )
				}
			}
		})
	}catch(err){
		console.log(err)
	}
}

export const getRolesAndPermissionsForState = () => (dispatch) => {
	try{
		dispatch({ type : 'AWAIT_DATA_ROLES_AND_PERMISSIONS', status : true })
		dispatch( getRolesForUser() )
		dispatch({ type : 'AWAIT_DATA_ROLES_AND_PERMISSIONS', status : false })
	}catch(err){
		console.log(err)
	}
}

export const loadFormRoleInReducerState = (name,value) => {
	return { type : constant.LOAD_FORM_ROLE_IN_REDUCER_STATE, name, value }
}

export const loadFormRolePermissionsInReducerState = (permissions) => {
	return { type : constant.LOAD_FORM_ROLE_PERMISSIONS_IN_REDUCER_STATE, permissions }
}

export const cleanFormRoleAndPermissions = ( cleanForm ) => {
	return { type : constant.CLEAN_FORM_ROLE_AND_PERMISSIONS, cleanForm }
}

export const saveFormState = ( state ) => {
	return { type : constant.SAVE_FORM_ROLE_STATE, state }
}

export const submitRoleAndPermissionsForm = (roleAndPermissions) => (dispatch) => {
	try{
		if(roleAndPermissions.permissions.length >= 1){
			const permissions = [...roleAndPermissions.permissions]
			var arrPermissionsID = []
			permissions.forEach( k => {
				arrPermissionsID.push(k._id)
			})
		}
		const role = { role : roleAndPermissions.rolename, permissions : arrPermissionsID }
		app.post('/save/new/role', role)
		.then( data => {
			if(data.status === 1){
				const state = true
				dispatch( saveFormState(state) )
			}
		})
	}catch(err){
		console.log(err)
	}
}