import React from 'react'
import {Tooltip, IconButton } from '@material-ui/core'
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';


const ButtonManageForm = ({title="", placement="top",color="primary", onClick = false, type="add"}) => {
	return(
		<Tooltip title={title} placement={placement} >
			<IconButton color={color} style={{ padding: '6px' }} onClick={ _ => {
				if(typeof onClick === 'function'){
					onClick()
				}else{
					return
				}
			}} >
				{ type === 'add' && <AddCircleOutlineIcon /> }
				{ type === 'remove' && <HighlightOffIcon /> }
			</IconButton>
		</Tooltip>
	)
}

export default ButtonManageForm;