import actionType from '../actions/index';

const initialState = {
	business : [],
	businessTypes : [],
	requestComplete : false,
	createBusiness : {
		errors : {},
		activateMandatoryForm : false,
		businessFormData : {
			businessName : "",
			taxIdentificationNumber : "",
			address : "",
			phoneNumber : "",
			businessType : {},
			workingHours : [],
			description : "",
		}
	},
	createBusinessType : {
		activeModalForm : false,
		errors : {},
		businessTypeFormData : {
			name : ""
		}
	}
}

export default function BusinessReducer( state = initialState, action ){
	let globalState = {...state}
	switch (action.type) {
        case actionType.LOAD_BUSINESS_TYPES :
			globalState.businessTypes = action.businessTypes
		break
        case actionType.UPDATE_FORM_CREATE_BUSINESS :
			globalState.createBusiness.businessFormData[action.attr] = action.value
		break
        case actionType.UPDATE_FORM_CREATE_BUSINESS_TYPE :
			globalState.createBusinessType.businessTypeFormData[action.attr] = action.value
		break
        case actionType.UPDATE_LIST_OF_ERRORS_IN_TYPE_OF_BUSINESS :
			globalState.createBusinessType.errors = action.errors
		break
        case actionType.ADD_A_NEW_TYPE_OF_BUSINESS_TO_THE_LIST :
			globalState.businessTypes = [...globalState.businessTypes, action.businessType]
		break
        case actionType.CLEAR_STATUS_CREAT_BUSINESS_TYPE :
			globalState.createBusinessType.activeModalForm = false
			globalState.createBusinessType.errors = {}
			globalState.createBusinessType.businessTypeFormData.name = ""
		break
        case actionType.UPDATE_MODAL_STATUS_CREATE_BUSINESS_TYPE :
			globalState.createBusinessType.activeModalForm = action.status
		break
        case actionType.UPDATE_WORKING_HOURS :
			globalState.createBusiness.businessFormData.workingHours = action.timetable
		break
        case actionType.UPDATE_LIST_OF_ERRORS_IN_CREATE_BUSINESS :
			globalState.createBusiness.errors = action.errors
		break
		case actionType.LOAD_BUSINESS :
			globalState.business = action.business
		break
		case actionType.ADD_A_NEW_BUSINESS_TO_THE_LIST :
			globalState.business = [...globalState.business, action.business]
		break
		case actionType.UPDATE_THE_STATUS_OF_THE_BUSINESSES_REQUEST :
			globalState.requestComplete = action.status
		break
		case actionType.UPDATE_THE_STATUS_OF_THE_FORM_TO_CREATE_BUSINESS :
			globalState.createBusiness.activateMandatoryForm = action.status
		break
		default:
			return state
	}
	return globalState
}