import React from 'react';
import { Switch, Route, useHistory } from "react-router-dom";
import { connect } from 'react-redux'
import Dashboard from './views/dashboard/Dashboard';
import Login from './views/auth/Login'
import theme from './styles/main'
import { ThemeProvider } from '@material-ui/core/styles';
import { FullProgress } from './components/ProgressApp';
import './styles/css/styles.css'
import './styles/icons/flaticon.css'
import { useValidToken } from './hooks/validateToken'
import { secureLogin } from './redux/actions/AuthAction';
import { CssBaseline } from '@material-ui/core';

function App({ dispatch, data }) {

  const [renderView, setRenderView] = React.useState(<></>)
  const history = useHistory()
  const userIsActive = useValidToken(data)

  React.useEffect(() => {
    if( userIsActive === "await" ){
      setRenderView(<FullProgress />)
    }else if( userIsActive ){
      dispatch(secureLogin()) 
      setRenderView(<Dashboard />)
    }else{
      history.push('/')
      setRenderView(<Login />)
    }
  },[userIsActive])

  return (
      <ThemeProvider theme={theme} >
        <CssBaseline />
        <Switch>
          <Route path="/" >
            { renderView }
          </Route>
        </Switch>
      </ThemeProvider>
  );
}

export default connect(state => {
  return {
    data : state.authState.data
  }
},null)(App);
