import React from 'react'
import { connect } from 'react-redux'

const RolesList = ({ dispatch, roles }) => {
	
	React.useEffect(() => {
		if(roles.length >= 1){
			dispatch({ type : 'AWAIT_DATA_ROLES_AND_PERMISSIONS', status : false })
		}
// eslint-disable-next-line react-hooks/exhaustive-deps
	},[roles])

	return(
		<div>
			ASDASDASDSADAS
		</div>
	)

}

export default connect(state => {
	return {
		roles : state.rolesAndPermissionsState.roles
	}
} ,null)(RolesList)