import React from 'react'
import { Dialog, DialogContent, DialogTitle, Zoom } from '@material-ui/core'
import Text from '../../../components/Text'
import CreateBusinessType from '.'

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Zoom ref={ref} {...props} />;
});

const CreateBusinessTypeModal = ({ open=false, updateModalStatusCreateBusinessType }) => {
    return(
        <>
            <Dialog maxWidth="sm" fullWidth open={open} onClose={() => updateModalStatusCreateBusinessType(false)} TransitionComponent={Transition} >
                <DialogTitle>
                    <Text text='Crear tipo de negocio' uppercase />
                </DialogTitle>
                <DialogContent style={{ padding :"8px 8px" }} >
                    <CreateBusinessType />
                </DialogContent>
            </Dialog>
        </>
    )
}

export default CreateBusinessTypeModal