import { io } from 'socket.io-client';
import { getReportsOfTheWeeks } from '../redux/actions/DashboardAction';
import store from '../redux/store';
const connection = "http://localhost:3005/"
// const connection = "https://kikedev-backapp.herokuapp.com/"
const socketIO = io(connection, { transports : ['websocket'] })
const dispatch = (action) => store.dispatch(action)

function emit(eventName, call={}){
    call.token = localStorage.getItem('user_token')
    socketIO.emit(eventName, call)
}

export default function Sockets(){
    socketIO.on('get-information-about-the-new-connection', () => {
        socketIO.emit('receive-user-information', {token: localStorage.getItem('user_token')})
    })
    socketIO.on('send/new/sale', () => {
        const weekDate = store.getState().Dashboard.weekDate
        dispatch(getReportsOfTheWeeks(weekDate))
    })
    this.emit = emit
}