import React from 'react';
import Form from './Form';
import { connect } from 'react-redux'
import { activateModalCreateCategory, addNewFieldToCreateCategoryForm, removeFieldToCreateCategoryForm, submitCategoriesForm, updateFieldInCategoryList, updateListOfErrors } from '../../../redux/actions/CategoriesAction';
import { closeFormAddNewCategory } from '../../../redux/actions/DashboardAction';

const mapPropsToState = state => {
	return{
		businessID : state.Dashboard.shiftID,
		error : state.Categories.createCategorie.error,
		awaitResponse : state.Categories.awaitResponse,
		successMessage : state.Categories.successMessage,
		categoriesForm : state.Categories.createCategorie.categoriesForm,
	}
}

const mapDispatchToProps = (dispatch) => {
    return {
        addNewFieldToCreateCategoryForm : () => dispatch(addNewFieldToCreateCategoryForm()),
        removeFieldToCreateCategoryForm : (index) => dispatch(removeFieldToCreateCategoryForm(index)),
        updateFieldInCategoryList : (index, attr, value) => dispatch(updateFieldInCategoryList(index, attr, value)),
        updateListOfErrors : (errors) => dispatch(updateListOfErrors(errors)),
        submitCategoriesForm : (categories) => dispatch(submitCategoriesForm(categories)),
        closeFormAddNewCategory : () => dispatch(activateModalCreateCategory(false)),
    }
}

const CreateCategorie = connect(mapPropsToState, mapDispatchToProps)((props) => {
    const { businessID } = props
    const { error, awaitResponse, categoriesForm } = props
    const { addNewFieldToCreateCategoryForm, removeFieldToCreateCategoryForm, updateFieldInCategoryList, updateListOfErrors, submitCategoriesForm, closeFormAddNewCategory } = props
    return(
        <>
            <Form error={error} awaitResponse={awaitResponse} businessID={businessID} categoriesForm={categoriesForm} addNewFieldToCreateCategoryForm={addNewFieldToCreateCategoryForm} removeFieldToCreateCategoryForm={removeFieldToCreateCategoryForm} updateFieldInCategoryList={updateFieldInCategoryList} updateListOfErrors={updateListOfErrors} submitCategoriesForm={submitCategoriesForm} closeFormAddNewCategory={closeFormAddNewCategory} />
        </>
    )
})

export default CreateCategorie