import store from '../../redux/store'
import app from '../../connection/Api'
import CompountProduct from '../compountproducts/class'
import Product from '../products/class'
const dispatch = (action) => store.dispatch(action)
const compountProduct = new CompountProduct()
const productClass = new Product()

function currencyFormat(value){
    const currency = Intl.NumberFormat('es-CO')
    const result = isNaN(value) ? 0 : currency.format(value)
    return result
}
function detectedPrice(isManualPrice, priceList, positionPrice, manualValue, isCurrency=true){
    if( isManualPrice ){
        return isCurrency ? currencyFormat(manualValue) : manualValue
    }else{
        return isCurrency ? currencyFormat(priceList[positionPrice].salePrice) : priceList[positionPrice].salePrice
    }
}
function calculateTotalSale(quantity, isManualPrice, prices, positionPrice, manualPriceValue, discount, isCurrency=true){
    let price = isManualPrice ? manualPriceValue : prices[positionPrice].salePrice ;

    if( discount >= 1 ){
        const discountValue = (discount/100)
        const discountTotal = ((quantity*price)*discountValue)
        const result = ((quantity*price)-discountTotal)
        return isCurrency ? currencyFormat(result) : result
    }else{
        const total = quantity*price
        return isCurrency ? currencyFormat(total) : total
    }
}
function calculateProfitSale(saleList, isCurrency=true){
    let totalSale = 0 
    let totalCosts = 0 
    for( let item of saleList ){
        const { isManualPrice, positionPrice, manualPriceValue, quantity, discount } = item.config
        const { priceList, cost } = getDataForSale(item.product)
        
        const priceProduct = calculateTotalSale(quantity, isManualPrice, priceList, positionPrice, manualPriceValue, discount, false)
        const costProduct = cost*quantity
        totalSale += priceProduct
        totalCosts += costProduct
    }

    const total = totalSale-totalCosts

    return isCurrency ? currencyFormat(total) : total
}
function calculatePercentageProfitSale(saleList){
    const profit = calculateProfitSale(saleList, false)
    let totalSale = 0;
    for( let item of saleList ){
        const { isManualPrice, positionPrice, manualPriceValue, quantity, discount } = item.config
        const { prices } = item.product
        const priceProduct = calculateTotalSale(quantity, isManualPrice, prices, positionPrice, manualPriceValue, discount, false)
        totalSale += priceProduct
    }
    const total = (profit/totalSale)*100
    return Number(total.toFixed(1))
}
function calculateBaseStockUnits(stock, compoundUnit){
    const calc = (stock*compoundUnit)/compoundUnit
    return Number(calc.toFixed(1))
}
async function saveSale(shoppingList, totalSale, totalProfit, totalPercentageProfit){
    try {
        if( !Array.isArray(shoppingList) || shoppingList.length === 0 || !Boolean(totalSale) || !Boolean(totalProfit) || !Boolean(totalPercentageProfit)) throw "Error al intentar enviar la venta"
        const payload = {
            products: shoppingList,
            customer: "",
            total: totalSale,
            profit: totalProfit,
            profitPercentage: totalPercentageProfit,
        }
        const result = await app.post('/save/sale', payload)
        return result
    } catch (error) {
        console.log(error)
    }
}
function getDataForSale(product, unitSizePosition){
    const compountProductMain = compountProduct.compountProductMain(product)
    const isCompount = compountProduct.isCompount(product)
    const unitSizeActive = compountProduct.getUnitSize(product.unitSize, unitSizePosition)
    const compountUnits = compountProduct.getUnitSize(product.unitSize, unitSizePosition, true)
    const stockCalc = productClass.calcStock(product.unitSize, product.stock, Boolean(unitSizePosition))
    let response = {
        name: product.name,
        skucode: product.skucode,
        stock : parseFloat(Number(stockCalc).toFixed(1)),
        unitSize: unitSizeActive,
        priceList: product.prices,
        cost : product.prices[0].cost
    }
    if( isCompount && Boolean(compountProductMain.main) ){
        response.cost = product.costMin*compountUnits
        
    }
    return response
}
export default function Market(listCodes){

    function keyShortcuts(evt){
        const eventCode = evt.keyCode||evt.which
        console.log(eventCode)
        for(let key of listCodes){
            if(key.code === eventCode ){
                if(key.type === "action") dispatch(key.action)
                key.action()
                
            }
        }
    }

    this.addKeyShortcuts = () => window.addEventListener('keyup', keyShortcuts)
    this.removeKeyShortcuts = () => window.removeEventListener('keyup', keyShortcuts)
    this.calculateBaseStockUnits = calculateBaseStockUnits
    this.calculateTotalSale = calculateTotalSale
    this.calculateProfitSale = calculateProfitSale
    this.calculatePercentageProfitSale = calculatePercentageProfitSale
    this.currencyFormat = currencyFormat
    this.detectedPrice = detectedPrice
    this.saveSale = saveSale
    this.getDataForSale = getDataForSale
}

export function focusInSearchbarProduct(){
    const elem = document.getElementById('productInput')
    if( Boolean(elem) ){
        elem.focus()
    }
}