import React from 'react'
import { Box, Grid, InputAdornment, Typography } from '@material-ui/core'
import TextField from '../../../components/TextField'
import Autocomplete from '../../../components/Autocomplete'
import HelpDescription from '../../../components/HelpDescription'
import Button from '../../../components/Button'
import Switch from '../../../components/Switch'

const Form = ({name, abbreviation, baseUnit, compoundUnit, compoundUnitOfMeasure, unitSizeList, isEdit, ...actions}) => {
    const { activeModalToCreateUnitOfMeasure, updateFormdataCreateMeasurementUnit } = actions

    const updateData = (payload) => {
        const { attr, value } = payload
        updateFormdataCreateMeasurementUnit(attr, value)
    }
    return(
        <Box my={1} >
            <Grid container spacing={1} >
                <Grid item xs={6} >
                    <TextField
                        label="Nombre"
                        placeholder="Caja"
                        value={name}
                        name="name"
                        onChange={(data) => updateData(data)}
                        required
                    />
                </Grid>
                <Grid item xs={6} >
                    <TextField
                        label="Abreviatura/Signo"
                        placeholder="CJ"
                        value={abbreviation}
                        name="abbreviation"
                        onChange={(data) => updateData(data)}
                        required
                    />
                </Grid>
                {compoundUnitOfMeasure &&
                    <>
                        <Grid item xs={6} >
                            <Autocomplete
                                required
                                label='Unidad de medida base'
                                options={unitSizeList}
                                onChange={ (data,e) => {
                                    const payload = {attr: data.attr, value : e}
                                    updateData(payload)
                                }}
                                name="baseUnit"
                                value={baseUnit}
                            />
                        </Grid>
                        <Grid item xs={6} >
                            <TextField
                                required
                                onChange={(data) => updateData(data)}
                                inputProps={{
                                    min : 0
                                }}
                                value={compoundUnit}
                                name="compoundUnit"
                                type="number"
                                label="Cantidad"
                                placeholder="24"
                                InputProps={{
                                    endAdornment : <InputAdornment position='end' >
                                        <HelpDescription title='Este campo es util para definir cuantas unidades representa su unidad de medida, por ejemplo "1 caja" representa "24 unidades". Con esto al crear un producto y asignarle la unidad de medida Caja al realizar una accion que amerite descontar "1 Caja" de este producto el sistema detectara y automaticamente calculara que se descontaron "24 unidades" del producto o si desea ingresar "1 Caja" del producto el sistema automaticamente calculara que se añadieron "24 unidades" nuevas' />
                                    </InputAdornment>
                                }}
                            />
                        </Grid>
                    </>
                }
                <Grid item xs={12} >
                    <Switch onChange={(value, name) => updateData({attr: name, value})} name="compoundUnitOfMeasure" checked={compoundUnitOfMeasure} text='Crear unidad de medida compuesta'   />
                </Grid>
                { compoundUnitOfMeasure &&
                    <Grid item xs={12} >
                        <Typography>
                            { Boolean(baseUnit) && Boolean(name) && Boolean(compoundUnit) ? `Unidad de medida: 1 ${name} (${abbreviation}) = ${compoundUnit} ${baseUnit.name}` : ""}
                        </Typography>
                    </Grid>
                }
                <Grid item xs={12} >
                    <Button variant='outlined' size='large' > {isEdit ? "Actualizar unidad de medida" : "Crear unidad de medida"} </Button>
                </Grid>
                <Grid item xs={12} >
                    <Button type='button' variant='outlined' color="secondary" size='large' onClick={() => activeModalToCreateUnitOfMeasure(false)} >Cancelar</Button>
                </Grid>
            </Grid>
        </Box>
    )
}

export default Form