import React from 'react'
import { Fade, Box, Grid, } from '@material-ui/core'
import TextField from '../../../components/TextField'
import Button from '../../../components/Button'
import ButtonManageForm from '../../../components/ButtonManageForm'
import { FullProgress } from '../../../components/ProgressApp'
import Categorie from '../class'
import Switch from '../../../components/Switch'
import parseData, { objectKeysData } from '../../../methods/parseData'

const Input = ({value='',error={},onChange, index}) => {
    return(
        <TextField
            value={value}
            type="text"
            variant="outlined"
            fullWidth
            label="Nombre de la categoria"
            size="small"
            name="name"
            error={ (error.name) && (error.position === index) && Boolean(error.name) }
            helperText={ (Boolean(error.name)) && (error.position === index) && "Este campo es obligatorio" }
            onChange={ e => onChange(index, e.attr, e.value) }
        />
    )
}

const CreateCategory = (props) => {
    //edit
    const { editForm, categorySelected, categoryUpdate, categoryUpdateName, categoryUpdateActive } = props
    const { callFormEditCategorie, storeUpdatedCategory } = props

    //create
    const { awaitResponse, categoriesForm, error } = props
    const { addNewFieldToCreateCategoryForm, removeFieldToCreateCategoryForm, updateFieldInCategoryList, updateListOfErrors, submitCategoriesForm, closeFormAddNewCategory } = props

	const submitCategories = (evt) => {
		evt.preventDefault()
		evt.stopPropagation()
		const categorie = new Categorie()
		const result = categorie.validateCategorieList(categoriesForm)
		updateListOfErrors(result.errors)
		if( result.status ){
			submitCategoriesForm(result.result)
		}
	}

    const updateCategorie = (evt) => {
        evt.preventDefault()
		evt.stopPropagation()
        console.log(categoryUpdate)
    }

    const handlerChangeCategory = ({attr, value}) => {
        const category = categoryUpdate
        category[attr] = value
        storeUpdatedCategory(category)
    }

    React.useEffect(() => {
        if( editForm && objectKeysData(categorySelected) ){
            const category = parseData(categorySelected)
            storeUpdatedCategory(category)
        }
    },[categorySelected, editForm])

	return(
		<Fade in={true} >
			<Grid container >
				<Grid item xs={12} md={10} sm={10} >
					<Box py={2} >
						<form action="#" method="POST" onSubmit={(event) => editForm ? updateCategorie(event) : submitCategories(event)} >
							<Grid container spacing={1} >
                                { !editForm ?
                                    <>
                                        { categoriesForm.map((item,index) => {
                                            return(
                                                <React.Fragment key={index} >
                                                    <Grid item xs={10} >
                                                        <Input
                                                            index={index}
                                                            value={categoriesForm[index].name}
                                                            error={error}
                                                            onChange={(i,attr,value) => updateFieldInCategoryList(i,attr,value)}
                                                        />
                                                    </Grid>
                                                        <Grid item xs={2} >
                                                            <Box display="flex" flexDirection="row">
                                                                { index >= 1 ?
                                                                    <ButtonManageForm
                                                                        type="remove"
                                                                        title="Eliminar"
                                                                        placement="right"
                                                                        color="secondary"
                                                                        onClick={() => removeFieldToCreateCategoryForm(index)}
                                                                    /> : 
                                                                    <ButtonManageForm
                                                                        title="Puede añadir hasta 5 categorias a la vez"
                                                                        placement="bottom"
                                                                        onClick={ () => {
                                                                            if( categoriesForm.length <= 4 ){
                                                                                addNewFieldToCreateCategoryForm()
                                                                            }
                                                                        }}
                                                                    />
                                                                }
                                                            </Box>
                                                        </Grid>
                                                </React.Fragment>
                                            )
                                        }) }
                                    </> :
                                    <>
                                        <Grid item xs={12} >
                                            <Input
                                                value={categoryUpdateName}
                                                // error={error || {}}
                                                onChange={(i,attr,value) => handlerChangeCategory({attr,value})}
                                            />
                                        </Grid>
                                        <Grid item xs={12} >
                                            <Switch
                                                name="active"
                                                onChange={(val) => handlerChangeCategory(val)}
                                                label='Estado'
                                                checked={categoryUpdateActive}
                                                text={categoryUpdateActive ? "Activo" : "Inactivo"}
                                            />
                                        </Grid>
                                    </>
                                }
								
								<Grid item xs={12} sm={6} md={6} lg={6}>
									<Button
                                        size='large'
										variant="outlined"
									> {editForm ? 'Guardar cambios' : 'Registrar'}  </Button>
								</Grid>
								<Grid item xs={12} sm={6} md={6} lg={6}>
									<Button
										type="button"
                                        size='large'
										variant="outlined"
										color="secondary"
										onClick={ _ => editForm ? callFormEditCategorie(false) : closeFormAddNewCategory() }
									>Cancelar</Button>
								</Grid>
							</Grid>
						{ awaitResponse && <FullProgress message='Creando categorias' /> }
						</form>
					</Box>
				</Grid>
			</Grid>
		</Fade>
	)
}

export default CreateCategory