import React from 'react'
import { Box } from '@material-ui/core'
import Button from '../../../components/Button'
import TextField from '../../../components/TextField'

const Form = ({ errors, updateFormCreateBusinessType, name }) => {

    const handlerDataForm = (data) => {
        const { attr, value } = data
        updateFormCreateBusinessType(attr, value)
    }



    return(
        <>
            <Box mb={4} >
                <TextField
                    error={errors.name && Boolean(errors.name)}
                    helperText={Boolean(errors.name) && errors.name}
                    value={name}
                    name="name"
                    label='Tipo de negocio'
                    placeholder="Ej: Tienda de articulos deportivos"
                    onChange={handlerDataForm}
                />
                <Box margin="auto" width={300} >
                    <Button size='large' >
                        Crear
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default Form