import constant from '../actions/index';

const formUnitSize = {
	name : "",
	abbreviation : "",
	baseUnit : {},
	compoundUnit : 0,
	compoundUnitOfMeasure : true
}

const initialState = {
	unitSizeList: false,
	list : [],
	limit : 10,
	page : 1,
	unitSizeData : {},
	createUnitSize : {
		isEdit: false,
        activeCreateUnitSizeModal : false,
		identifier: "",
        form : {...formUnitSize}
    },
	deleteUnitSize : {
		isOpenModal: false,
		unitSizeSelected: {},
		multipleUnitsSizeSelected : [],
	}
}

export default function UnitSizeReducer( state = initialState, action ){
	let globalState = {...state}
	switch (action.type) {
		case constant.ACTIVATE_MODAL_TO_CREATE_UNIT_OF_MEASURE:
			globalState.createUnitSize.activeCreateUnitSizeModal = action.status
		break;
		case constant.UPDATE_FORM_DATA_CREATE_MEASUREMENT_UNIT:
			globalState.createUnitSize.form[action.attr] = action.value
		break;
		case constant.UPDATE_LIST_OF_UNITS_OF_MEASURE:
			globalState.list = action.unitSizes
		break;
		case constant.UPDATE_DATA_OF_THE_REQUEST_OF_UNITS_OF_MEASUREMENT:
			globalState.unitSizeData = action.data
		break;
		case constant.UPDATE_PAGE_OF_THE_REQUEST_FOR_UNITS_OF_MEASUREMENT:
			globalState.page = action.position
		break;
		case constant.RESTORE_FORM_UNITS_OF_MEASUREMENTS:
			globalState.createUnitSize.identifier = ""
			globalState.createUnitSize.form = {...formUnitSize}
		break;
		case constant.EDIT_MODE_UNIT_OF_MEASURE:
			globalState.createUnitSize.isEdit = action.value
		break;
		case constant.UPDATE_UNIQUE_IDENTIFIER_OF_THE_SELECTED_UNIT:
			globalState.createUnitSize.identifier = action.value
		break;
		case constant.RESTORE_STATE_OF_THE_LIST_UNITS_OF_MEASUREMENTS:
			globalState.unitSizeList = false
			globalState.list = []
			globalState.limit = 10
			globalState.page = 1
			globalState.unitSizeData = {}
		break;
		case constant.I_AM_IN_THE_LIST_OF_MEASUREMENT_UNITS:
			globalState.unitSizeList = action.value
		break;
		case constant.OPEN_WINDOW_DELETE_UNIT_OF_MEASUREMENT:
			globalState.deleteUnitSize.isOpenModal = action.value
		break;
		case constant.SELECT_UNIT_OF_MEASUREMENT_TO_DELETE:
			globalState.deleteUnitSize.unitSizeSelected = action.unitSize
		break;
		case constant.SELECT_MULTIPLE_UNITS_OF_MEASUREMENT:
			globalState.deleteUnitSize.multipleUnitsSizeSelected = action.unitSizes
		break;
		default:
			return state
	}
	return globalState
}