import React from 'react'
import { Grid, Box, Paper, makeStyles, createStyles, Typography } from '@material-ui/core'
import Tooltip from './Tooltips'

const useStyles = makeStyles(theme => createStyles({
	radius : {
		borderRadius : "10px",
	},
	box:{
		padding : theme.spacing(3,2),
		position : "relative",
	},
	name :{
		textTransform : 'uppercase',
		color : theme.palette.text.main,
		fontWeight : theme.typography.fontWeightBold,
		margin : theme.spacing(0,1)
	},
	aliniation : {
		display : 'inline-flex',
		margin : theme.spacing(0,1)
	},
	button : {
		width : '100%',
		textAlign : 'left'
	},
	icon : {
		width: "100%",
		maxWidth : "80px",
	}
}))

const ItemCard = ({ title="", children, actions=false, icon=false, description="", pointer, onClick }) => {

	const classes = useStyles()
	return(
		<Grid item xs={12}  sm={12} md={4} lg={4} >
			<Paper elevation={3} className={classes.radius} {...(pointer ? { style : { cursor: "pointer" } } : {})} onClick={() => typeof onClick === "function" ? onClick() : null}  >
				<Box className={classes.box} >
					<Grid container >
						<Grid item xs={ icon ? 8 : 12} >
							{ title &&
							<Typography variant="body1" component="div" className={classes.name} >
								{title}
							</Typography>
							}
							{ children }
						</Grid>
						{icon &&
							<Grid item xs={4} >
								<Box display="flex" justifyContent="center" alignItems="center" width="100%" height="100%" >
									<Tooltip title={description} placement="top" >
										<img src={icon} alt={icon} className={classes.icon}  />
									</Tooltip>
								</Box>
							</Grid>
						}
					</Grid>
					{ actions &&
					<Box mt={1} >
						{ actions }
					</Box>
					}
				</Box>
			</Paper>
		</Grid>
	)
}

export default ItemCard