import React from 'react'

export const useValidToken = (data) => {
    const [auth, setAuth] = React.useState('await')
    React.useEffect(() => {
        const tkn = localStorage.getItem('user_token')
        if( tkn ){
            setAuth(true)
        }else{
            setAuth(false)
        }
    },[data])

    return auth
}