import React from 'react'
import { Box, Grid, IconButton, InputAdornment } from '@material-ui/core'
import { ColorCircle } from '../../../components/icons'
import TextField from '../../../components/TextField'
import Autocomplete from '../../../components/Autocomplete'
import Tooltip from '../../../components/Tooltips'
import AddCircleOutlineRoundedIcon from '@material-ui/icons/AddCircleOutlineRounded';
import HighlightOffRoundedIcon from '@material-ui/icons/HighlightOffRounded';
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import Button from '../../../components/Button'

const Form = ({ variantCategoryName, variantCategorySelected, variants, createCategoryVariant, ...actions}) => {
    const {
        addNewVariantToTheList,
        removeVariantToTheList,
        activateModalUseColorPalette,
        removeColorPaletteToVariant,
        searchListedVariantsCategory,
        updateVariantsData,
        updateNameOfNewVariantCategory,
        activateModalCreateVariant
    } = actions
    return(
        <>
            <Box display="flex" >
                { createCategoryVariant ? 
                    <TextField
                        onChange={(e) => updateNameOfNewVariantCategory(e.value)}
                        label="Categoria de variante"
                        placeholder="Ej: Colores"
                        value={variantCategoryName}
                    />
                : 
                    <Autocomplete
                        onChange={(e) => updateVariantsData(e)}
                        label="Seleccionar categoria de variante"
                        value={variantCategorySelected}
                    />
                }
                <Box>
                    <IconButton color={createCategoryVariant ? "secondary" : "primary"} onClick={() => createCategoryVariant ? searchListedVariantsCategory(false) : searchListedVariantsCategory(true)} >
                        { createCategoryVariant ?
                            <Tooltip title='Seleccionar una categoria de variante en lista' >
                                <SearchRoundedIcon />
                            </Tooltip> :
                            <Tooltip title='Crear una categoria de variante' >
                                <AddCircleOutlineRoundedIcon /> 
                            </Tooltip>
                        }
                    </IconButton>
                </Box>
            </Box>
            
            <Box maxHeight={240} overflow="scroll" py={1} >
                {variants.map((variant, index) => {
                    return(
                        <Box component="span" key={index} >
                            <Box display="flex" >
                                <TextField
                                    onChange={(e) => updateVariantsData(e, index)}
                                    label="Variante"
                                    placeholder="Ej: Rojo"
                                    value={variant.name}
                                    name="name"
                                    {...(variant.useColorPalette ? {
                                        InputProps : {
                                            endAdornment : <InputAdornment position='end' >
                                                <Tooltip title={`Click para eliminar el color`} >
                                                    <Box
                                                        style={{ cursor: "pointer" }}
                                                        bgcolor={variant.colorHex}
                                                        width="25px"
                                                        height="25px"
                                                        borderRadius={8}
                                                        onClick={() => removeColorPaletteToVariant(index)}
                                                    />
                                                </Tooltip>
                                            </InputAdornment>
                                        }
                                    } : {})}
                                />
                                <Box ml={1} >
                                    <Tooltip title={index === 0 ? "Añadir nueva variante" : "Remover variante"} >
                                        <IconButton color={index === 0 ? "primary" : "secondary"} onClick={() => index === 0 ? addNewVariantToTheList() : removeVariantToTheList(String(index)) } >
                                            {index === 0 ? <AddCircleOutlineRoundedIcon /> : <HighlightOffRoundedIcon /> }
                                        </IconButton>
                                    </Tooltip>
                                </Box>
                                <Box >
                                    <Tooltip title="Usar paleta de colores" >
                                        <IconButton color="secondary" onClick={() => activateModalUseColorPalette(true, index)} >
                                            <ColorCircle width={22} />
                                        </IconButton>
                                    </Tooltip>
                                </Box>
                            </Box>
                        </Box>
                    )
                })}
            </Box>
            <Box mb={1} >
                <Grid container spacing={2} >
                    <Grid item xs={6} >
                        <Button variant="outlined" size='large' > Crear variante </Button>
                    </Grid>
                    <Grid item xs={6} >
                        <Button variant="outlined" size='large' color='secondary' type='button' onClick={() => activateModalCreateVariant(false)} > Cancelar </Button>
                    </Grid>
                </Grid>
            </Box>
        </>
    )
}

export default Form