import { Typography } from '@material-ui/core'
import React from 'react'
import Table from '../../components/Table'
import Market from './class'
import ToolBar from './ToolBar'

const SaleTable = ({productID, customerID, successfulSearch, shoppingList, selectPaymentMethod, isFocusTable, ...actions}) => {
    const { updateProductIdentifierValue, activateResultList, updateResults, activateEmptyResultsAlert, lowerInPositionOfTheList, goUpInPositionOfTheList, activateManualPrice, changeDefaultPrice, changeDefaultUnitOfMeasure, saveProductInShoppingCard, saveSale, openPaymentMethodWindow, lowerInPositionOfThePaymentMethod, goUpInPositionOfThePaymentMethod, callNotificationSnackbar, focusShoppingListTable, loadProductConfiguration, activateWindowToRemoveProductFromTheShoppingList } = actions
    const market = new Market()
    const columns = [
        {
            field: 'name',
            headerName: 'Producto',
            editable: false,
            flex : 1,
            renderCell: (item) => {
                const { row } = item
                const { product } = row
                return(
                    <>{product.name}</>
                )
            }
        },{
            field: 'quantity',
            headerName: 'Cantidad',
            editable: false,
            flex : 1,
            renderCell: (item) => {
                const { row } = item
                const { config, product } = row
                return(
                    <>
                        x{config.quantity}
                        {product.unitSize.compoundUnitOfMeasure ? (Boolean(config.unitOfMeasurePosition) ? `(${product.unitSize.baseUnit.abbreviation})` : `(${product.unitSize.abbreviation} x${product.unitSize.compoundUnit})`) : `(${product.unitSize.abbreviation})`}
                    </>
                )
            }
        },{
            field: 'subtotal',
            headerName: 'Sub-Total',
            editable: false,
            flex : 1,
            renderCell: (item) => {
                const { row } = item
                const { config, product } = row
                const { isManualPrice, positionPrice, manualPriceValue } = config
                const { prices } = product
                return(
                    <>$ {market.detectedPrice(isManualPrice, prices, positionPrice, manualPriceValue )}</>
                )
            }
        },{
            field: 'total',
            headerName: 'Total',
            editable: false,
            flex : 1,
            renderCell: (item) => {
                const { row } = item
                const { config, product } = row
                const { isManualPrice, positionPrice, manualPriceValue, quantity, discount } = config
                const { prices } = product
                return(

                    <>$ {market.calculateTotalSale(quantity, isManualPrice, prices, positionPrice, manualPriceValue, discount)}</>
                )
            }
        },
    ]
    return(
        <>
            <ToolBar
                productID={productID}
                customerID={customerID}
                updateProductIdentifierValue={updateProductIdentifierValue}
                activateResultList={activateResultList}
                updateResults={updateResults}
                activateEmptyResultsAlert={activateEmptyResultsAlert}
                lowerInPositionOfTheList={lowerInPositionOfTheList}
                goUpInPositionOfTheList={ goUpInPositionOfTheList }
                activateManualPrice={ activateManualPrice }
                changeDefaultPrice={ changeDefaultPrice }
                changeDefaultUnitOfMeasure={ changeDefaultUnitOfMeasure }
                successfulSearch={successfulSearch}
                saveProductInShoppingCard={saveProductInShoppingCard}
                saveSale={saveSale}
                openPaymentMethodWindow={openPaymentMethodWindow}
                selectPaymentMethod={selectPaymentMethod}
                lowerInPositionOfThePaymentMethod={lowerInPositionOfThePaymentMethod}
                goUpInPositionOfThePaymentMethod={goUpInPositionOfThePaymentMethod}
                callNotificationSnackbar={callNotificationSnackbar}
                focusShoppingListTable={focusShoppingListTable}
                shoppingList={shoppingList}
                isFocusTable={isFocusTable}
                activateWindowToRemoveProductFromTheShoppingList={activateWindowToRemoveProductFromTheShoppingList}
            />
            <Typography style={{ fontSize: "12px" }} >
                Precione F4 para editar la lista de productos seleccionados
            </Typography>
            <Table
                onCellClick={(item) => {
                    focusShoppingListTable(true)
                    loadProductConfiguration(item.row.config)
                    updateResults([item.row.product])
                    activateResultList(true)
                }}
                disableFocus={false}
                rows={shoppingList}
                columns={columns}
                page={1}
                rowCount={10}
                loading={true}
                getRowId={row => row.product._id}
            />
        </>
    )
}

export default SaleTable