import { Box, IconButton } from '@material-ui/core'
import React from 'react'
import { DeleteSVG, DisabledSVG, EditSVG, ViewDataSVG } from './icons'
import Tooltip from './Tooltips'

const ActionButtons = ({edit=true, onEdit=false, deleted=true, onDeleted=false, disabled=true, onDisabled=false, enabled=false, onEnabled=false, view=true, onViewData=false, titleViewData }) => {

    return(
        <Box display="flex" >
            { edit &&
                <Tooltip title="Editar" placement="bottom" >
                    <Box mx={1} >
                        <IconButton size="small" color="secondary" {...(typeof onEdit === "function" && { onClick : () => onEdit() } )} >
                                <EditSVG width="20px" />
                        </IconButton>
                    </Box>
                </Tooltip>
            }
            { deleted &&
                <Tooltip title="Eliminar" placement="bottom" >
                    <Box mx={1} >
                        <IconButton size="small" color="primary" {...(typeof onDeleted === "function" && { onClick : () => onDeleted() } )} >
                                <DeleteSVG width="20px" />
                        </IconButton>
                    </Box>
                </Tooltip>
            }
            { disabled &&
                <Tooltip title="Inhabilitar" placement="bottom" >
                    <Box mx={1} >
                        <IconButton size="small" {...(typeof onDisabled === "function" && { onClick : () => onDisabled() } )} >
                            <DisabledSVG width="20px" />
                        </IconButton>
                    </Box>
                </Tooltip>
            }
            { view &&
                <Tooltip title={titleViewData ?? "Ver informacion"} placement="bottom" >
                    <Box mx={1} >
                        <IconButton size="small" {...(typeof onViewData === "function" && { onClick : () => onViewData() } )} >
                            <ViewDataSVG width="20px" />
                        </IconButton>
                    </Box>
                </Tooltip>
            }
        </Box>
    )
}

export default ActionButtons