import store from "../../redux/store";
import { activateModalCreateVariant } from "../../redux/actions/VariantsAction";
import history from "../../handlers/history";

const dispatch = (action) => store.dispatch(action)

export function goCreateVariant(){
    dispatch(activateModalCreateVariant(true))
}
export function goManageVariants(businessNAme){
    history.push(`/${businessNAme}/manage/variants`)
}