import React from 'react'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import MenuItem from '@material-ui/core/MenuItem';
import { Avatar, Box, makeStyles } from '@material-ui/core';
import { IconButton, Menu } from '@material-ui/core';
import avatar from '../styles/svg/018-account.svg'

const useStyles = makeStyles(theme=>({
	exitButton : {
		color : theme.palette.secondary.light,
		'&:hover' : {
			backgroundColor : theme.palette.secondary.light,
			color : theme.palette.secondary.contrastText
		}
	}
}))

const MenuProfile = ({username, photoProfile, name, lastname}) => {
	const clasess = useStyles()
	const [anchorEl, setAnchorEl] = React.useState(null);
	const open = Boolean(anchorEl);
	const history = useHistory()

	const handleMenu = (event) => {
		setAnchorEl(event.currentTarget);
	};

	const handleClose = () => {
		setAnchorEl(null);
	};
	return(
		<>
			<Box display="flex" flexDirection="row" alignItems="center" >
				<IconButton style={{ padding : '0px' }} aria-controls="menu-appbar" aria-haspopup="true" onClick={handleMenu} >
					<Avatar alt="profile-photo" src={avatar} style={{ height : "25px", width : "25px" }} />
				</IconButton>
			</Box>
			<Menu id="menu-appbar" anchorEl={anchorEl} keepMounted open={open} onClose={handleClose}
				anchorOrigin={{
					vertical: 'top',
					horizontal: 'right',
				}}
				transformOrigin={{
					vertical: 'top',
					horizontal: 'right',
				}}
			>
				<MenuItem
					className={clasess.exitButton}
					onClick={ _=> {
						localStorage.removeItem('user_token')
						localStorage.removeItem('businessId')
						history.push('/')
						window.location.reload()
					}}
				>Desconectarse</MenuItem>
				<MenuItem onClick={handleClose}> @{username} </MenuItem>
			</Menu>
		</>
	)
}

export default connect(state => {
	return{
		username : state.authState.data.username,
		photoProfile : state.authState.data.photoProfile,
		name : state.authState.data.name,
		lastname : state.authState.data.lastname,
	}
},null)(MenuProfile)