import React from 'react'
import { connect } from 'react-redux'
import CreateCategoryModal from '../views/categories/create_categories/CreateCategoryModal'
import CreateVariantModal from '../views/variants/create_variant/CreateVariantModal'
import CreateUnitSize from '../views/unitSize/create_unitSize'
import LoadProducts from '../views/products/load_product'

const mapPropsToState = state => {
    return {
		callFormAddBusiness : state.Dashboard.callFormAddBusiness,
		activePopupVariant : state.Products.activePopupVariant,
		addedNewPrice : state.PriceState.addedNewPrice,
    }
}

const PopupsModals = connect(mapPropsToState, null)(({ dispatch, callFormAddBusiness, activePopupVariant, addedNewPrice, createCategorie }) => {
    
    return(
        <>
        <CreateCategoryModal />
        <CreateVariantModal />
        <CreateUnitSize />
        <LoadProducts />
        </>
    )
})

export default PopupsModals