import constant from '../actions/index'

const initialState = {
    create_product_compount: {
        isEditable: false,
        isOpenModal: false,
        productSelected: {},
        inputValueProduct: [],
        positionUnitSize : 0,
        quantity : 1,
        mainProduct : false,
        compount: [],
    }
}

export default function compountProductReducer(state = initialState, action){
    const globalState = {...state}
    switch(action.type){
        case constant.UPDATE_PRODUCT_SEARCH_VALUE:
            globalState.create_product_compount.inputValueProduct = action.value
        break;
        case constant.SELECT_PRODUCT_FOR_COMPOSITE:
            globalState.create_product_compount.productSelected = action.product
        break;
        case constant.OPEN_COMPOSITE_PRODUCT_CONFIGURATION_WINDOW:
            globalState.create_product_compount.isOpenModal = action.value
        break;
        case constant.UPDATE_POSITION_OF_THE_PUTTING_UNIT:
            globalState.create_product_compount.positionUnitSize = action.postion
        break;
        case constant.UPDATE_SELECTED_QUANTITY:
            globalState.create_product_compount.quantity = action.value
        break;
        case constant.UPDATE_COMPOSITE_LIST:    
            globalState.create_product_compount.compount = action.value
        break;
        case constant.EDIT_SELECTED_COMPOSITE_PRODUCT:
            globalState.create_product_compount.isEditable = action.value
        break;
        case constant.ASSIGN_MAIN_PRODUCT:
            globalState.create_product_compount.mainProduct = action.value
        break;
        case constant.RESET_COMPOSITE_PRODUCT_STATUS:
            globalState.create_product_compount.productSelected = {}
            globalState.create_product_compount.inputValueProduct = []
            globalState.create_product_compount.positionUnitSize = 0
            globalState.create_product_compount.quantity = 1
            globalState.create_product_compount.isEditable = false
            globalState.create_product_compount.mainProduct = false
        break;
        default :
            return globalState
    }
     return globalState
}