import React from 'react'
import { Box, Grid } from '@material-ui/core'
import { connect } from 'react-redux'
import Table from '../../../components/Table'
import { getVariants, updateVariantsListPagePosition } from '../../../redux/actions/VariantsAction'
import ActionButtons from '../../../components/ActionButtons'
import TitleViews from '../../../components/TitleViews'
import { goCreateVariant } from '../class'
import { Variant } from '../../../components/icons'
import Searchbar from '../../../components/Searchbar'
import Autocomplete from '../../../components/Autocomplete'

const mapPropsToState = (state) => {
    return {
        variants : state.Variants.list,
        limit : state.Variants.limit,
        page : state.Variants.page,
        variantsTotal : state.Variants.variantData.variants_total,
        position : state.Variants.variantData.num_page,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getVariants : (limit, page) => dispatch(getVariants(limit, page)),
        updateVariantsListPagePosition : (page) => dispatch(updateVariantsListPagePosition(page)),
    }
}

const ListVariants = connect(mapPropsToState, mapDispatchToProps)(({limit, page, variants, position, variantsTotal, ...actions}) => {
    const columns = [
        {
            field: 'variantCategory',
            valueGetter : (params) => `${params.row.variantCategory.name || ''}`,
            headerName: 'Categoria',
            editable: true,
            flex : 1,
        },{
            field: 'name',
            headerName: 'Variante',
            editable: false,
            flex : 1,
            renderCell : ({row}) => {
                return(
                    <Box display="flex" alignItems="center" >
                        {row.useColorPalette ? <Box mr={1} borderRadius={8} width={25} height={25} bgcolor={row.colorHex} /> : null} { row.name } 
                    </Box>
                )
            }
        },{
            field: 'variantCode',
            headerName: 'Codigo',
            editable: false,
            flex : 1,
            renderCell : ({row}) => {
                return(
                    <Box display="flex" alignItems="center" >
                        {`VAR-${row.variantCode <= 9 ? `0${row.variantCode}` : row.variantCode }`}
                    </Box>
                )
            }
        },{
            field: 'createdAt',
            headerName: ' ',
            renderCell : (params) => {
                return <ActionButtons
                view={false}
                disabled={false}
            />
            },
            flex : 1,
        },
    ]
    const { getVariants, updateVariantsListPagePosition } = actions
    React.useEffect(() => {
        getVariants(limit, page)
    },[limit, page])
    return(
        <>
            <TitleViews
                mb={2}
                title="Lista de variantes"
                tooltip="Crear nuevas variantes"
                onClick={() => goCreateVariant()}
                icon={<Variant />}
            />
            <Grid container spacing={2} >
                <Grid item xs={6} >
                    <Autocomplete label='Categoria de variante' />
                </Grid>
                <Grid item xs={6} >
                    <Searchbar placeholder="Buscar en variantes..." />
                </Grid>
            </Grid>
            <Table
                updatePage={(pg) => updateVariantsListPagePosition(pg)}
                page={position}
                rowCount={variantsTotal}
                rows={variants}
                columns={columns}
                overlayText="Cargando variantes"
                loading={true}
            />
        </>
    )
})

export default ListVariants