import history from "../../handlers/history"
import Market from "../market/class"

export const goCreateCompountProduct = (businessName) => {
    history.push(`/${businessName}/create/compount/product`)
}
export const goManageCompountProduct = (businessName) => {
    history.push(`/${businessName}/manage/compount/product`)
}
function getCostPriorit(product, position){
    return Boolean(position) ? product.costMin : product.prices[0].cost
}
function getUnitSizeForPosition(unitSize, position, compountUnit=false){
    if(unitSize.compoundUnitOfMeasure){
        const text = Boolean(position) ? `${unitSize.baseUnit.abbreviation}` : `${unitSize.abbreviation} x${unitSize.compoundUnit}`
        const units = Boolean(position) ? 1 : unitSize.compoundUnit
        return !compountUnit ? text : units
    }else{
        return !compountUnit ? `${unitSize.abbreviation}` : Boolean(unitSize.compoundUnit) ? unitSize.compoundUnit : 1
    }
}
function calculateCost(product, quantity, positionUnitSize=0, isCurrency=true){
    const cost = getCostPriorit(product, positionUnitSize)
    const market = new Market()
    const result = cost*quantity
    return isCurrency ? market.currencyFormat(result) : result
}
function isCompountProduct(product){
    return product.isCompount
}
function getMainCompountProduct(compount){
    if( Array.isArray(compount) && compount.length >= 1 ){
        for(let item of compount){
            if( item.main ){
                return item
            }
        }
        return false
    }else{
        return false
    }
}
function compountProductMain(product){
    const isCompount = isCompountProduct(product)
    if( isCompount ){
        const compountMain = getMainCompountProduct(product.compount)
        return Boolean(compountMain) ? compountMain : false
    }else{
        return false
    }
}
export default function CompountProduct(){
    this.compountProductMain = compountProductMain
    this.isCompount = isCompountProduct
    this.calculateCost = calculateCost
    this.getCostPriorit = getCostPriorit
    this.getUnitSize = getUnitSizeForPosition
}