import React from 'react'
import { Box, Fade, Divider, IconButton, Menu as MenuMaterial, Typography } from '@material-ui/core'
import Badge from './Badge';

const Menu = ({ children, title="", width="", maxHeight="", badgeValue=0, iconComponent }) => {
    const [defineDemensions, setDefineDemensions] = React.useState(false)
    const [anchorEl, setAnchorEl] = React.useState(null);
	const open = Boolean(anchorEl);

    React.useEffect(() => {
        var dimensions = {}
        if( Boolean(width) ) dimensions.width = width
        if( Boolean(maxHeight) ) dimensions.maxHeight = (maxHeight * 4.5)

        if( Object.keys(dimensions).length >= 1 ){
            setDefineDemensions(dimensions)
        }
    },[width, maxHeight])
    
    const handleClose = () => {
		setAnchorEl(null);
	};

    const handleMenu = (event) => {
		setAnchorEl(event.currentTarget);
	};
    return(
        <>
        <Box display="flex" flexDirection="row" alignItems="center" >
            <IconButton color="primary" style={{ padding : '0px' }} aria-controls="menu-appbar" aria-haspopup="true" onClick={handleMenu} >
                <Badge badgeContent={badgeValue} x="left" >
                    { iconComponent }
                </Badge>
            </IconButton>
        </Box>
        <MenuMaterial id="menu-appbar" TransitionComponent={Fade} anchorEl={anchorEl} keepMounted open={open} onClose={handleClose}
				anchorOrigin={{
					vertical: 'top',
					horizontal: 'right',
				}}
				transformOrigin={{
					vertical: 'top',
					horizontal: 'right',
				}}
                {...( Boolean(defineDemensions) && {
                    PaperProps : {
                        style: defineDemensions,
                    }
                })}
            >
                {title &&
                    <Typography component="span" variant="subtitle1" >
                        <Box p={1} fontSize={20} >
                            {title}
                            <Divider />
                        </Box>
                    </Typography>
                }
                { children }
        </MenuMaterial>
        </>
    )
}

export default Menu