import React from 'react'
import { Box, IconButton } from '@material-ui/core'
import TextField from '../../components/TextField'
import Tooltip from '../../components/Tooltips'
import { DeleteSVG, EditSVG, Payment, Print, Save } from '../../components/icons'
import Market from './class'
import Product from '../products/class'

export const ToolsButton = ({letter="f2", title="",icon, onClick}) => {
    return(
        <>
            <Box>
                <Tooltip title={title} placement='top' >
                    <Box display="flex" alignItems="center" >
                        <IconButton onClick={() => typeof onClick === "function" ? onClick() : null} >
                            {icon}
                        </IconButton>
                        <Box>({letter})</Box>
                    </Box>
                </Tooltip>
            </Box>
        </>
    )
}

const ToolBar = ({productID, customerID, successfulSearch, selectPaymentMethod, shoppingList, isFocusTable, ...actions}) => {
    const { updateProductIdentifierValue, activateResultList, updateResults, activateEmptyResultsAlert, lowerInPositionOfTheList, goUpInPositionOfTheList, activateManualPrice, changeDefaultPrice, changeDefaultUnitOfMeasure, saveProductInShoppingCard, saveSale, openPaymentMethodWindow, lowerInPositionOfThePaymentMethod, goUpInPositionOfThePaymentMethod, callNotificationSnackbar, focusShoppingListTable, activateWindowToRemoveProductFromTheShoppingList } = actions
    
    React.useEffect(() => {
        const productInput = document.getElementById('productInput')
        if( Boolean(productInput) ){
            productInput.focus()
        }
    },[])

    React.useEffect(() => {
        const product = new Product()
        const market = new Market([
            {
                code : 13,
                action : async () => {
                    if( isFocusTable && !successfulSearch && !selectPaymentMethod ){
                        const elemValueAttr = document.activeElement.getAttribute('data-rowindex')
                        const elem = document.querySelector(`div[data-rowindex='${elemValueAttr}']`)
                        elem.click()
                    }
                    const result = await product.search(productID)
                    if( successfulSearch ) saveProductInShoppingCard() 
                    if( !successfulSearch && !selectPaymentMethod){
                        if( !Boolean(result) ) return
                        if(result.status){
                            activateResultList(true)
                            updateResults(result.payload)
                        }else{
                            activateEmptyResultsAlert(true)
                        }
                    }
                    if( selectPaymentMethod && !isFocusTable && !successfulSearch){
                        openPaymentMethodWindow(false)
                        callNotificationSnackbar(true, "Nuevo metodo de pago seleccionado", "success")
                    }
                },
            },{
                code : 32,
                action : () => {
                    const elem = document.getElementById('quantityProduct')
                    if( Boolean(elem) ) elem.focus()
                }
            },{
                code : 40,
                action : () => {
                    if( successfulSearch ) lowerInPositionOfTheList()
                    if( selectPaymentMethod ) lowerInPositionOfThePaymentMethod()
                }
            },{
                code : 38,
                action : () =>{ 
                    if( successfulSearch ) goUpInPositionOfTheList()
                    if( selectPaymentMethod ) goUpInPositionOfThePaymentMethod()
                }
            },{
                code : 66,
                action : () => {
                    if( successfulSearch ){
                        activateWindowToRemoveProductFromTheShoppingList(true)
                    }
                }
            },{
                code : 77,
                action : () => {
                    if( successfulSearch ){
                        activateManualPrice()
                    }
                }
            },{
                code : 80,
                action : () => {
                    if( successfulSearch ){
                        changeDefaultPrice()
                    }
                }
            },{
                code : 115,
                action : () => {
                    if( shoppingList.length >= 1 ){
                        focusShoppingListTable(true)
                        document.querySelector('div[data-colindex="0"]').focus()
                    }
                }
            },{
                code : 85,
                action : () =>{
                    if( successfulSearch ){
                        changeDefaultUnitOfMeasure()
                    }
                }
            },{
                code : 113,
                action : () => saveSale()
            },{
                code : 17,
                action : () => {
                    focusShoppingListTable(false)
                    if( !selectPaymentMethod ){
                        openPaymentMethodWindow(true)
                    }else{
                        openPaymentMethodWindow(false)
                    }
                }
            }
        ])
        market.addKeyShortcuts()
        return () => {
            market.removeKeyShortcuts()
        }
    },[productID, successfulSearch, selectPaymentMethod, shoppingList, isFocusTable])
    return(
        <Box display="flex" alignItems="center" >
            <Box  display="flex">
                <Box maxWidth="250px" mr={1} >
                    <TextField
                        id="productInput"
                        onChange={(evt) => updateProductIdentifierValue(evt.value)}
                        mb={0}
                        size="small"
                        label="buscar producto"
                        value={productID}
                    />
                </Box>
                <Box maxWidth="250px" mr={3} >
                    <TextField mb={0} size="small" label="buscar cliente" value={customerID} />
                </Box>
            </Box>
            <Box display="flex" >
                <ToolsButton title='Guardar venta' letter='F2' icon={<Save />} onClick={() => saveSale()} />
                <ToolsButton title='Borrar venta' letter='F4' icon={<DeleteSVG />} />
                <ToolsButton title='Seleccionar metodoto de pago' letter='CTRL' icon={<Payment />} onClick={() => openPaymentMethodWindow(true)} />
                <ToolsButton title='Imprimir venta' letter='F6' icon={<Print />} />
            </Box>
        </Box>
    )
}

export default ToolBar