import constant from '../actions/index'

const initialState = {
	awaitData : false,
	saveForm : false,
	roles : [],
	newRole : {
		name : '',
		permissions : []
	}
}

export default function RolesAndPermissionsReducer(state = initialState, action){
	const globalState = {...state}
	switch(action.type){
		case constant.LOAD_ROLES_TO_REDUCER_STATE:
			globalState.roles = action.roles
		break;
		case constant.LOAD_PERMISSIONS_TO_REDUCER_STATE:
			globalState.permissions = action.permissions
		break;
		case constant.AWAIT_DATA_ROLES_AND_PERMISSIONS:
			globalState.awaitData = action.status
		break;
		case constant.LOAD_FORM_ROLE_IN_REDUCER_STATE:
			globalState.newRole[action.name] = action.value
		break;
		case constant.LOAD_FORM_ROLE_PERMISSIONS_IN_REDUCER_STATE:
			globalState.newRole.permissions = action.permissions
		break;
		case constant.CLEAN_FORM_ROLE_AND_PERMISSIONS:
			globalState.newRole = action.cleanForm
		break;
		case constant.SAVE_FORM_ROLE_STATE:
			globalState.saveForm = action.state
		break;
		case constant.UPDATE_STATE_ROLE:
			globalState[action.state] = action.value
		break;
		default :
			return globalState
	}

	return globalState
}