import Reports from '../../views/reports/class'
import constant from './index'

export const updateReportingDates= (initDate, finDate) => {
    return {type: constant.UPDATE_REPORTING_DATES, initDate, finDate }
}

export const addReportsToList = (reports) => {
    return {type: constant.ADD_REPORTS_TO_LIST, reports }
}

export const getReports = (initDate, finDate) => async (dispatch) => {
    try{
        const report = new Reports()
        const results = await report.filter(initDate, finDate)
        if( results.status ){
            dispatch(addReportsToList(results.payload))
        }
    }catch(err){
        console.log(err)
    }
}