import React from 'react'
import { Badge as Notifications } from '@material-ui/core'

const Badge = ({children, badgeContent=0, y="top", x="right" }) => {
    return(
        <Notifications badgeContent={badgeContent} max={10} color="secondary"
            anchorOrigin={{
                vertical: y,
                horizontal: x,
            }}
        >
            {children}
        </Notifications>
    )
}

export default Badge
