import constant from '../actions/index'

const initialState = {
	activeModals : false,
	login : {
		preloaderLogin : false,
		form : {
			email : "",
			password : ""
		}
	},
	register : {
		formBasicInformation : false,
		formCompleteProfile : false,
		formFirstBusiness : false,
		showPassword : false,
		addPhoto : false,
		duplicateEmail : false,
		duplicateUsername : false,
		errors : {},
		form : {
			username : "",
			email : "",
			password : "",
			repeatPassword : "",
			name : "",
			lastname : "",
			phoneNumber : "",
			countrie : {},
			region : {},
			citie : {},
			profilePhoto : {}
		}
	},
	data : {},
}

export default function handlerAuthentication(state = initialState, action){
	const globalState = {...state}
	switch(action.type){
			case constant.UPDATE_STATE_AUTH :
				globalState[action.state] = action.value
			break;
			default :
				return state
		}
	return globalState
}