import React from 'react'
import BodyForm from './BodyForm'
import { connect } from 'react-redux'
import { getBusinessTypes, submitNewBusiness, updateFormCreateBusiness, updateListOfErrorsInCreateBusiness, updateModalStatusCreateBusinessType, updateWorkingHours } from '../../../redux/actions/BusinessAction'
import { updateListOfDaysOfTheWeek } from '../../../redux/actions/DashboardAction'

const mapStateToProps = (state) => {
    return {
        days : state.Dashboard.days,
        week : state.Dashboard.week,
        businessTypes : state.Business.businessTypes,
        errors : state.Business.createBusiness.errors,
        businessName : state.Business.createBusiness.businessFormData.businessName,
        taxIdentificationNumber : state.Business.createBusiness.businessFormData.taxIdentificationNumber,
        address : state.Business.createBusiness.businessFormData.address,
        phoneNumber : state.Business.createBusiness.businessFormData.phoneNumber,
        businessType : state.Business.createBusiness.businessFormData.businessType,
        workingHours : state.Business.createBusiness.businessFormData.workingHours,
        description : state.Business.createBusiness.businessFormData.description
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateFormCreateBusiness : (attr, value) => dispatch(updateFormCreateBusiness(attr, value)),
        updateListOfDaysOfTheWeek : (weeks) => dispatch(updateListOfDaysOfTheWeek(weeks)),
        getBusinessTypes : () => dispatch(getBusinessTypes()),
        updateModalStatusCreateBusinessType : (status) => dispatch(updateModalStatusCreateBusinessType(status)),
        updateWorkingHours : (action, weekday) => dispatch(updateWorkingHours(action, weekday)),
        updateListOfErrorsInCreateBusiness : (errors) => dispatch(updateListOfErrorsInCreateBusiness(errors)),
        submitNewBusiness : (business) => dispatch(submitNewBusiness(business)),
    }
}

const CreateBusiness = connect( mapStateToProps, mapDispatchToProps )(({ getBusinessTypes, updateListOfDaysOfTheWeek, updateFormCreateBusiness, updateWorkingHours, week, days, businessTypes, updateListOfErrorsInCreateBusiness, errors, submitNewBusiness, ...rest }) => {
    
    React.useEffect(() => {
        getBusinessTypes()
    },[])

    return(
        <>
            <BodyForm week={week} days={days} businessTypes={businessTypes} updateListOfDaysOfTheWeek={updateListOfDaysOfTheWeek} updateFormCreateBusiness={updateFormCreateBusiness} businessFormData={rest} updateModalStatusCreateBusinessType={updateModalStatusCreateBusinessType} updateWorkingHours={updateWorkingHours} updateListOfErrorsInCreateBusiness={updateListOfErrorsInCreateBusiness} errors={errors} submitNewBusiness={submitNewBusiness} />
        </>
    )
})

export default CreateBusiness