import { Box, Grid, useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import FormContainer from '../../../components/FormContainer'
import Form from './Form'
import WorkingHours from './WorkingHours'
import Business from '../class'

const BodyForm = ({ week, days, errors, businessTypes, updateListOfDaysOfTheWeek, updateFormCreateBusiness, updateWorkingHours, businessFormData, updateModalStatusCreateBusinessType, updateListOfErrorsInCreateBusiness, submitNewBusiness }) => {
    const [files, setFiles] = React.useState(null)
    const theme = useTheme()
    const matchDown = useMediaQuery(theme.breakpoints.down("md"))
    const matchUp = useMediaQuery(theme.breakpoints.up("lg"))
    const submitForm = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        const business = new Business()
        const result = business.formDataNewBusiness(businessFormData, files)
        if( result instanceof FormData ){
            updateListOfErrorsInCreateBusiness({})
            submitNewBusiness(result)
        }else{
            updateListOfErrorsInCreateBusiness(result)
        }
    }

    return(
        <>
            <Grid container spacing={2} justify='center' >
                <Grid item xs={12} sm={8} md={8} lg={6} >
                    <FormContainer>
                        <Box p={2} >
                            <form onSubmit={submitForm} >
                                <Form days={days} errors={errors} fileInstance={setFiles} businessTypes={businessTypes} week={week} match={matchDown} updateListOfDaysOfTheWeek={updateListOfDaysOfTheWeek} updateFormCreateBusiness={updateFormCreateBusiness} updateModalStatusCreateBusinessType={updateModalStatusCreateBusinessType} updateWorkingHours={updateWorkingHours} {...businessFormData} />
                            </form>
                        </Box>
                    </FormContainer>
                </Grid>
                {matchUp &&
                    <Grid item item xs={12} sm={8} md={8} lg={6} >
                        <FormContainer>
                            <WorkingHours updateWorkingHours={updateWorkingHours} days={days} week={week} updateFormCreateBusiness={updateFormCreateBusiness} updateListOfDaysOfTheWeek={updateListOfDaysOfTheWeek} workingHours={businessFormData.workingHours} />
                        </FormContainer>
                    </Grid>
                }
            </Grid>
        </>
    )
}

export default BodyForm