import React from 'react'
import { connect } from 'react-redux'
import CreateVariant from '.'
import Dialog from '../../../components/Dialog'
import { activateModalCreateVariant } from '../../../redux/actions/VariantsAction'

const mapPropsToState = state => {
    return {
        isOpen : state.Variants.create_variant.activeModalCreateVariant
    }
}

const mapDispatchToProps = dispatch => {
    return {
        activateModalCreateVariant : (status) => dispatch(activateModalCreateVariant(status))
    }
}

const CreateVariantModal = connect(mapPropsToState, mapDispatchToProps)(({isOpen, ...actions}) => {
    const { activateModalCreateVariant } = actions
    return(
        <>
            <Dialog open={isOpen} close={() => activateModalCreateVariant(false)} title="Crear variantes" >
                <CreateVariant />
            </Dialog>
        </>
    )
})

export default CreateVariantModal