import moment from 'moment'
export const format = (formatDate='YYYY-MM-DD') => formatDate

export const formatDate = (date, formatting=null) => moment(date).format( format(formatting) );
export const getToday = () => moment().format( format() );
export const getYesterday = () => moment().subtract(1, "d").format( format() );
export const isValid = (date) => moment(date, format(), true).isValid();
export const pastDays = (days) => moment().subtract(days, "d").format( format() );