import React from 'react'
import { connect } from 'react-redux'
import Dialog from '../../../components/Dialog'
import { activeModalToCreateUnitOfMeasure, editModeUnitOfMeasure, restoreFormUnitsOfMeasurements, submitUnitSize, updateFormdataCreateMeasurementUnit } from '../../../redux/actions/UnitSizeAction'
import Form from './Form'

const mapDispatchToProps = (dispatch) => {
    return {
        activeModalToCreateUnitOfMeasure : (status) => dispatch(activeModalToCreateUnitOfMeasure(status)),
        updateFormdataCreateMeasurementUnit : (attr, value) => dispatch(updateFormdataCreateMeasurementUnit(attr, value)),
        submitUnitSize : (payload, isEdit) => dispatch(submitUnitSize(payload, isEdit)),
        editModeUnitOfMeasure : (value) => dispatch(editModeUnitOfMeasure(value)),
        restoreFormUnitsOfMeasurements : () => dispatch(restoreFormUnitsOfMeasurements()),
    }
}
const mapPropsToState = (state) => {
    return {
        activeCreateUnitSizeModal : state.UnitSize.createUnitSize.activeCreateUnitSizeModal,
        name : state.UnitSize.createUnitSize.form.name,
        abbreviation : state.UnitSize.createUnitSize.form.abbreviation,
        baseUnit : state.UnitSize.createUnitSize.form.baseUnit,
        compoundUnit : state.UnitSize.createUnitSize.form.compoundUnit,
        compoundUnitOfMeasure : state.UnitSize.createUnitSize.form.compoundUnitOfMeasure,
        unitSize : state.UnitSize.createUnitSize.form,
        isEdit : state.UnitSize.createUnitSize.isEdit,
        identifier : state.UnitSize.createUnitSize.identifier,
        list : state.UnitSize.list,
    }
}

const CreateUnitSize = connect(mapPropsToState, mapDispatchToProps)(({name, abbreviation, baseUnit, compoundUnit, compoundUnitOfMeasure, unitSize, list, activeCreateUnitSizeModal, isEdit, identifier, ...actions}) => {
    const { activeModalToCreateUnitOfMeasure, updateFormdataCreateMeasurementUnit, submitUnitSize, editModeUnitOfMeasure, restoreFormUnitsOfMeasurements } = actions
    const submitForm = (evt) => {
        evt.preventDefault()
        evt.stopPropagation()
        if(isEdit) unitSize._id = identifier
        submitUnitSize(unitSize, isEdit)
    }

    React.useEffect(() => {
        if( !activeCreateUnitSizeModal && isEdit ){
            editModeUnitOfMeasure(false)
        }
        if( !isEdit ){
            restoreFormUnitsOfMeasurements()
        }
    },[activeCreateUnitSizeModal, isEdit])
    return(
        <Dialog open={activeCreateUnitSizeModal} title={isEdit ? "Editar unidad de medida": "Crear unidad de medida"} close={() => activeModalToCreateUnitOfMeasure(false)} >
            <form onSubmit={submitForm} >
                <Form
                    unitSizeList={list}
                    activeModalToCreateUnitOfMeasure={activeModalToCreateUnitOfMeasure}
                    updateFormdataCreateMeasurementUnit={updateFormdataCreateMeasurementUnit}
                    name={name}
                    abbreviation={abbreviation}
                    baseUnit={baseUnit}
                    compoundUnit={compoundUnit}
                    compoundUnitOfMeasure={compoundUnitOfMeasure}
                    isEdit={isEdit}
                />
            </form>
        </Dialog>
    )
})

export default CreateUnitSize