import constant from '../actions/index.js'

export const fieldForm = {
	name : ""
}

const initialState = {
	categories : [],
	categoriesData : {},
	fetchingComplete : false,
	limit : 10,
	page : 1,
	categorySelected : {},
	categoriesInList : [],
	createCategorie : {
		activeCreateCategorieModal : false,
		createCategorieModalMessage : '',
		createCategorieModalMessageSeverity : '',
		error : {},
		categoriesForm : [{...fieldForm}]
	},
	editCategory : {
		categoryUpdate : {},
		activeEditCategorieModal : false,
	},
	deleteCategory : {
		alert : {
			isOpen : false,
		}
	},
	deleteFormCategorie : false,
	changeStateCategorie : false,
	awaitResponse : false,
	dataForm : {},
	deleteForm : {
		categories : []
	},
	editForm : {},
	changeStatusForm : {
		categories : []
	},

}

export default function CategoriesReducer(state = initialState, action){
	const globalState = state
	switch(action.type){
		case constant.NEW_FORM_WITH_ITEM_REMOVED :
			globalState.dataForm = action.newForm
		break;
		case constant.UPDATE_SELECTED_CATEGORY :
			globalState.categorySelected = action.category
		break;
		case constant.AWAIT_RESPONSE_FROM_SAVE_CATEGORIES :
			globalState.awaitResponse = action.status
		break;
		case constant.ADD_CATEGORIE_DELETE_FORM_REDUCER_STATE :
			globalState.deleteForm.categories = [...globalState.deleteForm.categories, action.value]
		break;
		case constant.CLEAR_DATA_FORM_DELETE :
			globalState.deleteForm = { categories : [] }
		break;
		case constant.CALL_FORM_DELETE_CATEGORIE :
			globalState.deleteFormCategorie = action.status
		break;
		case constant.CALL_FORM_EDIT_CATEGORIE:
			globalState.editCategory.activeEditCategorieModal = action.status
		break;
		case constant.ADD_CATEGORIE_FORM_EDIT :
			globalState.editForm[action.name] = `${action.value}/?${action.id}`
		break;
		case constant.CLEAR_DATA_FORM_EDIT_CATEGORY :
			globalState.editForm = action.value
		break;
		case constant.CALL_FORM_CHANGE_STATUS_CATEGORIE:
			globalState.changeStateCategorie = action.status
		break;
		case constant.ADD_CATEGORIE_CHANGE_STATUS_FORM_REDUCER_STATE :
			globalState.changeStatusForm.categories = [...globalState.changeStatusForm.categories, action.value]
		break;
		case constant.CLEAR_DATA_FORM_CHANGE_STATUS_CATEGORY :
			globalState.changeStatusForm = { categories : [] }
		break;
		case constant.ADD_NEW_FIELD_TO_CREATE_CATEGORY_FORM :
			globalState.createCategorie.categoriesForm = [...globalState.createCategorie.categoriesForm, fieldForm]
		break;
		case constant.REMOVE_FIELD_TO_CREATE_CATEGORY_FORM :
			globalState.createCategorie.categoriesForm = action.newList
		break;
		case constant.UPDATE_FIELD_IN_CATEGORY_LIST :
			globalState.createCategorie.categoriesForm = action.newList
		break;
		case constant.UPDATE_LIST_OF_ERRORS :
			globalState.createCategorie.error = action.errors
		break;
		case constant.ADD_NEW_CATEGORIES_TO_THE_CATEGORY_LIST :
			globalState.categories = [...globalState.categories, ...action.categories]
		break;
		case constant.LOAD_LIST_OF_CATEGORIES :
			globalState.categories = action.categories
		break;
		case constant.CLOSE_FORM_CREATE_CATEGORIES :
			globalState.createCategorie.error = {}
			globalState.createCategorie.categoriesForm = [{...fieldForm}]
		break;
		case constant.ACTIVATE_MODAL_CREATE_CATEGORY :
			globalState.createCategorie.activeCreateCategorieModal = action.status
			globalState.createCategorie.createCategorieModalMessage = Boolean(action.message) ? action.message : ""
			globalState.createCategorie.createCategorieModalMessageSeverity = Boolean(action.serverity) ? action.serverity : ""
		break;
		case constant.UPDATE_INFORMATION_OF_THE_APPLICATION_OF_THE_CATEGORIES:
			globalState.categoriesData = action.data
		break;
		case constant.UPDATE_CATEGORIES_PAGE:
			globalState.page = action.page
		break;
		case constant.UPDATE_CATEGORY_LIMITS:
			globalState.limit = action.limit
		break;
		case constant.UPDATE_STATUS_OF_EARNED_CATEGORIES:
			globalState.fetchingComplete = action.status
		break;
		case constant.UPDATE_LIST_OF_CATEGORIES:
			globalState.categoriesInList = action.categories_list
		break;
		case constant.STORE_UPDATED_CATEGORY:
			globalState.editCategory.categoryUpdate = action.categoryUpdate
		break;
		case constant.UPDATE_ALERT_MODAL_STATUS:
			globalState.deleteCategory.alert.isOpen = action.status
		break;
		default :
			return globalState
	}

	return globalState
}