import React from 'react';
import { connect } from 'react-redux'
import Media from 'react-media'
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import { AppBar, Toolbar, IconButton, Box } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import MovilbarNotification from './MovilbarNotifications'
import MenuProfile from '../../../components/MenuProfile';
import Mails from '../../../components/Mails';
import Notifications from '../../../components/Notifications';

const Navbar = ({dispatch, sidebarState, photoProfile}) => {
  const useStyles = makeStyles((theme) => ({
    appBar: {
      zIndex : 1000,
      backgroundColor : "#ffffff1f",
      backdropFilter: "blur(6px)",
      transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    appBarMovile : {
      zIndex: theme.zIndex.drawer + 1,
    },
    appBarShift: {
      width: `calc(100% - ${sidebarState.drawerWidth}px)`,
      marginLeft: sidebarState.drawerWidth,
      transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    hide: {
      display: 'none',
    }
  }));
  const classes = useStyles();

  return (
    <Media queries={{
        small: "(max-width: 599px)",
        medium: "(min-width: 600px) and (max-width: 2199px)",
      }}>
        {matches => (
          <React.Fragment>
            {matches.small && 
              <AppBar
                className={classes.appBarMovile}
                position="fixed"
              >
                <Toolbar variant="dense" >
                   <MovilbarNotification>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={ _ => dispatch({type : 'UPDATE_DRAWER_STATE' }) }
                        edge="start"
                      >
                      <MenuIcon />
                    </IconButton>
                   </MovilbarNotification>
                </Toolbar>
              </AppBar>
            }
            {matches.medium &&
              <AppBar
                elevation={0}
                position="fixed"
                className={ clsx(classes.appBar, { [classes.appBarShift]: sidebarState.drawerState, } ) }
              >
                <Toolbar variant="dense" >
                  <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    onClick={ _ => dispatch({type : 'UPDATE_DRAWER_STATE' }) }
                    edge="start"
                    className={clsx(classes.menuButton, sidebarState.drawerState && classes.hide)}
                  >
                  <MenuIcon />
                </IconButton>
                    <Box display="flex" justifyContent="end" width="100%" >
                      <Box mx={1} >
                        <Notifications />
                      </Box>
                      <Box mx={1} >
                        <Mails />
                      </Box>
                      <Box mx={1}>
                        <MenuProfile />
                      </Box>
                    </Box>
                </Toolbar>
              </AppBar>
            }
          </React.Fragment>
        )}
      </Media>
  );
}


export default connect( state => {
  return{
    sidebarState : state.sidebarState,
  } 
})(Navbar);
