import history from "../../handlers/history"
import { updateReportingDates } from "../../redux/actions/ReportsAction"
import store from "../../redux/store"
import app from '../../connection/Api'
import { isValid } from "../../methods/Moment"
import Market from "../market/class"
const dispatch = (action) => store.dispatch(action)
const market = new Market()

async function filterSale(initDate, finDate, page=1, limit=0){
    try {
        if( !isValid(initDate) || !isValid(finDate)) throw "Error de fechas"

        let url = `/filter/sale?initDate=${initDate}&finDate=${finDate}&`;
        if( Boolean(page) ) url = url+`page=${page}&`
        if( Boolean(limit) ) url = url+`page=${limit}&`


        const result = await app.get(url)
        return result
    } catch (error) {
        console.log(error)
    }
}
function calculateDaySale(daySale){
    let total = 0;
    if( Array.isArray(daySale) && daySale.length >= 1 ){
        for(let sale of daySale){
            total += sale.total
        }
        return total
    }else{
        return 0
    }
}

function calculateProfitDay(daySale){
    if( Array.isArray(daySale) && daySale.length >= 1 ){
        let profitDay = 0;
        let totalSale = calculateDaySale(daySale);
        for(let sale of daySale ){
            profitDay += sale.profit
            // const { products } = sale
            // for(let item of products){
            //     const _product = market.getDataForSale(item.product, item.config.unitOfMeasurePosition)
            //     cost += _product.cost
            // }
        }
        return profitDay
    }else{
        return 0
    }
}
function calculateProfitPercentageDay(daySale){
    if( Array.isArray(daySale) && daySale.length >= 1 ){
        const grossSales = calculateDaySale(daySale)
        const netIncome = calculateProfitDay(daySale)
        const total = (netIncome/grossSales)*100
        return Number(total.toFixed(1))
    }else{
        return 0
    }
}

export default function Reports(){
    this.filter = filterSale
    this.calculateDaySale = calculateDaySale
    this.calculateProfitDay = calculateProfitDay
    this.calculateProfitPercentageDay = calculateProfitPercentageDay
}

export const goTableReports = (businessName, initDate, finDate) => {
    dispatch(updateReportingDates(initDate, finDate))
    history.push(`/${businessName}/manage/reports`)
}